import { Component, ViewEncapsulation, TemplateRef, booleanAttribute, input } from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import { IconComponent } from '../icon/icon.component';

/** CollapsibleOnlyType breakpointa ‚Äì when is it a collapsible, when not */
export type CollapsibleOnlyType = 'only-xlg' | 'only-lg' | 'only-md' | 'only-sm';

@Component({
  selector: 'drv-collapsible',
  templateUrl: './collapsible.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./collapsible.scss'],
  imports: [NgTemplateOutlet, IconComponent],
  host: {
    class: 'drv-collapsible',
    '[class.drv-collapsible--fullsize]': 'this.fullWidthStretch()',
  },
})
export class CollapsibleComponent {
  readonly buttonText = input('Bereich √∂ffnen');
  readonly collapsibleOnly = input<CollapsibleOnlyType>('only-xlg');
  readonly fullWidthStretch = input<boolean, unknown>(false, { transform: booleanAttribute });
  readonly activeStateTemplate = input<TemplateRef<unknown>>(undefined);

  protected isOpen = false;

  protected get contentClasses(): string {
    const cssClasses = [
      'drv-collapsible__content',
      `drv-collapsible__content--${this.collapsibleOnly() || 'only-xlg'}`,
    ];

    if (this.isOpen) {
      cssClasses.push('drv-collapsible__content--open');
    }

    return cssClasses.join(' ');
  }

  protected toggle(): void {
    this.isOpen = !this.isOpen;
  }
}
