import {
  Component,
  ViewEncapsulation,
  booleanAttribute,
  ViewChildren,
  QueryList,
  ElementRef,
  OnChanges,
  input,
  model,
} from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { nanoid } from 'nanoid';

import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { ConfigService } from '../../services/config.service';
import { trackByFn } from '../../utilities/utils';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';
import { IconComponent } from '../icon/icon.component';
import { SymbolsComponent } from '../symbols/symbols.component';

import { type AriaAttributes, type BackgroundUI } from '../../types';
import { type FormItemMessage } from '../formitem/formitem.component';

@Component({
  selector: 'drv-starrating',
  templateUrl: './starrating.component.html',
  styleUrl: './starrating.component.scss',
  encapsulation: ViewEncapsulation.None,
  imports: [AriaAttributeDirective, NgClass, NgTemplateOutlet, IconComponent, SymbolsComponent],
  hostDirectives: [ValueAccessorDirective],
  host: {
    class: 'drv-starrating',
  },
})
export class StarRatingComponent implements OnChanges {
  readonly id = input<string>(`starrating-${nanoid()}`);
  readonly grouplabel = input<string>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('(Pflichtfeld)');
  readonly optionalText = model<string>(undefined);

  readonly hasError = model(false);
  readonly errorText = model<FormItemMessage[]>([]);
  readonly errorPrefix = input('Fehler:');
  readonly hasInfo = model(false);
  readonly infoText = model<FormItemMessage[]>([]);
  readonly infoPrefix = input('Hinweis:');
  readonly hasWarning = model(false);
  readonly warningText = model<FormItemMessage[]>([]);
  readonly warningPrefix = input('Warnung:');
  readonly disabled = model<boolean>(false);
  readonly collapseMessages = model(false);
  readonly backgroundUI = input<BackgroundUI>('light');
  readonly announceChanges = input(true, { transform: booleanAttribute });

  readonly required = model<boolean | unknown>(undefined);
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });

  readonly starText = input(' von 5 Sternen.');
  /** @internal */ @ViewChildren('radio') public _radios: QueryList<ElementRef>;

  protected ratingOptions = [1, 2, 3, 4, 5];
  protected rating: number;
  protected trackByFn = trackByFn;
  protected ariaAttributes: AriaAttributes;

  private labelClicked = false;

  constructor(
    /** @internal */
    public readonly valueAccessor: ValueAccessorDirective<number>,
    private configService: ConfigService
  ) {
    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.rating = v;
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  get cssClasses(): string[] {
    return [
      ['drv-fieldset', `drv-fieldset--${this.backgroundUI()}`, this.hasError() ? 'drv-fieldset--error' : ''].join(' '),
      ['drv-input-group', 'drv-starrating__wrapper'].join(' '),
    ];
  }

  get value(): number {
    return this.rating;
  }

  set value(value: number) {
    this.valueAccessor.valueChange(value);
    this.rating = value;
  }

  /** @internal */
  ngOnChanges(): void {
    const ariaAttributes: AriaAttributes = {};

    if (this.hasError || this.hasInfo || this.hasWarning) {
      ariaAttributes.describedby = `${this.id()}_errors`;
    }

    if (this.hasError) {
      ariaAttributes.invalid = true;
    }

    this.ariaAttributes = ariaAttributes;
  }

  protected setRating(value: number): void {
    this.value = value;
  }

  protected onFocusout(event: FocusEvent): void {
    for (const item of this._radios) {
      item.nativeElement.classList.remove('js-focus-visible');
    }

    this.valueAccessor.onBlur(event);
  }

  protected onFocusIn(e: FocusEvent): void {
    const target = e.target as HTMLElement;
    if (!target.matches('.drv-starrating__option') || this.labelClicked) {
      return;
    }
    if (!target.matches(':focus-visible')) {
      target.classList.add('js-focus-visible');
    }
    this.labelClicked = false;
  }

  protected labelClick({ target }): void {
    if (target.matches('.drv-starrating__option')) {
      this.labelClicked = true;
    }
  }
}
