import { Component, ViewEncapsulation, booleanAttribute, input } from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';

import { BackgroundUILightColor } from '../../types';
import { BreadcrumbComponent, type BreadcrumbItemInterface } from './breadcrumb.component';

import { GridDirective } from '../grid/grid.directive';
import { RowDirective } from '../grid/row.directive';
import { ColDirective } from '../grid/col.directive';

export type H1StyleType = 'title-large' | 'title-regular';

@Component({
  selector: 'drv-stage',
  templateUrl: './stage.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./stage.scss'],
  imports: [NgTemplateOutlet, GridDirective, RowDirective, ColDirective, BreadcrumbComponent],
  host: {
    class: 'drv-stage',
    '[class.drv-stage--light]': 'backgroundUI() === "light"',
    '[class.drv-stage--color]': 'backgroundUI() === "color"',
  },
})
export class StageComponent {
  readonly headline = input.required<string>();
  readonly introText = input<string>(undefined);
  readonly backgroundUI = input<BackgroundUILightColor>('light');
  readonly h1Style = input<H1StyleType>('title-regular');
  readonly breadcrumbItems = input<BreadcrumbItemInterface[]>(undefined);
  readonly breadcrumbAriaLabel = input('Breadcrumb');
  readonly breadcrumbBackLinkOnly = input(false, { transform: booleanAttribute });
  readonly fullWidth = input(false, { transform: booleanAttribute });
  readonly renderWithGridMarkup = input(true, { transform: booleanAttribute });

  protected get h1CssStyle(): string {
    return ['drv-stage__title', this.h1Style() === 'title-regular' ? 'drv-title-regular' : 'drv-title-large']
      .filter(Boolean)
      .join(' ');
  }
}
