import {
  Component,
  OnInit,
  OnChanges,
  HostBinding,
  HostListener,
  ViewEncapsulation,
  ElementRef,
  booleanAttribute,
  numberAttribute,
  SimpleChanges,
  model,
  input,
  inject,
  output,
  viewChild,
  ViewChild,
  viewChildren,
} from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { DeviceDetectorService } from 'ngx-device-detector';
import scrollIntoView from 'scroll-into-view-if-needed';

import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { normalizeEventKey, trackByFn } from '../../utilities/utils';
import { AriaAttributes, BackgroundUI } from '../../types';
import { IconComponent } from '../icon/icon.component';
import { ButtonComponent } from '../button/button.component';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';

export interface FilterOptions {
  label: string;
  value: string;
  selected?: boolean;
  disabled?: boolean;
  hasFocus?: boolean;
}

@Component({
  selector: 'drv-filter',
  templateUrl: './filter.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./filter.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [NgClass, NgTemplateOutlet, IconComponent, ButtonComponent, AriaAttributeDirective],
})
export class FilterComponent implements OnInit, OnChanges {
  private deviceService = inject(DeviceDetectorService);
  readonly valueAccessor = inject<ValueAccessorDirective<string[]>>(ValueAccessorDirective);

  readonly id = input.required<string>();
  readonly buttonText = model.required<string>();
  readonly label = input<string>(undefined);
  readonly placeholder = input<string>('Bitte ausw√§hlen');
  readonly labelVisible = input(false, { transform: booleanAttribute });
  readonly disabled = model<boolean>(false);
  readonly backgroundUI = input<BackgroundUI>('light');
  readonly btnResetText = input('Filter l√∂schen');
  readonly selectedText = input('Filter gesetzt');
  readonly selectedSuffixText = input('ausgew√§hlt');
  readonly selectCount = input<number, unknown>(undefined, { transform: numberAttribute });
  readonly options = input<FilterOptions[]>([]);
  readonly multiple = input(false, { transform: booleanAttribute });
  readonly showResetButton = input(false, { transform: booleanAttribute });
  readonly setValueOnBlur = input(false, { transform: booleanAttribute });

  readonly closed = output<null>();
  readonly clear = output<null>();

  @ViewChild('trigger', { static: false }) public trigger: ElementRef | HTMLElement;
  readonly list = viewChild<ElementRef>('list');
  readonly container = viewChild<ElementRef>('container');
  readonly selectElement = viewChild<ElementRef>('selectElement');
  readonly listitems = viewChildren<ElementRef>('listitem');
  readonly resetButton = viewChild<ButtonComponent>(ButtonComponent);

  @HostBinding('attr.id') protected externalId = '';
  @HostBinding('class') protected hostClass = 'drv-filter';

  protected isOpen = false;
  protected trackByFn = trackByFn;

  private innerValue?: string[];
  private initialButtonText = '';

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const valueAccessor = this.valueAccessor;

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.innerValue = v;
      if (Array.isArray(this.innerValue)) {
        this.options().forEach(option => {
          option.selected = this.innerValue.includes(option.value);
        });
      }
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  @HostBinding('attr.role') protected get hostRole(): string | null {
    return this.deviceService.isDesktop() && this.options().length ? 'application' : null;
  }

  protected get labelClass(): string {
    return `drv-label${this.labelVisible() ? '' : ' drv-vh'}`;
  }

  protected get triggerButtonAriaAttributes(): AriaAttributes {
    const expanded = this.isOpen;

    if (!this.options().length) {
      return {
        hasPopUp: true,
        expanded,
      };
    }

    return {
      labelledBy: `drv_filter-label_${this.id()} drv_filter-btn_${this.id()}`,
      expanded,
      controls: `drv-filter-list_${this.id()}`,
      activedescendant: this.activeDescendant || undefined,
    };
  }

  protected get triggerButtonCssClasses(): string {
    return [
      'drv-btn drv-btn--icon-right',
      `drv-btn-secondary--${this.backgroundUI()}`,
      'drv-filter__trigger',
      this.selectedOptionCount > 0 ? 'drv-filter__trigger--selected' : '',
    ]
      .filter(Boolean)
      .join(' ');
  }

  protected get activeDescendant(): string {
    const id = this.options()
      .filter(({ hasFocus }) => hasFocus)
      .map(opt => `drv_filter-value_${this.id()}-${this.options().indexOf(opt)}`)
      .pop();

    return id || null;
  }

  protected get selectedOptionCount(): number {
    const options = this.options();
    const selectCount = this.selectCount();
    if (selectCount !== null && !options.length) {
      return selectCount;
    }

    return options.filter(({ selected }) => selected).length;
  }

  protected get activeDescendantCount(): number {
    return this.options().filter(({ hasFocus }) => hasFocus).length;
  }

  protected get isMobile(): boolean {
    return !this.deviceService.isDesktop();
  }

  get value(): string[] {
    return this.innerValue;
  }

  set value(value: string[]) {
    this.valueAccessor.valueChange(value);
    this.innerValue = value;
  }

  @HostListener('window:click', ['$event.target']) protected handleWindowClick(target: MouseEvent): void {
    if (!this.container()?.nativeElement?.contains(target) && this.isOpen) {
      this.isOpen = false;
      this.closed.emit(null);
    }
  }

  /** @internal */
  ngOnInit(): void {
    this.initialButtonText = this.buttonText();

    if (!this.options().length) {
      console.warn('FilterComponent: No items provided. ControlValueAccessor will not work and ng-content is rendered');
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.buttonText && !changes.buttonText.firstChange) {
      this.buttonText.set(this.initialButtonText);
    }
  }

  protected buttonClickHandler(): void {
    this.toggle(!this.isOpen);
  }

  protected select(index: number): void {
    if (!this.multiple()) {
      this.options().forEach(opt => (opt.selected = false));
      this.options()[index].selected = true;
      this.toggle(false);
    } else {
      this.options()[index].selected = !this.options()[index].selected;
    }

    this.setValue();
  }

  protected setValueFromSelect(event: Event): void {
    const target: HTMLSelectElement = event.target as HTMLSelectElement;
    const { options, selectedIndex } = target;

    if (!this.multiple()) {
      this.options().forEach(opt => (opt.selected = false));
      const index = Math.max(selectedIndex - 1, 0);

      if (selectedIndex !== 0) {
        this.options()[index].selected = true;
      }
    } else {
      const selectedValues = Array.from(options)
        .map(({ selected, value }) => (selected ? value : null))
        .filter(el => el !== null);
      this.options().forEach(opt => (opt.selected = selectedValues.includes(opt.value)));
    }

    this.setValue();
    this.closed.emit(null);
  }

  protected clearValue(): void {
    const options = this.options();
    if (options.length) {
      options.forEach(opt => (opt.selected = false));
      this.setValue();
    }

    this.toggle(false);
    this.clear.emit(null);
  }

  protected buttonFocusout(event: FocusEvent): void {
    const { relatedTarget } = event;
    const focusedElement = this.options().findIndex(({ hasFocus }) => hasFocus);

    if (focusedElement > -1) {
      this.options()[focusedElement].hasFocus = false;

      if (this.setValueOnBlur()) {
        this.selectOption(focusedElement);
      }
    }

    if (relatedTarget === null || !this.container().nativeElement.contains(relatedTarget)) {
      this.toggle(false, false);
    }

    this.valueAccessor.onBlur(event);
  }

  protected resetButtonBlurHandler(event: FocusEvent): void {
    const { relatedTarget } = event;

    if (relatedTarget === null || !this.container().nativeElement.contains(relatedTarget)) {
      this.toggle(false, false);
    }
  }

  protected keydownButton(event: KeyboardEvent): void {
    const key = normalizeEventKey(event);
    const idx = this.options().findIndex(({ hasFocus }) => hasFocus);

    const { prev, next } = this.getNeighbourIndex(idx);

    const actions = {
      enter(): void {
        this.selectOption(idx);
        this.toggle();
      },
      esc(): void {
        this.toggle(false);
      },
      down(): void {
        if (!this.isOpen) {
          this.toggle(true);
          return;
        }

        this.focusOption(idx, next);
      },
      up(): void {
        if (!this.isOpen) {
          this.toggle(true);
          return;
        }

        this.focusOption(idx, prev);
      },
      space(): void {
        this.selectOption(idx);

        if (!this.multiple) {
          this.toggle();
        } else if (!this.isOpen) {
          this.toggle(true);
        }
      },
    };

    if (actions[key]) {
      event.preventDefault();
      actions[key].call(this);
    }
  }

  protected keydownResetButton(event: KeyboardEvent): void {
    const key = normalizeEventKey(event);

    const actions = {
      esc(): void {
        this.toggle(false);
      },
      down(): void {
        this.focusOption(-1, 0, true);
      },
      up(): void {
        this.focusOption(-1, this.options().length - 1, true);
      },
    };

    if (actions[key]) {
      event.preventDefault();
      (this.trigger as ElementRef).nativeElement.focus();
      actions[key].call(this);
    }
  }

  protected keydownButtonNoContent(event: KeyboardEvent): void {
    const key = normalizeEventKey(event);
    if (!this.trigger) {
      this.trigger = event.target as HTMLElement;
    }

    if (['down', 'up', 'enter', 'space'].includes(key)) {
      event.preventDefault();
      this.toggle();
    }
  }

  protected toggle(isOpen?: boolean, focusInput = true): void {
    this.isOpen = isOpen === undefined ? !this.isOpen : isOpen;

    if (!this.options().length) {
      if (this.isOpen) {
        this.closed.emit(null);
      }
      return;
    }

    setTimeout(() => {
      if (this.isOpen) {
        const firstOptionIndex = this.options().findIndex(option => option.selected);
        const element = this.listitems().at(firstOptionIndex)?.nativeElement;

        if (element) {
          scrollIntoView(element, { scrollMode: 'if-needed', block: 'nearest' });
        }
      } else {
        if (focusInput) {
          (this.trigger as ElementRef).nativeElement.focus();
        }
        this.closed.emit(null);
      }
    }, 10);
  }

  private getNeighbourIndex(idx: number): Record<'prev' | 'next', number> {
    let next = idx > -1 ? Math.min(idx + 1, this.options().length - 1) : 0;
    let prev = idx > -1 ? Math.max(idx - 1, 0) : this.options().length - 1;

    const options = this.options();
    while (options[next] && options[next].disabled) {
      next = next + 1;
    }

    while (options[prev] && options[prev].disabled) {
      prev = prev - 1;
    }

    if (next === idx || next > options.length - 1) {
      next = 0;

      while (options[next] && options[next].disabled) {
        next = next + 1;
      }
    }

    if (prev === idx || prev < 0) {
      prev = options.length - 1;

      while (options[prev] && options[prev].disabled) {
        prev = prev - 1;
      }
    }

    return { prev, next };
  }

  protected noContentFocusOut(event: FocusEvent): void {
    const { relatedTarget }: { relatedTarget: EventTarget } = event;

    if ((relatedTarget === null || !this.container()?.nativeElement?.contains(relatedTarget)) && this.isOpen) {
      this.isOpen = false;
      this.closed.emit(null);
    }
  }

  private focusOption(current: number, next: number, force = false): void {
    const options = this.options();
    if (current > -1) {
      options[current].hasFocus = false;
    }

    let element: HTMLElement | null = null;
    const verifyArray = [0, options.length - 1];

    if (this.selectedOptionCount > 0 && verifyArray.includes(current) && verifyArray.includes(next) && !force) {
      this.resetButton()?.input().nativeElement.focus();
    } else {
      element = this.listitems().at(next)?.nativeElement;
      options[next].hasFocus = true;
    }

    if (element) {
      scrollIntoView(element, { scrollMode: 'if-needed', block: 'nearest' });
    }
  }

  private selectOption(idx: number): void {
    if (!this.isOpen || idx === -1) {
      return;
    }

    if (!this.multiple()) {
      const selectedOption = this.options().find(({ selected }) => selected);
      if (selectedOption) {
        selectedOption.selected = false;
      }
      this.options()[idx].selected = true;
    } else {
      this.options()[idx].selected = !this.options()[idx].selected;
    }

    this.setValue();
  }

  private setValue(): void {
    this.value = this.options()
      .filter(({ selected }) => selected)
      .map(({ value }) => value);
  }
}
