import {
  Component,
  ViewEncapsulation,
  ElementRef,
  AfterViewInit,
  OnChanges,
  SimpleChanges,
  AfterContentInit,
  TemplateRef,
  model,
  input,
  inject,
  output,
  viewChild,
} from '@angular/core';
import { nanoid } from 'nanoid';
import { NgClass, NgTemplateOutlet } from '@angular/common';

import { getCellClass } from './shared';
import type { BackgroundUIDarkColor } from '../../types';
import type { IconType } from '../../types/icons';
import { ButtonComponent } from '../button/button.component';
import { IconComponent } from '../icon/icon.component';
import { ToggletipComponent } from '../toggletip/toggletip.component';

export type TableHeaderItemCollapseType = 'sm' | 'md' | 'lg' | 'xlg' | 'max';
export type TableHeaderItemOrderType = 'asc' | 'desc';
export type TableHeaderItemUnitType = 'em' | 'rem' | 'px' | '%';
export type TableHeaderItemAlignmentType = 'start' | 'end';

export interface TableHeaderItem {
  id?: string;
  label: string;
  labelHidden?: boolean;
  noSort?: boolean;
  sorted?: boolean;
  order?: TableHeaderItemOrderType;
  width?: {
    unit: TableHeaderItemUnitType;
    value: number;
  };
  collapses?: TableHeaderItemCollapseType;
  mainInfo?: boolean;
  sticky?: boolean;
  alignment?: TableHeaderItemAlignmentType;
  toggletip?: string;
  toggletipTemplate?: TemplateRef<unknown>;
}

export interface SortEvent {
  index: number;
  order: TableHeaderItemOrderType;
}

@Component({
  selector: 'drv-thead',
  templateUrl: './thead.component.html',
  encapsulation: ViewEncapsulation.None,
  imports: [NgClass, NgTemplateOutlet, ButtonComponent, IconComponent, ToggletipComponent],
})
export class TableHeadComponent implements AfterViewInit, OnChanges, AfterContentInit {
  private elementRef = inject(ElementRef);

  readonly data = model<TableHeaderItem[]>([]);
  readonly selectable = input(false);
  readonly sortable = input(false);
  readonly expandable = input(false);
  readonly allCheckboxLabel = input('Alle Eintr√§ge selektieren');
  readonly expandAllLabel = input('Alle Zeile auf-/zuklappen');
  readonly expandableMobile = input(false);
  readonly checked = input(false);
  readonly indeterminate = input(false);
  readonly expanded = model(false);
  readonly tHeadBackgroundUI = input<BackgroundUIDarkColor>('dark');

  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');

  readonly sorted = output<SortEvent>();
  readonly allSelected = output<boolean>();
  readonly expandChange = output<boolean>();
  readonly all = viewChild<ElementRef>('all');

  protected allId = `table-all-${nanoid()}`;
  protected getCellClass = getCellClass;
  protected lastSortedIndex: number | null = null;

  ngAfterContentInit(): void {
    const data = this.data();
    if (this.sortable() && data) {
      const activeSortIndex = data.findIndex(({ sorted }) => sorted);
      if (activeSortIndex === -1) {
        this.sortData(0);
      }
    }
  }

  ngAfterViewInit(): void {
    const nativeElement: HTMLElement = this.elementRef.nativeElement;
    const parentElement: HTMLElement = nativeElement.parentElement;

    if (!nativeElement || !parentElement) {
      return;
    }

    if (nativeElement.firstElementChild) {
      parentElement.insertBefore(nativeElement.firstElementChild, nativeElement);
    }

    parentElement.removeChild(nativeElement);

    this.setCheckboxState();
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { checked, indeterminate } = changes;

    if (checked || indeterminate) {
      this.setCheckboxState();
    }
  }

  protected getSort(item: TableHeaderItem): string | null {
    const { sorted, order, noSort } = item;
    if (!this.sortable() || noSort) {
      return null;
    }

    return sorted ? { asc: 'ascending', desc: 'descending' }[order] : 'none';
  }

  protected getSortIcon(head: TableHeaderItem): IconType {
    const type = head.sorted ? { asc: 'sort-arrows-up', desc: 'sort-arrows-down' }[head.order] : 'sort-arrows';
    return type as IconType;
  }

  protected getWidth(item: TableHeaderItem): string | null {
    const { width } = item;
    if (!width) {
      return null;
    }

    const { unit, value } = width;

    return `--width: ${value}${unit}`;
  }

  protected sortData(index: number): void {
    const activeSortIndex = this.data().findIndex(({ sorted }) => sorted);
    const data = this.data();
    const order = data[index].order === undefined || data[index].order === 'desc' ? 'asc' : 'desc';

    this.data.set(
      this.data().map((item, i) => ({
        ...item,
        sorted: i === index,
        order: activeSortIndex === index ? order : item.order || 'asc',
      }))
    );

    this.sorted.emit({
      index,
      order: this.data()[index].order,
    });
  }

  protected allCheckboxChange(): void {
    const { checked } = this.all().nativeElement;
    this.allSelected.emit(checked);
  }

  protected toggleAllExpandableRows(): void {
    this.expanded.set(!this.expanded());
    this.expandChange.emit(this.expanded());
  }

  protected trackByFn(index: number): number {
    return index;
  }

  private setCheckboxState(): void {
    const all = this.all();
    if (all) {
      if (this.indeterminate()) {
        all.nativeElement.checked = false;
        all.nativeElement.indeterminate = true;
      } else {
        all.nativeElement.checked = this.checked();
        all.nativeElement.indeterminate = false;
      }
    }
  }
}
