import {
  Component,
  OnDestroy,
  OnInit,
  ViewEncapsulation,
  AfterViewInit,
  booleanAttribute,
  model,
  ElementRef,
  input,
  viewChild,
} from '@angular/core';
import { NgClass } from '@angular/common';
import { nanoid } from 'nanoid';

export type ChartSize = 'small' | 'medium' | 'large';

@Component({
  selector: 'drv-donut',
  templateUrl: './donut.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./donut.scss'],
  imports: [NgClass],
  host: {
    class: 'drv-donut',
    '[class.drv-donut--small]': 'this.type() === "small"',
    '[class.drv-donut--medium]': 'this.type() === "medium"',
    '[class.drv-donut--large]': 'this.type() === "large"',
    '[attr.title]': 'this.accessibleText()',
  },
})
export class DonutComponent implements OnInit, OnDestroy, AfterViewInit {
  readonly accessibleText = input.required<string>();
  readonly percent = model<number>(0);
  readonly label = input<string>(undefined);
  readonly animated = input(false, { transform: booleanAttribute });
  readonly type = input<ChartSize>('large');
  readonly outline = input(false, { transform: booleanAttribute });
  readonly svg = viewChild<ElementRef>('svg');

  protected size = 90;
  protected strokeWidth = 10;
  protected radius = this.size / 2 - this.strokeWidth;
  protected innerRadius = this.radius - this.strokeWidth / 2;
  protected innerRadiusSmall = this.radius - this.strokeWidth;
  protected labelId = `chart-${nanoid()}`;
  protected reducedMotion = false;
  protected calculatedPercent = 0;
  protected observer: IntersectionObserver;

  private startTime: number;
  private duration = 1000;

  protected get viewBox(): string {
    return `0 0 ${this.size} ${this.size}`;
  }

  protected get getPath(): string {
    return this.getPathFromAngle(this.calculatedPercent);
  }

  /** @internal */
  ngOnInit(): void {
    this.percent.set(Math.min(Math.max(this.percent(), 0), 100));
    this.reducedMotion =
      window && window.matchMedia ? window.matchMedia('(prefers-reduced-motion: reduce)').matches : false;

    const sizes = {
      small: [24, 4],
      medium: [72, 8],
      large: [90, 10],
    };

    [this.size, this.strokeWidth] = sizes[this.type()];
    this.radius = this.size / 2;
    this.innerRadius = this.radius - this.strokeWidth / 2;
    this.innerRadiusSmall = this.radius - this.strokeWidth;

    if (this.animated() && !this.reducedMotion) {
      return;
    }

    this.calculatedPercent = this.percent();
  }

  /** @internal */
  ngAfterViewInit(): void {
    if (!this.animated() || this.reducedMotion) {
      return;
    }

    if ('IntersectionObserver' in window) {
      const threshold = 0.4;

      this.observer = new IntersectionObserver(
        ([{ intersectionRatio }]) => {
          if (intersectionRatio >= threshold) {
            this.startTime = document.timeline.currentTime as number;
            this.draw();
            this.observer.unobserve(this.svg().nativeElement);
          }
        },
        { threshold }
      );

      this.observer.observe(this.svg().nativeElement);
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    if (this.observer) {
      this.observer.unobserve(this.svg().nativeElement);
    }
  }

  private draw(): void {
    const elapsed = performance.now() - (this.startTime as number);
    const ratio = Math.min(1, elapsed / this.duration);

    this.calculatedPercent = this.percent() * ratio * ratio;

    if (elapsed < this.duration) {
      window.requestAnimationFrame(this.draw.bind(this));
    }
  }

  private getPathFromAngle(percent: number): string {
    const startangle = 0;
    const endangle = (percent / 100) * 2 * Math.PI;
    const big = endangle - startangle > Math.PI ? 1 : 0;

    const outerStartX = this.radius + this.radius * Math.sin(startangle);
    const outerStartY = this.radius - this.radius * Math.cos(startangle);
    const outerEndX = this.radius + this.radius * Math.sin(endangle);
    const outerEndY = this.radius - this.radius * Math.cos(endangle);

    const innerStartX = this.radius + this.innerRadiusSmall * Math.sin(startangle);
    const innerStartY = this.radius - this.innerRadiusSmall * Math.cos(startangle);

    const innerEndX = this.radius + this.innerRadiusSmall * Math.sin(endangle);
    const innerEndY = this.radius - this.innerRadiusSmall * Math.cos(endangle);

    return `
      M ${outerStartX},${outerStartY}
      A ${this.radius},${this.radius} 0 ${big} 1 ${outerEndX},${outerEndY}
      L ${innerEndX},${innerEndY}
      A ${this.innerRadiusSmall},${this.innerRadiusSmall} 0 ${big} 0 ${innerStartX},${innerStartY}
      Z
    `;
  }
}
