import { Component, ViewEncapsulation, input } from '@angular/core';
import { IconComponent } from '../icon/icon.component';
import { ButtonComponent } from '../button/button.component';
import type { IconType } from '../../types/icons';

@Component({
  selector: 'drv-statusscreen',
  templateUrl: './statusscreen.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./statusscreen.scss'],
  imports: [IconComponent, ButtonComponent],
  host: {
    class: 'drv-statusscreen',
    '[attr.title]': 'hostTitle',
  },
})
export class StatusScreenComponent {
  readonly title = input.required<string>();
  readonly iconLabel = input.required<string>();
  readonly iconType = input<IconType>('document');
  readonly buttonLabel = input<string>(undefined);
  readonly buttonHref = input<string>(undefined);
}
