import {
  Component,
  ElementRef,
  OnInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  ViewEncapsulation,
  booleanAttribute,
  OnDestroy,
  Input,
  input,
  inject,
  Output,
  EventEmitter,
  viewChild,
} from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { filter, Subscription, take } from 'rxjs';
import { IconComponent } from '../icon/icon.component';
import { SymbolsComponent } from '../symbols/symbols.component';

@Component({
  selector: 'drv-accordionitem',
  templateUrl: './accordionitem.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  imports: [IconComponent, SymbolsComponent],
  host: {
    class: 'drv-accordionitem',
    '[attr.title]': 'null',
  },
})
export class AccordionItemComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input({ transform: booleanAttribute }) isOpen = false;

  private router = inject(Router);
  private activatedRoute = inject(ActivatedRoute);
  private changeDetector = inject(ChangeDetectorRef);

  /** @internal */
  readonly detailsElement = viewChild<ElementRef>('detailsElement');
  readonly message = input('');
  readonly title = input('');
  readonly iconType = input<'status-success' | 'status-processing' | 'status-warning' | null>(null);
  readonly itemId = input('');
  @Output() toggled = new EventEmitter<AccordionItemComponent>();

  private routerSubscription: Subscription;

  protected get open(): boolean | null {
    return this.isOpen ? true : null;
  }

  /** @internal */
  markForCheck(): void {
    this.changeDetector.markForCheck();
  }

  /** @internal */
  ngOnInit(): void {
    this.routerSubscription = this.router.events.pipe(filter(event => event instanceof NavigationEnd)).subscribe(() => {
      const fragment = this.router.routerState.snapshot.root.fragment;

      this.toggleFromHash(fragment);
    });
  }

  /** @internal */
  ngAfterViewInit(): void {
    this.activatedRoute.fragment.pipe(take(1)).subscribe((fragment: string | null) => {
      this.toggleFromHash(fragment);
    });
  }

  /** @internal */
  ngOnDestroy(): void {
    if (this.routerSubscription) {
      this.routerSubscription.unsubscribe();
    }
  }

  /**
   * @param {string} hash URL-Hash
   * @internal
   */
  toggleFromHash(hash: string): void {
    if (hash && hash === this.itemId()) {
      window.setTimeout(() => {
        this.changeDetector.markForCheck();
        this.isOpen = true;
        this.detailsElement().nativeElement.scrollIntoView({ behavior: 'smooth', inline: 'nearest' });
      });
    }
  }

  protected emitToggle(): void {
    this.isOpen = this.detailsElement().nativeElement.open;
    this.toggled.emit(this);
  }
}
