import { Pipe, PipeTransform } from '@angular/core';
import { formatVSNR } from './insurancenr.helper';

@Pipe({
  name: 'insurancenumber',
  // standalone: true,
})
export class InsuranceNumberFormatPipe implements PipeTransform {
  transform(input: string): string {
    return formatVSNR(input);
  }
}
