import { Component, Input, ViewEncapsulation, booleanAttribute, input } from '@angular/core';
import { RouterModule, RouterLink } from '@angular/router';

import { trackByFn } from '../../utilities/utils';
import { IconComponent } from '../icon/icon.component';

export interface FormError {
  id: string;
  text: string;
}

@Component({
  selector: 'drv-formerrors',
  templateUrl: './formerrors.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./formerrors.scss'],
  imports: [RouterModule, RouterLink, IconComponent],
  host: {
    class: 'drv-formerrors',
  },
})
export class FormerrorsComponent {
  readonly routerLink = input<string[]>(['./']);
  readonly errorHeadline = input.required<string>();
  readonly hintHeadline = input.required<string>();
  readonly hasErrors = input(false, { transform: booleanAttribute });
  readonly hasHints = input(false, { transform: booleanAttribute });

  protected trackByFn = trackByFn;

  private _errors: FormError[];
  private _hints: FormError[];

  @Input() public get errors(): FormError[] {
    return this._errors;
  }

  public set errors(errorArray: FormError[]) {
    this._errors = (errorArray && this.removeNullValues(errorArray)) || [];
  }

  @Input() public get hints(): FormError[] {
    return this._hints;
  }

  public set hints(errorArray: FormError[]) {
    this._hints = (errorArray && this.removeNullValues(errorArray)) || [];
  }

  removeNullValues(array: FormError[]): FormError[] {
    return array.filter(Boolean);
  }
}
