// TODO: Make pattern example for jumplinks or delete it
import { Component, Input, ViewEncapsulation, numberAttribute } from '@angular/core';
import { LinklistComponent, type LinklistData, type LinklistColumns } from '../linklist/linklist.component';

export interface JumplinkData {
  text: string;
  href: string;
}

/**
 * Transforms data model from jumplinks into linklists
 * @param {JumplinkData[]} jumplinks Jumplink data
 * @returns {LinklistData[]} Linklist data
 * @example
 * [{ href: '#foo', text: 'Foo' }] becomes
 * [{ text: 'Foo', route: [], routeExtras: { fragment: 'foo' }, ... }]
 */
function transformToLinklist(jumplinks: JumplinkData[]): LinklistData[] {
  return jumplinks.map(link => ({
    text: link.text,
    icon: 'arrow-down',
    target: '_self',
    route: [],
    routeExtras: {
      fragment: link.href.startsWith('#') ? link.href.split('#')[1] : link.href,
    },
  }));
}

@Component({
  selector: 'drv-jumplinks',
  templateUrl: './jumplinks.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./jumplinks.scss'],
  imports: [LinklistComponent],
  host: {
    class: 'drv-jumplinks',
  },
})
export class JumplinksComponent {
  @Input({ transform: transformToLinklist }) data: LinklistData[] = [];
  @Input({ transform: numberAttribute }) columns: LinklistColumns = 1;

  protected templateData: LinklistData[] = [];
}
