import { Component, Input, ViewEncapsulation, input } from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import type { IconType } from '../../types/icons';
import type { BackgroundUI } from '../../types/backgroundUI.type';
import { IconComponent } from '../icon/icon.component';

export type IcontextTagname = 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'p';
export type IcontextVariant = 'default' | 'decorative';
export type IcontextHeadingClass = 'title-regular' | 'title-medium' | 'title-small';

@Component({
  selector: 'drv-icontext',
  templateUrl: './icontext.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./icontext.scss'],
  imports: [IconComponent, NgTemplateOutlet],
  host: {
    class: 'drv-icontext',
    '[class.drv-icontext--decorative]': 'this.variant() === "decorative"',
    '[class.drv-icontext--light]': 'this.backgroundUI() === "light"',
    '[class.drv-icontext--dark]': 'this.backgroundUI() === "dark"',
    '[class.drv-icontext--color]': 'this.backgroundUI() === "color"',
  },
})
export class IcontextComponent {
  readonly iconType = input.required<IconType>();
  readonly tagName = input<IcontextTagname>(undefined);
  readonly backgroundUI = input<BackgroundUI>(undefined);
  readonly variant = input<IcontextVariant>(undefined);

  private _headingClass: IcontextHeadingClass | string = 'drv-icon-heading';

  @Input() get headingClass(): string {
    return this._headingClass;
  }

  set headingClass(classes: string) {
    this._headingClass = classes ? `${this._headingClass} drv-${classes}` : this._headingClass;
  }
}
