import {
  Component,
  ViewEncapsulation,
  forwardRef,
  AfterContentInit,
  numberAttribute,
  OnChanges,
  SimpleChanges,
  input,
  output,
  contentChildren,
} from '@angular/core';

import { ButtonComponent } from '../button/button.component';

@Component({
  selector: 'drv-segmentedbutton',
  template: '<ng-content />',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./segmentedbutton.scss'],
  host: {
    class: 'drv-segmentedbutton',
  },
})
export class SegmentedButtonComponent implements AfterContentInit, OnChanges {
  readonly activeIndex = input<number, unknown>(undefined, { transform: numberAttribute });
  readonly buttonClick = output<number>();
  readonly _buttons = contentChildren(forwardRef(() => ButtonComponent));

  private selectedIndex: number;
  private buttonArr = [];

  /** @internal */
  ngAfterContentInit(): void {
    this.selectedIndex = this.activeIndex() || 0;

    this.buttonArr = Array.from(this._buttons());
    this._buttons().forEach((button, index) => {
      button.kind.set('secondary');
      button.onClickHandler = this.clickHandler(button);
      button.ariaAttributes.set({ ...button.ariaAttributes(), pressed: index === this.activeIndex() });
    });
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.activeIndex && !changes.activeIndex.firstChange) {
      this.selectedIndex = this.activeIndex();
      this.buttonArr.forEach((button, index) => {
        button.ariaAttributes.set({ ...button.ariaAttributes, pressed: index === this.activeIndex() });
      });
    }
  }

  protected clickHandler(button: ButtonComponent) {
    return (): void => {
      const ariaAttributes = button.ariaAttributes();
      if (!ariaAttributes.pressed) {
        button.ariaAttributes.set({ ...ariaAttributes, pressed: true });
      }
      this.buttonArr
        .filter(btn => btn !== button)
        .forEach(btn => {
          btn.ariaAttributes.set({ ...btn.ariaAttributes, pressed: false });
        });
      this.selectedIndex = this.buttonArr.indexOf(button);
      this.buttonClick.emit(this.selectedIndex);
    };
  }
}
