import { Injectable, ElementRef, QueryList } from '@angular/core';
import { ControlValueAccessor, FormControlName, FormGroupDirective, Validators } from '@angular/forms';
import { FormComponent } from '../types/form-components';
import { InputGroupComponent } from '../components/inputgroup/inputgroup.component';
import { StarRatingComponent } from '../components/starrating/starrating.component';
import { FilterComponent } from '../components/filter/filter.component';

interface IValueAccessor extends ControlValueAccessor {
  _elementRef?: ElementRef;
}

interface IComponentValueAccessor extends ControlValueAccessor {
  host: FormComponent;
}

@Injectable({
  providedIn: 'root',
})
export class NativeElementRepositoryService {
  private repository: QueryList<FormControlName>;
  private formGroupDirective!: FormGroupDirective;

  get submitted(): boolean {
    return this.formGroupDirective.submitted;
  }

  init(formControls: QueryList<FormControlName>, formGroupDirective: FormGroupDirective): void {
    this.repository = formControls;
    this.formGroupDirective = formGroupDirective;
  }

  findByControlName(name: string | number): FormControlName | undefined {
    return this.repository.find(control => control.name === name);
  }

  getNativeElement(control: FormControlName): HTMLElement | undefined {
    const accessor = control.valueAccessor as IComponentValueAccessor;

    if (accessor.host) {
      if (accessor.host instanceof StarRatingComponent) {
        const children = accessor.host._radios;
        return children.get(0).nativeElement;
      }

      if (accessor.host instanceof InputGroupComponent) {
        const children = accessor.host.children;
        return children.size > 0 ? [...children.keys()][0].input().nativeElement : null;
      }

      if (accessor.host instanceof FilterComponent) {
        return (accessor.host.trigger as ElementRef).nativeElement;
      }

      return accessor.host.input().nativeElement;
    }

    return (accessor as IValueAccessor)?._elementRef?.nativeElement;
  }

  findFirst(): FormControlName | undefined {
    return this.repository.get(0);
  }

  findFirstRequired(): FormControlName | undefined {
    return this.repository.find(control => control.control.hasValidator(Validators.required));
  }

  submittedFindFirstWithError(): FormControlName | undefined {
    return this.repository.find(control => this.submitted && control.control.errors !== null);
  }
}
