import {
  Component,
  OnInit,
  ViewEncapsulation,
  ElementRef,
  AfterViewInit,
  forwardRef,
  OnDestroy,
  AfterContentInit,
  booleanAttribute,
  OnChanges,
  SimpleChanges,
  ChangeDetectorRef,
  model,
  input,
  inject,
  output,
  viewChildren,
  viewChild,
  contentChildren,
} from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { Subject } from 'rxjs';
import { nanoid } from 'nanoid';

import { TableExpandedRowComponent } from './table-expandedrow.component';
import { TableExpandRowComponent } from './table-expandrow.component';
import { getCellClass } from './shared';
import { type TableHeaderItem, TableHeadComponent, SortEvent } from './thead.component';
import type { BackgroundUIDarkColor } from '../../types';
import { ConfigService } from '../../services/config.service';
import { trackByFn } from '../../utilities/utils';
import { ButtonComponent } from '../button/button.component';

export type TableDataBody = (string | number)[];
export interface TableData {
  header: TableHeaderItem[];
  body?: TableDataBody[];
  selectedRows?: number[];
}
export type OnSelectChange = { index: number; row?: TableDataBody | TableExpandRowComponent }[];
export type SortFunction = (body: TableDataBody[], index: number, order: string) => TableDataBody[];

@Component({
  selector: 'drv-table',
  templateUrl: './table.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./table.scss', '../checkbox/checkbox.scss'],
  imports: [NgClass, NgTemplateOutlet, TableHeadComponent, ButtonComponent],
  host: {
    class: 'drv-table-wrapper',
    '[class.drv-table-wrapper--fixed-height]': 'fixedHeight()',
    '[attr.tabindex]': 'this.fixedHeight() ? "0" : undefined',
    '[attr.role]': 'this.fixedHeight() ? "region" : undefined',
    '[attr.aria-label]': 'this.fixedHeight() && !this.hideCaption() ? this.caption() : undefined',
  },
})
export class TableComponent implements OnInit, AfterViewInit, AfterContentInit, OnDestroy, OnChanges {
  private elementRef = inject(ElementRef);
  private configService = inject(ConfigService);
  private cdRef = inject(ChangeDetectorRef);

  readonly hideCaption = input(false, { transform: booleanAttribute });
  readonly sortable = input(false, { transform: booleanAttribute });
  readonly selectable = input(false, { transform: booleanAttribute });
  readonly expandableMobile = input(false, { transform: booleanAttribute });
  readonly stickyHead = input(false, { transform: booleanAttribute });
  readonly fixedHeight = input(false, { transform: booleanAttribute });
  readonly mainInfoAsHeaderCell = input(false, { transform: booleanAttribute });
  readonly caption = input<string>(undefined);
  readonly variant = input<'default' | 'condensed'>('default');
  readonly layout = input<'default' | 'fixed'>('default');
  readonly tHeadBackgroundUI = model<BackgroundUIDarkColor>(undefined);
  readonly layoutMobile = input<'default' | 'stacked' | 'stacked-labelled' | 'stacked-columns' | 'custom-grid'>(
    'default'
  );
  readonly cellAlignmentVertical = input<'top' | 'middle' | 'bottom' | null>(null);
  readonly data = model<TableData | undefined>(undefined);
  readonly singleCheckboxLabel = input('Eintrag selektieren');
  readonly allCheckboxLabel = input('Alle Eintr√§ge selektieren');
  readonly expandableBtnLabel = input('Details anzeigen');

  readonly sorted = output<SortEvent>();
  readonly selected = output<OnSelectChange>();
  readonly allSelected = output<boolean>();

  readonly boxes = viewChildren<ElementRef>('box');
  readonly all = viewChild<ElementRef>('all');
  readonly expandRows = contentChildren(
    forwardRef(() => TableExpandRowComponent),
    { descendants: true }
  );
  readonly expandableRows = contentChildren(
    forwardRef(() => TableExpandedRowComponent),
    { descendants: true }
  );

  protected headerId = `table-caption-${nanoid()}`;
  protected allId = `table-all-${nanoid()}`;
  protected boxesId = `table-boxes-${nanoid()}`;
  protected expandedRows: boolean[] = [];
  protected allCheckBoxChecked = false;
  protected allCheckBoxIndeterminate = false;
  protected getCellClass = getCellClass;
  protected trackByFn = trackByFn;
  private selectedRows: OnSelectChange = [];
  private rowLength = 0;
  private scrollable: boolean;
  private observer: MutationObserver;
  private destroyed$ = new Subject();
  private initialBody: TableDataBody[] = [];

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const configService = this.configService;

    this.tHeadBackgroundUI.set(configService.config.theadDefaultBackgroundUI);
  }

  protected get headerClassName(): string {
    return `drv-table-caption${this.hideCaption() ? '--vh' : ''}`;
  }

  protected get selectedRowsfromData(): number[] {
    const data = this.data();
    if (!data?.body || !data?.selectedRows) {
      return [];
    }

    return [...new Set(data.selectedRows)].filter(row => row <= this.data().body.length);
  }

  readonly sort = input<SortFunction>((body: TableDataBody[], index: number, order: string): TableDataBody[] => {
    const isNumber = (n: unknown): boolean => typeof n === 'number' && !isNaN(n);
    if (body.map(row => row[index]).every(isNumber)) {
      const comparator = (a: TableDataBody, b: TableDataBody): number =>
        order === 'asc' ? (a[index] as number) - (b[index] as number) : (b[index] as number) - (a[index] as number);
      return body.sort(comparator);
    }
    return body.sort((a, b) => {
      if (a[index] < b[index]) {
        return order === 'asc' ? -1 : 1;
      }
      if (a[index] > b[index]) {
        return order === 'asc' ? 1 : -1;
      }
      return 0;
    });
  });

  /** @internal */
  ngOnInit(): void {
    this.rowLength = this.data()?.body?.length || 0;

    const data = this.data();
    if (data?.body?.length) {
      this.expandedRows = data.body.map(() => false);
      this.initialBody = [...data.body];
    }
  }

  /** @internal */
  ngAfterContentInit(): void {
    const data = this.data();
    const expandRows = this.expandRows();
    const expandableRows = this.expandableRows();
    if (!data && expandRows && expandableRows) {
      if (expandRows.length !== expandableRows.length) {
        throw new Error('drv-table: expandable and expanded rows must be equal in length');
      }
      this.expandedRows = expandRows.map(row => row.expanded);
    }

    if (this.selectedRowsfromData.length) {
      this.allCheckBoxIndeterminate = this.selectedRowsfromData.length !== data.body.length;
      this.allCheckBoxChecked = this.selectedRowsfromData.length === data.body.length;
    }
  }

  /** @internal */
  ngAfterViewInit(): void {
    if (window && 'ResizeObserver' in window) {
      const resizeObserver = new ResizeObserver(([{ target }]) => {
        this.scrollable = target.scrollWidth > target.clientWidth;

        if (this.scrollable) {
          if (!this.fixedHeight()) {
            target.setAttribute('tabindex', '0');
            target.setAttribute('role', 'region');
            target.setAttribute('aria-label', !this.hideCaption() ? this.caption() : undefined);
          }
          target.classList.add('drv-table--scrollable');
        } else {
          if (!this.fixedHeight()) {
            target.removeAttribute('tabindex');
            target.removeAttribute('role');
            target.removeAttribute('aria-label');
          }
          target.classList.remove('drv-table--scrollable');
        }
      });
      resizeObserver.observe(this.elementRef.nativeElement);
    }

    if (this.selectedRowsfromData.length) {
      this.boxes().forEach((box, index) => {
        box.nativeElement.checked = this.selectedRowsfromData.includes(index);
        this.cdRef.detectChanges();
      });
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    const boxes = this.boxes();
    if (changes.data && boxes?.length) {
      this.selectedRows = boxes.reduce((selectedRows, box, index) => {
        box.nativeElement.checked = this.selectedRowsfromData.includes(index);
        this.cdRef.detectChanges();

        if (box.nativeElement.checked) {
          selectedRows.push({
            index,
            row: this.data().body[index],
          });
        }
        return selectedRows;
      }, []);

      this.setAllCheckboxAndEmit();
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    this.observer?.disconnect();
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  protected sortData({ index, order }): void {
    const { body } = this.data();

    if (body) {
      this.data().body = this.sort()(body, index, order);
    }

    this.sorted.emit({
      index,
      order,
    });
  }

  protected allCheckboxChange(checked): void {
    const data = this.data();
    if (!data?.body) {
      this.allSelected.emit(checked);
      return;
    }

    this.selectedRows = checked ? data?.body.map((row, index) => ({ index, row })) : [];
    this.boxes().forEach(box => {
      box.nativeElement.checked = checked;
    });

    this.allCheckBoxChecked = checked;

    if (checked) {
      this.allCheckBoxIndeterminate = false;
    }

    this.allSelected.emit(checked);
    this.selected.emit(this.selectedRows);
  }

  protected checkboxChange(idx: number): void {
    if (!this.data()?.body) {
      this.selected.emit([{ index: idx, row: this.expandRows()[idx] }]);
      return;
    }

    this.selectedRows = this.boxes().reduce((selectedRows, box, index) => {
      if (box.nativeElement.checked) {
        selectedRows.push({
          index,
          row: this.data().body[index],
        });
      }
      return selectedRows;
    }, []);

    this.setAllCheckboxAndEmit();
  }

  protected getMainInfoLabel(index: number): string {
    const data = this.data();
    const headerMainInfoIndex = data?.header?.findIndex(({ mainInfo }) => mainInfo);

    return headerMainInfoIndex > -1
      ? `${data.body[index][headerMainInfoIndex]} ${this.expandableBtnLabel()}`
      : this.expandableBtnLabel();
  }

  protected isMainInfoCell(index: number): boolean {
    if (!this.mainInfoAsHeaderCell()) {
      return false;
    }

    const headerMainInfoIndex = this.data()?.header?.findIndex(({ mainInfo }) => mainInfo);
    return headerMainInfoIndex > -1 && index === headerMainInfoIndex;
  }

  protected expandClick(row: TableExpandRowComponent | TableDataBody): void {
    if ('expanded' in row) {
      row.expanded = !row.expanded;
    }
  }

  protected expandRowClick(index: number): void {
    this.expandedRows[index] = !this.expandedRows[index];
  }

  protected getInitialBodyIndex(item: TableDataBody): number {
    const index = this.initialBody.findIndex(initialItem => initialItem === item);
    return index + 1;
  }

  private setAllCheckboxAndEmit(): void {
    this.allCheckBoxIndeterminate = false;
    this.allCheckBoxChecked = this.selectedRows.length === this.rowLength;

    if (this.selectedRows.length > 0 && this.selectedRows.length !== this.rowLength) {
      this.allCheckBoxIndeterminate = true;
    }

    this.selected.emit(this.selectedRows);
  }
}
