import {
  Component,
  ViewEncapsulation,
  forwardRef,
  ElementRef,
  AfterViewInit,
  booleanAttribute,
  numberAttribute,
  input,
  inject,
  output,
  viewChild,
  contentChild,
} from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import type { Route } from '@angular/router';
import { NgTemplateOutlet } from '@angular/common';

import type { IconType } from '../../types/icons';
import { normalizeEventKey } from '../../utilities/utils';
import { SubTreeViewComponent } from './subtreeview.component';
import { TreeViewService } from './treeview.service';
import { IconComponent } from '../icon/icon.component';

@Component({
  selector: 'drv-treeviewnode',
  template: `
    <a
      #anchor
      role="treeitem"
      [attr.href]="getHref()"
      [attr.aria-selected]="false"
      [attr.aria-expanded]="isExpanded"
      [attr.aria-owns]="subtree()?.id()"
      [attr.aria-current]="currentPage() ? 'page' : null"
      [attr.tabindex]="currentPage() ? 0 : -1"
      (click)="nodeClickHandler($event)"
      (keydown)="nodeKeydownHandler($event)"
    >
      @if (subtree()) {
        <drv-icon
          class="drv-treeview__icon drv-treeview__icon--expand"
          type="chevron-down"
          (click)="iconClickHandler($event)"
        >
        </drv-icon>
      }
      @if (icon()) {
        <drv-icon class="drv-treeview__icon" [type]="icon()"></drv-icon>
      }
      <span class="drv-treeview__text">{{ text() }}</span>
      @if (badgeNumber()) {
        <span class="drv-treeview__badge">{{ badgeNumber() }}</span>
      }
    </a>
    @if (subtree()) {
      <ng-container [ngTemplateOutlet]="subtree().subtreeTemplate()" />
    }
  `,
  encapsulation: ViewEncapsulation.None,
  imports: [NgTemplateOutlet, IconComponent],
  host: {
    role: 'none',
  },
})
export class TreeViewNodeComponent implements AfterViewInit {
  private router = inject(Router, { optional: true })!;
  private treeViewService = inject(TreeViewService);

  readonly text = input<string>(undefined);
  readonly icon = input<IconType>(undefined);
  readonly href = input<string>(undefined);
  readonly route = input<Route[] | string[]>(undefined);
  readonly routeExtras = input<NavigationExtras>(undefined);
  readonly badgeNumber = input<number, unknown>(undefined, { transform: numberAttribute });
  readonly active = input<boolean, unknown>(undefined, { transform: booleanAttribute });
  readonly currentPage = input(false, { transform: booleanAttribute });
  readonly expandChange = output<boolean>();
  readonly itemClick = output<MouseEvent>();
  /** @internal */
  readonly anchor = viewChild<ElementRef>('anchor');
  readonly subtree = contentChild(forwardRef(() => SubTreeViewComponent));
  readonly elRef = inject(ElementRef);

  protected isExpanded = false;
  private anchors: HTMLAnchorElement[] = [];

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    this.treeViewService.anchors$.subscribe(anchor => {
      if (this.anchors.includes(anchor.nativeElement)) {
        this.anchor().nativeElement.focus();
      }
    });
  }

  /** @internal */
  ngAfterViewInit(): void {
    this.anchors = [
      ...this.elRef.nativeElement.querySelectorAll(
        '[role="group"]:not([role="group"] [role="group"]) > drv-treeviewnode > a'
      ),
    ];

    if (this.currentPage()) {
      window.queueMicrotask(() => {
        this.treeViewService.setCurrentPage(this.anchor());
      });
    }

    this.treeViewService.currentPage$.subscribe(anchor => {
      const anchors = [...this.elRef.nativeElement.querySelectorAll('a')].filter(
        a => a !== this.anchor().nativeElement
      );

      if (anchors.includes(anchor.nativeElement)) {
        this.isExpanded = true;
        this.treeViewService.setCurrentPage(this.anchor());
      }
    });
  }

  protected getHref(): string {
    const href = this.href();
    if (href) {
      return href;
    }
    const route = this.route();
    return route && this.router ? '.' + this.router.createUrlTree(route, this.routeExtras() || {}).toString() : '/#';
  }

  protected nodeClickHandler(event: MouseEvent): void {
    if (!this.href()) {
      event.preventDefault();
    }

    if (this.anchors.length) {
      this.isExpanded = !this.isExpanded;
      this.expandChange.emit(this.isExpanded);
    }

    this.itemClick.emit(event);

    const route = this.route();
    if (route) {
      void this.router.navigate(route, this.routeExtras() || {});
    }
  }

  protected iconClickHandler(e: PointerEvent | MouseEvent): void {
    this.isExpanded = !this.isExpanded;
    e.stopPropagation();
    e.preventDefault();
    this.expandChange.emit(this.isExpanded);
  }

  protected nodeKeydownHandler(e: KeyboardEvent): void {
    const key = normalizeEventKey(e);

    const actions = {
      left(): void {
        if (this.isExpanded) {
          this.isExpanded = false;
          this.expandChange.emit(this.isExpanded);
        } else {
          this.treeViewService.focusAnchor(this.anchor());
        }
      },
      right(): void {
        if (!this.isExpanded) {
          this.isExpanded = true;
          this.expandChange.emit(this.isExpanded);
        } else if (this.anchors.length) {
          this.anchors.at(0).focus();
        }
      },
    };

    if (actions[key]) {
      e.preventDefault();
      e.stopPropagation();
      actions[key].call(this);
    }
  }
}
