import { Component, HostBinding, ViewEncapsulation, booleanAttribute, input } from '@angular/core';

export type ButtonBarVariant = 'actions' | 'narrow' | 'navigation' | 'navigation-no-line' | '';
export type ButtonBarPlacing =
  | 'start'
  | 'center'
  | 'end'
  | 'space-between'
  | 'space-between-first'
  | 'space-between-last';
export type ButtonBarLayoutMobile = 'inline' | 'block' | 'wrap';
@Component({
  selector: 'drv-buttonbar',
  template: '<ng-content />',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./buttonbar.scss'],
})
export class ButtonbarComponent {
  readonly variant = input<ButtonBarVariant>('');
  readonly placing = input<ButtonBarPlacing>('start');
  readonly layoutMobile = input<ButtonBarLayoutMobile>('wrap');
  readonly wrap = input(false, { transform: booleanAttribute });

  @HostBinding('class') protected get containerCssClasses(): string {
    const variant = this.variant();
    return [
      'drv-buttonbar',
      variant ? `drv-buttonbar--${variant}` : '',
      `drv-buttonbar--${this.layoutMobile()}`,
      this.wrap() && this.layoutMobile() !== 'wrap' ? 'drv-buttonbar--wrap' : '',
      `drv-buttonbar--place-${this.placing()}`,
    ]
      .filter(Boolean)
      .join(' ');
  }
}
