import {
  Component,
  TemplateRef,
  AfterContentInit,
  ChangeDetectorRef,
  model,
  input,
  inject,
  viewChild,
} from '@angular/core';

import type { IconType } from '../../types/icons';

@Component({
  selector: 'drv-tabpanel',
  template: `
    <ng-template #templateref>
      <ng-content />
    </ng-template>
  `,
  host: {
    '[attr.title]': 'null',
  },
})
export class TabpanelComponent implements AfterContentInit {
  private cdRef = inject(ChangeDetectorRef);

  readonly title = input.required<string>();
  readonly icon = input<IconType>(undefined);
  readonly active = model(false);
  /** @internal */
  readonly panelTemplate = viewChild<TemplateRef<unknown>>('templateref');

  /** @internal */
  ngAfterContentInit(): void {
    // As the panelTemplate prop is set async, the CD is not aware of this change when it
    // initially checks the bindings. Recheck and update after the content projection is complete.
    this.cdRef.detectChanges();
  }
}
