import { Component, ViewEncapsulation, HostBinding, input, inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { format } from '@formkit/tempo';

import { trackByFn } from '../../utilities/utils';
import type { IconType } from '../../types/icons';
import { ButtonComponent } from '../../components/button/button.component';
import { IconComponent } from '../../components/icon/icon.component';

export interface EventInfo {
  icon: IconType;
  text: string;
}

@Component({
  selector: 'drv-eventteaser',
  templateUrl: './eventteaser.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./event-teaser.scss'],
  styles: [
    `
      drv-eventteaser {
        display: block;
      }
      drv-eventteaser drv-icon {
        display: inline;
      }
    `,
  ],
  imports: [ButtonComponent, IconComponent],
})
export class EventteaserComponent {
  private document = inject<Document>(DOCUMENT);

  readonly highlight = input(false);
  readonly firstDate = input<Date>(undefined);
  readonly secondDate = input<Date>(undefined);
  readonly hint = input('');
  readonly warningPrefix = input('');
  readonly moreDates = input(''); // TODO: add event emitter for drv-button in template
  readonly nextEvent = input('');
  readonly title = input('');
  readonly link = input('');
  readonly infos = input<EventInfo[]>([]);
  @HostBinding('attr.title') hostTitle = null;

  protected trackByFn = trackByFn;

  get cssClass(): string {
    const cssclasses = ['drv-event-teaser'];

    if (this.highlight()) {
      cssclasses.push('drv-event-teaser--highlight');
      cssclasses.push('drv-col-sm-bleed--left drv-col-sm-bleed--right drv-bg--ui-02');
    }

    return cssclasses.join(' ');
  }

  get ariaLabel(): string {
    const secondDate = this.secondDate();
    if (secondDate) {
      return `${this.format(this.firstDate(), 'D. MMMM')} bis ${this.format(secondDate, 'D. MMMM')}`;
    }
    return '';
  }

  get dateTimes(): string[] {
    const secondDate = this.secondDate();
    const firstDate = this.firstDate();
    return [firstDate ? this.format(firstDate) : '', secondDate ? this.format(secondDate) : ''];
  }

  get displayDates(): string[] {
    const secondDate = this.secondDate();
    if (secondDate) {
      return [
        `${this.format(this.firstDate(), 'D')} - ${this.format(secondDate, 'D')}`,
        `${this.format(this.firstDate(), 'MMM')} / ${this.format(secondDate, 'MMM YY')}`,
      ];
    }

    const firstDate = this.firstDate();
    const firstDateValue = this.firstDate();
    return [firstDate ? this.format(firstDate, 'D') : '', firstDateValue ? this.format(firstDateValue, 'MMM YY') : ''];
  }

  get locale(): string {
    return this.document.documentElement.lang || 'de';
  }

  private format(date: string | Date, formatString = 'YYYY-MM-DD'): string {
    const dateObj = typeof date === 'string' || typeof date === 'number' ? new Date(date) : date;
    return format(dateObj, formatString, this.locale);
  }
}
