import {
  Directive,
  Component,
  OnDestroy,
  AfterViewInit,
  ElementRef,
  ViewEncapsulation,
  OnInit,
  booleanAttribute,
  input,
  inject,
  output,
  viewChild,
} from '@angular/core';
import { Subscription } from 'rxjs';
import A11yDialog from 'a11y-dialog';
import { SidepanelService } from './sidepanel.service';
import { ButtonComponent } from '../button/button.component';

export type SidepanelVariant = 'default' | 'modal';
export type SidepanelSize = 'xsmall' | 'small' | 'medium' | 'large';

@Directive({ selector: 'drv-sidepanel-content', host: { class: 'drv-sidepanel__content' } })
export class SidepanelContentDirective {}

@Directive({ selector: 'drv-sidepanel-actions', host: { class: 'drv-sidepanel__actions' } })
export class SidepanelActionsDirective {}

@Component({
  selector: 'drv-sidepanel',
  templateUrl: './sidepanel.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./sidepanel.scss'],
  imports: [ButtonComponent],
  host: {
    class: 'drv-sidepanel',
    '[class.drv-sidepanel--xsmall]': 'this.panelSize() === "xsmall"',
    '[class.drv-sidepanel--small]': 'this.panelSize() === "small"',
    '[class.drv-sidepanel--medium]': 'this.panelSize() === "medium"',
    '[class.drv-sidepanel--large]': 'this.panelSize() === "large"',
    '[class.drv-sidepanel--modal]': 'this.variant() === "modal"',
    '[attr.aria-labelledby]': 'this.panelTitleId',
    '[attr.id]': 'this.panelId()',
    role: 'dialog',
  },
})
export class SidepanelComponent implements OnDestroy, AfterViewInit, OnInit {
  private modalService = inject(SidepanelService);
  private elementRef = inject(ElementRef);

  readonly panelTitle = input<string>('');
  readonly closeButtonLabel = input.required<string>();
  readonly hideCloseButton = input(false, { transform: booleanAttribute });
  readonly panelSize = input<SidepanelSize>('medium');
  readonly variant = input<SidepanelVariant>('modal'); // TODO: v8 use 'default' as default variant
  readonly closed = output();
  readonly open = output();
  readonly panelId = input.required<string>();
  readonly button = viewChild<ButtonComponent>('button');

  private dialog: A11yDialog;
  private subscription: Subscription;

  protected get panelTitleId(): string | undefined {
    if (this.panelTitle()) {
      return `sidepanel-heading-${this.panelId()}`;
    }
    return 'Modal';
  }

  /** @internal */
  ngOnInit(): void {
    this.subscribeToModals();
  }

  /** @internal */
  ngAfterViewInit(): void {
    const closeButton = this.button()?.input().nativeElement;

    if (closeButton) {
      closeButton.dataset.a11yDialogHide = '';
    }

    this.dialog = new A11yDialog(this.elementRef.nativeElement);
    this.dialog.on('show', this.toggleDialog.bind(this));
    this.dialog.on('hide', this.toggleDialog.bind(this));

    // TODO: SidepanelVariant='default' (non-modal) needs `aria-modal="false"` attribute - a11y dialog avoids this
    // https://developer.mozilla.org/en-US/docs/Web/Accessibility/ARIA/Reference/Attributes/aria-modal
    // https://github.com/w3c/aria-practices/issues/599
  }

  /** @internal */
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    this.modalService.unRegisterModal(this.panelId());
  }

  private subscribeToModals(): void {
    this.subscription = this.modalService.modals.subscribe(data => {
      if (data) {
        const { panelId, open } = data;
        const { shown } = this.dialog;

        if (panelId === this.panelId() && shown !== open) {
          this.dialog[open ? 'show' : 'hide']();
        }
      }
    });
  }

  private toggleDialog(event: CustomEvent): void {
    const isOpen = event.type === 'show';

    this.subscription?.unsubscribe();
    void this.modalService
      .updateModal({
        panelId: this.panelId(),
        open: isOpen,
      })
      .then(() => {
        this.subscribeToModals();
      });

    this[isOpen ? 'open' : 'closed'].emit();
  }
}
