import { Component, ViewEncapsulation, forwardRef, input, contentChildren } from '@angular/core';
import { ButtonComponent } from '../../components/button/button.component';
import { GridDirective } from '../../components/grid/grid.directive';
import { RowDirective } from '../../components/grid/row.directive';
import { ColDirective } from '../../components/grid/col.directive';

@Component({
  selector: 'drv-apppage',
  templateUrl: './apppage.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./layout.scss'],
  styles: [
    `
      drv-apppage {
        display: contents;
      }
    `,
  ],
  imports: [GridDirective, RowDirective, ColDirective],
})
export class ApppageComponent {
  readonly fullWidth = input(false);
  readonly _buttons = contentChildren(forwardRef(() => ButtonComponent));

  get hasHelpButton(): boolean {
    return !!this._buttons().find(button => button.cssClass().includes('drv-help__btn'));
  }
}
