import { Directive, ElementRef, Renderer2, OnChanges, SimpleChanges, input, inject } from '@angular/core';

@Directive({
  // eslint-disable-next-line @angular-eslint/directive-selector
  selector: '[aria-attributes]',
})
export class AriaAttributeDirective implements OnChanges {
  private elRef = inject(ElementRef);
  private renderer = inject(Renderer2);

  readonly ariaAttributes = input<Record<string, unknown>>({}, { alias: 'aria-attributes' });

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.ariaAttributes) {
      this.setAttributes(changes.ariaAttributes.currentValue, changes.ariaAttributes.previousValue);
    }
  }

  private setAttributes(current: unknown, previous: unknown): void {
    if (Object.prototype.toString.call(current) !== '[object Object]') {
      return;
    }

    const keys = Object.keys(current);
    const prevKeys = previous ? Object.keys(previous) : [];

    if (prevKeys.length) {
      prevKeys
        .filter(key => !keys.includes(key))
        .forEach(key => {
          this.renderer.removeAttribute(this.elRef.nativeElement, `aria-${key}`);
        });
    }

    keys.forEach(key => {
      if (current[key] === '' || current[key] === undefined || current[key] === null) {
        this.renderer.removeAttribute(this.elRef.nativeElement, `aria-${key}`);
      } else {
        this.renderer.setAttribute(this.elRef.nativeElement, `aria-${key}`, current[key]);
      }
    });
  }
}
