import { Directive, input } from '@angular/core';

@Directive({
  selector: '[drv-grid]',
  host: {
    class: 'drv-grid',
    '[class.drv-grid--full-width]': 'this.fullWidth()',
    '[class.drv-grid--condensed]': 'this.condensed()',
    '[class.drv-grid--narrow]': 'this.narrow()',
  },
})
export class GridDirective {
  readonly fullWidth = input(false);
  readonly condensed = input(false);
  readonly narrow = input(false);
}
