import { Component, ElementRef, OnInit, ViewEncapsulation, booleanAttribute, input, inject } from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import { trackByFn } from '../../utilities/utils';
import { IconComponent } from '../icon/icon.component';

@Component({
  selector: 'drv-list',
  templateUrl: './list.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./list.scss'],
  styles: [
    `
      drv-list {
        display: block;
      }
    `,
  ],
  imports: [IconComponent, NgTemplateOutlet],
})
export class ListComponent implements OnInit {
  private elementRef = inject(ElementRef);

  readonly items = input<string[]>([]);
  readonly type = input<'ol' | 'ul' | 'check'>('ul');
  readonly removeHost = input(true, { transform: booleanAttribute });

  protected trackByFn = trackByFn;

  /** @internal */
  ngOnInit(): void {
    if (!this.removeHost()) {
      return;
    }
    const nativeElement: HTMLElement = this.elementRef.nativeElement;
    const parentElement: HTMLElement = nativeElement.parentElement;

    // move all children out of the element
    while (nativeElement.firstChild) {
      parentElement.insertBefore(nativeElement.firstChild, nativeElement);
    }

    // remove the empty element(the host)
    parentElement.removeChild(nativeElement);
  }
}
