import { BehaviorSubject } from 'rxjs';
import { Injectable, inject } from '@angular/core';
import { LiveAnnouncer } from '@angular/cdk/a11y';

import { nanoid } from 'nanoid';
import { PortalContentType } from '../../types';

export type NotificationVariant = 'alert' | 'warning' | 'success' | 'info';

export interface Notification {
  id?: string;
  variant: NotificationVariant;
  title: string;
  text?: string;
  portalContent?: PortalContentType;
  isRemoveAble?: boolean;
  needsFeedback?: boolean;
  duration?: number;
  scrOnly?: boolean;
}

interface LocalNotification extends Notification {
  notificationId?: string;
}

@Injectable({
  providedIn: 'root',
})
export class NotificationService {
  private _notifications = new BehaviorSubject<LocalNotification[]>([]);
  private _removals = new BehaviorSubject<string[]>([]);
  private _announcer: LiveAnnouncer;

  readonly notifications = this._notifications.asObservable();

  readonly removals = this._removals.asObservable();

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const liveAnnouncer = inject(LiveAnnouncer);

    this._announcer = liveAnnouncer;
  }

  update(data: LocalNotification): void {
    if (data.scrOnly) {
      void this._announcer.announce(`${data.title || ''} ${data.text || ''}`);
      return;
    }

    const values: LocalNotification[] = this._notifications.getValue();

    values.push({
      notificationId: nanoid(),
      ...data,
    });

    this._notifications.next(values);
  }

  remove(id: string): void {
    const values: LocalNotification[] = this._notifications.getValue();
    const index = values.findIndex(({ notificationId: idToFind }) => idToFind === id);
    values.splice(index, 1);
    this._notifications.next(values);
    this._removals.next([id]);
  }

  clear(): void {
    this._notifications.next([]);
  }
}
