import {
  Component,
  ViewEncapsulation,
  forwardRef,
  booleanAttribute,
  input,
  ContentChildren,
  QueryList,
} from '@angular/core';

import { normalizeEventKey } from '../../utilities/utils';
import { TreeViewNodeComponent } from './treeviewnode.component';

// TODO: Add keyboard typeahead navigation

@Component({
  selector: 'drv-treeview',
  template: `
    <div
      role="tree"
      [attr.aria-label]="label()"
      (keydown)="keyEventHandler($event)"
      (focusin)="focusHandler($event)"
      tabindex="-1"
    >
      <ng-content></ng-content>
    </div>
  `,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./treeview.scss'],
  host: {
    class: 'drv-treeview',
    '[class.drv-treeview--condensed]': 'this.condensed()',
    '[attr.aria-label]': 'this.label()',
    role: 'navigation',
  },
})
export class TreeViewComponent {
  public readonly label = input.required<string>();
  public readonly condensed = input(true, { transform: booleanAttribute });
  private activeIndex = 0;

  @ContentChildren(forwardRef(() => TreeViewNodeComponent), { descendants: true })
  protected items: QueryList<TreeViewNodeComponent>;

  protected keyEventHandler(e: KeyboardEvent): void {
    const key = normalizeEventKey(e);

    let next = Math.min(this.activeIndex + 1, this.items.toArray().length - 1);
    let prev = Math.max(this.activeIndex - 1, 0);

    while (
      this.items.get(next) &&
      this.items.toArray().at(next).anchor().nativeElement.offsetParent === null &&
      this.items.get(next).anchor().nativeElement.offsetParent === null &&
      next < this.items.toArray().length
    ) {
      next = next + 1;
    }

    while (
      this.items.get(prev) &&
      this.items.toArray().at(prev).anchor().nativeElement.offsetParent === null &&
      prev >= 0
    ) {
      prev = prev - 1;
    }

    const actions = {
      down(): void {
        if (!this.items.get(next)) {
          return;
        }
        this.items.get(next).anchor().nativeElement.focus();
      },
      up(): void {
        if (!this.items.get(prev)) {
          return;
        }
        this.items.get(prev).anchor().nativeElement.focus();
      },
      home(): void {
        this.items.first.anchor().nativeElement.focus();
      },
      end(): void {
        this.items.last.anchor().nativeElement.focus();
      },
    };

    if (actions[key]) {
      e.preventDefault();
      e.stopPropagation();
      actions[key].call(this);
    }
  }

  protected focusHandler(e: FocusEvent): void {
    const { target: anchor } = e;
    this.activeIndex = this.items.toArray().findIndex(item => item.anchor().nativeElement === anchor);
  }
}
