import {
  Component,
  ViewEncapsulation,
  TemplateRef,
  ChangeDetectorRef,
  AfterContentInit,
  input,
  inject,
  viewChild,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
@Component({
  selector: 'drv-itemlistitem',
  template: `
    <ng-container *ngTemplateOutlet="templateref" />
    <ng-template #templateref>
      <ng-content />
    </ng-template>
  `,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./itemlist.scss'],
  imports: [NgTemplateOutlet],
  host: {
    role: 'listitem',
    class: 'drv-itemlist__item',
    '[class]': 'this.hostClass() || null',
  },
})
export class ItemlistItemComponent implements AfterContentInit {
  private cdRef = inject(ChangeDetectorRef);

  public readonly hostClass = input<string>(undefined);
  /** @internal */
  readonly itemTemplate = viewChild<TemplateRef<unknown>>('templateref');

  /** @internal */
  ngAfterContentInit(): void {
    this.cdRef.detectChanges();
  }
}
