import {
  Directive,
  Component,
  OnDestroy,
  AfterViewInit,
  ElementRef,
  ViewEncapsulation,
  OnInit,
  booleanAttribute,
  input,
  inject,
  output,
  viewChild,
} from '@angular/core';
import { Subscription } from 'rxjs';
import A11yDialog from 'a11y-dialog';
import { Undo, suppressOthers } from 'aria-hidden';

import { ButtonComponent, type IconOnlyType } from '../button/button.component';
import { ModalService, type ModalRole } from './modal.service';

@Directive({ selector: 'drv-modal-content', host: { class: 'drv-modal__content' } })
export class ModalContentDirective {}

@Directive({ selector: 'drv-modal-actions', host: { class: 'drv-modal__actions' } })
export class ModalActionsDirective {}

@Component({
  selector: 'drv-modal',
  templateUrl: './modal.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./modal.scss'],
  imports: [ButtonComponent],
  host: {
    class: 'drv-modal',
    '[attr.role]': 'this.role()',
    '[attr.aria-labelledby]': 'this.modalTitleId',
    '[attr.id]': 'this.modalId()',
  },
})
export class ModalComponent implements OnDestroy, AfterViewInit, OnInit {
  private modalService = inject(ModalService);
  private elementRef = inject(ElementRef);

  readonly modalTitle = input('');
  readonly closeButtonLabel = input('');
  readonly closeButtonIconOnlyType = input<IconOnlyType>(undefined);
  readonly hideCloseButton = input(false, { transform: booleanAttribute });

  readonly closed = output();
  readonly open = output();

  readonly button = viewChild<ButtonComponent>('button');

  readonly role = input<ModalRole>('dialog');
  readonly modalId = input.required<string>();

  private undo: Undo;
  private dialog: A11yDialog;
  private subscription: Subscription;

  protected get modalTitleId(): string | undefined {
    if (this.modalTitle()) {
      return `modal-heading-${this.modalId()}`;
    }
    return 'Modal';
  }

  protected get hideButton(): boolean {
    if (this.role() === 'alertdialog') {
      return true;
    }

    return this.hideCloseButton();
  }

  /** @internal */
  ngOnInit(): void {
    this.subscribeToModals();
  }

  /** @internal */
  ngAfterViewInit(): void {
    const closeButton = this.button()?.input().nativeElement;

    if (closeButton) {
      closeButton.dataset.a11yDialogHide = '';
    }

    this.dialog = new A11yDialog(this.elementRef.nativeElement);
    this.dialog.on('show', this.toggleDialog.bind(this));
    this.dialog.on('hide', this.toggleDialog.bind(this));
  }

  /** @internal */
  ngOnDestroy(): void {
    this.dialog.destroy();
    this.modalService.unRegisterModal(this.modalId());
    this.subscription.unsubscribe();
  }

  private subscribeToModals(): void {
    this.subscription = this.modalService.modals.subscribe(data => {
      if (data) {
        const { id, open } = data;
        const { shown } = this.dialog;

        if (id === this.modalId() && shown !== open) {
          this.dialog[open ? 'show' : 'hide']();
        }
      }
    });
  }

  private toggleDialog(event: CustomEvent): void {
    const isOpen = event.type === 'show';

    this.subscription?.unsubscribe();

    void this.modalService
      .updateModal({
        id: this.modalId(),
        open: isOpen,
      })
      .then(() => {
        this.subscribeToModals();

        if (isOpen) {
          this.undo = suppressOthers(this.elementRef.nativeElement);
        } else if (this.undo) {
          this.undo();
        }
      });

    this[isOpen ? 'open' : 'closed'].emit();
  }
}
