// TODO: reintroduce as ViewTransition odr delete
import { Component, HostBinding, ViewEncapsulation, AfterViewInit } from '@angular/core';
import { animate, style, transition, trigger, keyframes } from '@angular/animations';
import { NgTemplateOutlet, NgClass } from '@angular/common';

import { ItemlistComponent } from './itemlist.component';

@Component({
  selector: 'drv-itemlist-animated',
  template: `
    @for (item of items(); track item; let i = $index) {
      <div role="listitem" [ngClass]="['drv-itemlist__item', item.hostClass() || '']" @ListItemAnimation>
        <ng-container [ngTemplateOutlet]="item.itemTemplate()"></ng-container>
      </div>
    }
    <ng-content select="drv-pictogram" />
  `,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./itemlist.scss'],
  imports: [NgTemplateOutlet, NgClass],
  animations: [
    trigger('ListItemAnimation', [
      transition(':enter', [
        animate(
          '1s',
          keyframes([
            style({ opacity: 0, offset: 0 }),
            style({ opacity: 0, offset: 0.75 }),
            style({ opacity: 1, offset: 1.0 }),
          ])
        ),
      ]),
      transition(':leave', [
        animate(
          '1s',
          keyframes([
            style({ opacity: 1, offset: 0 }),
            style({ opacity: 1, offset: 0.75 }),
            style({ opacity: 0, offset: 1.0 }),
          ])
        ),
      ]),
    ]),
  ],
})
export class ItemlistAnimatedComponent extends ItemlistComponent implements AfterViewInit {
  /** @internal */
  @HostBinding('@.disabled') public animationsDisabled = true;

  constructor() {
    super();
  }

  /** @internal */
  ngAfterViewInit(): void {
    window.setTimeout(() => {
      this.animationsDisabled =
        window && window.matchMedia ? window.matchMedia('(prefers-reduced-motion: reduce)').matches : false;
    }, 10);
  }
}
