import { Component, ViewEncapsulation, input } from '@angular/core';

export type SpacerContentPlacing =
  | 'start'
  | 'center'
  | 'end'
  | 'space-between'
  | 'space-between-first'
  | 'space-between-last';
export type SpacingVariant = 'default' | 'contents' | 'sections';

@Component({
  selector: 'drv-spacer',
  template: '<ng-content />',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./spacer.scss'],
  host: {
    '[class]': 'this.containerCssClasses',
  },
})
export class SpacerComponent {
  readonly placing = input<SpacerContentPlacing>('start');
  readonly variant = input<SpacingVariant>('default');

  protected get containerCssClasses(): string {
    return ['drv-spacer', `drv-spacer--${this.variant()}`, `drv-spacer--place-${this.placing()}`]
      .filter(Boolean)
      .join(' ');
  }
}
