import { booleanAttribute, Component, HostBinding, ViewEncapsulation, input, output } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators, ReactiveFormsModule } from '@angular/forms';
import { nanoid } from 'nanoid';

import { NotificationService } from '../notification/notification.service';
import { ButtonComponent } from '../button/button.component';
import { StarRatingComponent } from '../starrating/starrating.component';
import { RichtextComponent } from '../richtext/richtext.component';
import { TextareaComponent } from '../textarea/textarea.component';

export interface NfcFeedbackResult {
  identifier: string;
  value?: number | string;
}

@Component({
  selector: 'drv-nfk',
  templateUrl: './nfc.component.html',
  imports: [ReactiveFormsModule, StarRatingComponent, RichtextComponent, ButtonComponent, TextareaComponent],
  encapsulation: ViewEncapsulation.None,
  styleUrl: './nfc.component.scss',
})
export class NFComponent {
  readonly id = input<string>(`nfc-${nanoid()}`);
  readonly required = input(true, { transform: booleanAttribute });
  readonly feedbackTitle = input('Ihr Feedback');
  readonly question = input('Wie einfach war es f√ºr Sie, dieses Verfahren anzuwenden?');
  readonly buttonText = input('Feedback abschicken');
  readonly successTitle = input('Vielen Dank f√ºr Ihr Feedback');
  readonly successText = input(
    'Mit Ihrem Feedback helfen Sie uns, die Qualit√§t unserer Angebote stetig zu verbessern.'
  );
  readonly errorText = input('Bitte treffen Sie eine Auswahl');
  readonly hasFreeText = input(true, { transform: booleanAttribute });
  readonly freeTextLabel = input('Was k√∂nnten wir verbessern?');
  readonly freeTextMaxLength = input(4000);

  readonly formSubmit = output<NfcFeedbackResult[]>();

  public form: UntypedFormGroup;

  protected isSubmitted = false;
  protected invalidOnSubmit = false;

  private questionIdentifier = 'simplicity'; // Use Case: Online-Verfahren
  private freeTextIdentifier = 'freeText';

  constructor(
    private nService: NotificationService,
    private fb: UntypedFormBuilder
  ) {
    this.form = fb.group({
      starrating: ['', [Validators.required]],
      [this.freeTextIdentifier]: [''],
    });
  }

  @HostBinding('attr.class') protected get containerCssClasses(): string {
    return ['drv-nfk'].filter(Boolean).join(' ');
  }

  public onSubmit(event: Event): void {
    event.preventDefault();
    this.invalidOnSubmit = this.form.status !== 'VALID';

    if (!this.invalidOnSubmit) {
      this.formSubmit.emit([
        {
          identifier: this.questionIdentifier,
          value: this.form.controls.starrating.value,
        },
        {
          identifier: this.freeTextIdentifier,
          value: this.form.controls[this.freeTextIdentifier].value,
        },
      ]);

      this.isSubmitted = true;

      window.queueMicrotask(() => {
        this.nService.update({
          id: 'nfk_success',
          variant: 'success',
          title: this.successTitle(),
          text: this.successText(),
          duration: 3,
          scrOnly: true,
        });
      });
    }
  }
}
