import {
  Component,
  OnChanges,
  OnInit,
  ViewEncapsulation,
  booleanAttribute,
  numberAttribute,
  input,
  inject,
} from '@angular/core';
import { PictogramsType } from '../../types/pictograms';
import { ConfigService } from '../../services/config.service';

export { type PictogramsType };
@Component({
  selector: 'drv-pictogram',
  templateUrl: './pictogram.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./pictogram.scss'],
  host: {
    class: 'drv-pictogram-host',
  },
})
export class PictogramComponent implements OnInit, OnChanges {
  private configService = inject(ConfigService);

  readonly label = input<string>(undefined);
  readonly type = input.required<PictogramsType>();
  readonly size = input(70, { transform: numberAttribute });
  readonly ariaHidden = input<boolean | null, unknown>(null, { transform: booleanAttribute });

  protected svgSrc: string;
  protected pictogramSpritePath: string;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const configService = this.configService;

    this.pictogramSpritePath = configService.config.pictogramSpritePath;
  }

  protected get xLink(): string {
    return `${this.pictogramSpritePath}#${this.svgSrc}`;
  }

  protected classNames = 'drv-pictogram';

  protected get sizeInRem(): number {
    return this.size() / 16;
  }

  /** @internal */
  ngOnInit(): void {
    this.setSource();
  }

  /** @internal */
  ngOnChanges(): void {
    window.setTimeout(() => this.setSource(), 10);
  }

  private setSource(): void {
    this.svgSrc = this.type();
  }
}
