import { Component, ViewEncapsulation, booleanAttribute, numberAttribute, input, inject } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';

import type { BackgroundUILightDark } from '../../types';
import type { IconType } from '../../types/icons';

import { trackByFn } from '../../utilities/utils';
import { GridDirective } from '../grid/grid.directive';
import { RowDirective } from '../grid/row.directive';
import { ColDirective } from '../grid/col.directive';
import { IconComponent } from '../icon/icon.component';

export interface FooterLinkItem {
  href?: string;
  text: string;
  htmltarget?: string;
  htmltitle?: string;
  icon?: IconType;
  // createUrlTree expects any[] https://angular.dev/api/router/Router#createUrlTree
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  route?: any[];
  routeExtras?: NavigationExtras;
  callbackFn?: () => void;
}

@Component({
  selector: 'drv-footer',
  templateUrl: './footer.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./footer.scss'],
  imports: [GridDirective, RowDirective, ColDirective, IconComponent],
  host: {
    role: 'contentinfo',
    class: 'drv-footer',
    '[class.drv-footer--full-width]': 'this.fullWidth()',
    '[class.drv-footer--dark]': 'this.backgroundUI() === "dark"',
    '[class.drv-footer--light]': 'this.backgroundUI() === "light"',
  },
})
export class FooterComponent {
  private router = inject(Router, { optional: true })!;

  readonly items = input<FooterLinkItem[]>([]);
  readonly copyright = input<string>(undefined);
  readonly version = input<string>(undefined);
  readonly footertext = input<string>(undefined);
  readonly columns = input(0, { transform: numberAttribute });
  readonly fullWidth = input(false, { transform: booleanAttribute });
  readonly showVersion = input(false, { transform: booleanAttribute });
  readonly backgroundUI = input<BackgroundUILightDark>('dark');

  protected trackByFn = trackByFn;

  protected getHref(item: FooterLinkItem): string {
    if (item.href) {
      return item.href;
    }
    return item.route && this.router
      ? '.' + this.router.createUrlTree(item.route, item.routeExtras || {}).toString()
      : '/#';
  }

  protected itemClickHandler(e: MouseEvent, item: FooterLinkItem): void {
    if (item.route) {
      e.preventDefault();
      void this.router.navigate(item.route, item.routeExtras || {});
    }

    if (item.callbackFn) {
      item.callbackFn();
    }
  }
}
