import {
  Component,
  HostBinding,
  ViewEncapsulation,
  booleanAttribute,
  HostListener,
  ElementRef,
  model,
  input,
  inject,
  output,
  viewChild,
} from '@angular/core';
import { NgClass } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { nanoid } from 'nanoid';

import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { BackgroundUI } from '../../types';

export interface ToggleChange {
  source: ToggleComponent;
  value: boolean;
}

export type ToggleLayout = 'block' | 'inline';
export type ToggleLabelStyle = 'bold' | 'normal';

@Component({
  selector: 'drv-toggle',
  templateUrl: './toggle.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./toggle.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [NgClass, FormsModule],
})
export class ToggleComponent {
  readonly valueAccessor = inject<ValueAccessorDirective<boolean>>(ValueAccessorDirective);

  readonly id = input<string>(`toggle-${nanoid()}`);
  readonly externalLabelId = input<string>(undefined);
  readonly name = input<string>(undefined);
  readonly disabled = model<boolean>(false);
  readonly value = input<string>(undefined);
  readonly label = input.required<string>();
  readonly labelHidden = input(false, { transform: booleanAttribute });
  readonly backgroundUI = input<BackgroundUI>('light');
  readonly layout = input<ToggleLayout>('block');
  readonly labelStyle = input<ToggleLabelStyle>('bold');
  readonly toggleStateOff = input('Aus');
  readonly toggleStateOn = input('An');
  readonly input = viewChild<ElementRef<HTMLInputElement>>('input');
  readonly toggled = output<ToggleChange>();
  @HostBinding('attr.id') protected externalId = '';
  @HostBinding('class') protected hostClass = 'drv-toggle-host';

  private innerValue?: boolean;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const valueAccessor = this.valueAccessor;

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      if (v !== this.innerValue) {
        this.innerValue = !!v;
        const inputValue = this.input();
        if (inputValue?.nativeElement) {
          inputValue.nativeElement.checked = !!v;
        }
      }
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  @HostBinding('attr.title') protected get title(): string {
    return this.labelHidden() ? this.label() : null;
  }

  protected get containerCssClasses(): string {
    return ['drv-toggle', `drv-toggle--${this.backgroundUI()}`, `drv-toggle--${this.layout()}`]
      .filter(Boolean)
      .join(' ');
  }

  protected get labelClasses(): string {
    return [
      'drv-toggle__label',
      `drv-toggle__label--${this.labelStyle()}`,
      this.labelHidden() || (!this.label() && this.title) ? 'drv-toggle__label--hidden' : null,
    ]
      .filter(Boolean)
      .join(' ');
  }

  get model(): boolean {
    return this.innerValue;
  }

  set model(value: boolean) {
    if (value !== this.innerValue) {
      this.innerValue = value;
      this.valueAccessor.valueChange(value);

      this.toggled.emit({
        source: this,
        value,
      });
    }
  }

  @HostListener('focusout', ['$event']) protected onFocusout(event: FocusEvent): void {
    this.valueAccessor.onBlur(event);
  }

  protected nativeChange(e: Event): void {
    e.stopPropagation();
  }
}
