import { Pipe, PipeTransform } from '@angular/core';

/*
 * Formats a byte number in a human readable Size
 * See: https://stackoverflow.com/questions/10420352/converting-file-size-in-bytes-to-human-readable-string/10420404
 * Takes an decimal point argument that defaults to 0.
 * Usage:
 *   value | decimalPoints:points
 * Example (for de-DE locale):
 *   {{ 156 * 1024 | decimalPoints: 2 }}
 *   formats to: 156,00 kB
 */
@Pipe({ name: 'humanFileSize' })
export class HumanFileSizePipe implements PipeTransform {
  transform(input: string, decimalPoints = 0): string {
    let bytes = parseInt(input, 10);

    if (Number.isNaN(bytes)) {
      return input;
    }
    const thresh = 1024;
    const toLocaleStringOptions = {
      minimumFractionDigits: decimalPoints,
      maximumFractionDigits: decimalPoints,
    };

    if (Math.abs(bytes) < thresh) {
      return bytes + ' B';
    }

    const units = ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    let u = -1;
    const r = 10 ** decimalPoints;

    do {
      bytes /= thresh;
      ++u;
    } while (Math.round(Math.abs(bytes) * r) / r >= thresh && u < units.length - 1);

    return `${bytes.toLocaleString(window.navigator.language, toLocaleStringOptions)} ${units[u]}`;
  }
}
