import { Component, HostBinding, ViewEncapsulation, booleanAttribute, input, model } from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { nanoid } from 'nanoid';

import type { BackgroundUI } from '../../types';
import { trackByFn } from '../../utilities/utils';
import { SymbolsComponent } from '../symbols/symbols.component';
import { IconComponent } from '../icon/icon.component';

export interface FormItemMessage {
  message: string;
  type?: string;
}

@Component({
  selector: 'drv-formitem',
  templateUrl: './formitem.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./form-item.scss', './field-message.scss'],
  imports: [NgClass, NgTemplateOutlet, SymbolsComponent, IconComponent],
  host: {
    class: 'drv-formitem',
    '[class.drv-form__item--error]': 'hasError()',
    '[class.drv-form__item--disabled]': 'disabled()',
  },
})
export class FormitemComponent {
  readonly id = input<string>(`formitem-${nanoid()}`);
  readonly cssClass = input('');
  readonly hasError = model<boolean>(false);
  readonly errorText = model<FormItemMessage[]>([]);
  readonly errorPrefix = input('Fehler:');
  readonly hasInfo = model<boolean>(false);
  readonly infoText = model<FormItemMessage[]>([]);
  readonly infoPrefix = input('Hinweis:');
  readonly hasWarning = model<boolean>(false);
  readonly warningText = model<FormItemMessage[]>([]);
  readonly warningPrefix = input('Warnung:');
  readonly disabled = model<boolean>(false);
  readonly collapseMessages = model(false);
  readonly backgroundUI = input<BackgroundUI>('light');
  readonly announceChanges = input(true, { transform: booleanAttribute });

  @HostBinding('attr.id') protected externalId = '';

  protected trackByFn = trackByFn;

  @HostBinding('class') protected get containerCssClasses(): string {
    return ['drv-form__item', `drv-form__item--${this.backgroundUI()}`, ...this.cssClass().split(' ')]
      .filter(Boolean)
      .join(' ');
  }
}
