import {
  Component,
  AfterViewInit,
  HostBinding,
  HostListener,
  ViewEncapsulation,
  OnChanges,
  SimpleChanges,
  ElementRef,
  TemplateRef,
  OnDestroy,
  booleanAttribute,
  numberAttribute,
  model,
  input,
  inject,
  viewChild,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import flatpickr from 'flatpickr';
import type { BaseOptions } from 'flatpickr/dist/types/options';

import { German } from 'flatpickr/dist/esm/l10n/de.js';
import { FormitemComponent } from '../formitem/formitem.component';
import { LabelComponent } from '../label/label.component';
import { IconComponent } from '../icon/icon.component';
import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { ConfigService } from '../../services/config.service';

@Component({
  selector: 'drv-timepicker',
  templateUrl: './timepicker.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['../datepicker/datepicker.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [NgTemplateOutlet, FormitemComponent, LabelComponent, IconComponent],
  host: {
    class: 'drv-timepicker',
  },
})
export class TimePickerComponent extends FormitemComponent implements AfterViewInit, OnChanges, OnDestroy {
  private configService = inject(ConfigService);
  private elementRef = inject(ElementRef);
  readonly valueAccessor = inject<ValueAccessorDirective<string>>(ValueAccessorDirective);

  readonly disabled = model<boolean>(false);
  readonly required = model<boolean>(undefined);
  readonly native = input(false, { transform: booleanAttribute });
  readonly inline = input(false, { transform: booleanAttribute });
  readonly pickrLocale = input(German);
  readonly label = input<string>(undefined);
  readonly nextMonthButtonLabel = input('N√§chster Monat');
  readonly prevMonthButtonLabel = input('Vorheriger Monat');
  readonly minuteIncrement = input(15, { transform: numberAttribute });
  readonly placeholder = input('00:00');
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });
  readonly input = viewChild<ElementRef>('input');

  readonly inlineHolder = viewChild<ElementRef>('inlineHolder');
  @HostBinding('attr.id') protected externalId = '';

  @HostBinding('class') protected get containerCssClasses(): string {
    return [...this.cssClass().split(' ')].filter(Boolean).join(' ');
  }

  protected iconSpritePath: string;

  private innerValue: string;
  private instance: flatpickr.Instance;
  private changeableOptions: string[] = ['minuteIncrement'];

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    super();
    const configService = this.configService;
    const valueAccessor = this.valueAccessor;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);
    this.iconSpritePath = configService.config.spritePath;

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.innerValue = v;
      this.instance?.setDate(v, false, 'H:i');
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  protected get cssClasses(): string[] {
    const defaultClasses = ['drv-text-input', 'drv-datepicker__input'];

    return [
      [...defaultClasses, this.inline() ? 'drv-datepicker__input--calendar' : ''].join(' '),
      [...defaultClasses, `drv-text-input--${this.backgroundUI()}`].join(' '),
    ];
  }

  protected get describedBy(): string | null {
    if (this.hasError() || this.hasInfo() || this.hasWarning()) {
      return `${this.id()}_error`;
    }

    return null;
  }

  @HostListener('focusout', ['$event']) protected onFocusout(event: FocusEvent): void {
    if (event.target === this.input().nativeElement) {
      this.valueAccessor.onBlur(event);
    }
  }

  /** @internal */
  ngAfterViewInit(): void {
    if (this.native()) {
      return;
    }

    const pickrOptions = {
      enableTime: true,
      noCalendar: true,
      dateFormat: 'H:i',

      time_24hr: true,
      allowInput: true,
      minuteIncrement: this.minuteIncrement(),
      wrap: true,
    };

    if (this.innerValue) {
      Object.assign(pickrOptions, {
        defaultDate: this.innerValue,
      });
    }

    const pickrLocale = this.pickrLocale();
    if (pickrLocale) {
      Object.assign(pickrOptions, {
        locale: pickrLocale,
      });
    }

    const inline = this.inline();
    if (inline) {
      Object.assign(pickrOptions, {
        inline: true,
        appendTo: this.inlineHolder().nativeElement,
        wrap: false,
      });
    }

    this.instance = flatpickr(
      inline ? this.input().nativeElement : this.elementRef.nativeElement.querySelector('.drv-form__item'),
      pickrOptions
    );
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (this.instance) {
      Object.keys(changes)
        .filter(changeKey => this.changeableOptions.includes(changeKey))
        .forEach(inputKey => {
          this.instance.set(inputKey as keyof BaseOptions, this[inputKey]);
        });
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    if (this.instance) {
      this.instance.destroy();
    }
  }

  protected onChange({ target }): void {
    this.innerValue = target.value;
    this.valueAccessor.valueChange(target.value);
  }
}
