import { Component, ViewEncapsulation, booleanAttribute, input } from '@angular/core';

export type CardImageVariant = 'portrait-2x3' | 'portrait-3x4' | 'landscape-4x3' | 'landscape-1x1';

@Component({
  selector: 'drv-card-image',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./card.scss'],
  template: '<ng-content />',
  host: {
    '[class]': 'this.containerCssClasses',
  },
})
export class CardImageComponent {
  readonly variant = input<CardImageVariant>('landscape-4x3');
  readonly fill = input(false, { transform: booleanAttribute });

  protected get containerCssClasses(): string {
    const variant = this.variant();
    return ['drv-card__image', variant && `drv-card__image--${variant}`, this.fill() && 'drv-card__image--fill']
      .filter(Boolean)
      .join(' ');
  }
}
