import { Directive, Component, ViewEncapsulation, booleanAttribute, input } from '@angular/core';

export type CardVariant = 'default' | 'overlayed-actions';

@Directive({ selector: 'drv-card-content', host: { class: 'drv-card__content' } })
export class CardContentDirective {}

@Directive({ selector: 'drv-card-actions', host: { class: 'drv-card__actions' } })
export class CardActionsDirective {}

@Component({
  selector: 'drv-card',
  templateUrl: './card.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./card.scss'],
  host: {
    class: 'drv-card',
    '[class.drv-card--clickable]': 'this.clickable()',
    '[class.drv-card--overlayed-actions]': 'this.variant !== "default"',
    '[attr.id]': 'this.cardId()',
    '[attr.aria-labelledby]': '`${this.cardId()}_header`',
    role: 'region',
  },
})
export class CardComponent {
  readonly clickable = input<boolean, unknown>(undefined, { transform: booleanAttribute });
  readonly variant = input<CardVariant>('default');
  readonly cardId = input.required<string>();
}
