import {
  Component,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  HostBinding,
  ViewEncapsulation,
  OnChanges,
  SimpleChanges,
  model,
  input,
  inject,
  output,
  viewChild,
  ElementRef,
  EventEmitter,
  Output,
  OnInit,
  OnDestroy,
} from '@angular/core';
import { Subject, takeUntil } from 'rxjs';
import { nanoid } from 'nanoid';
import { AriaAttributes, BackgroundUI } from '../../types';
import { ConfigService } from '../../services/config.service';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';

import { InputGroupComponent } from '../inputgroup/inputgroup.component';
import { InputGroupService } from '../inputgroup/inputgroup.service';

export interface RadioChange {
  source: RadiobuttonComponent;
  value: string | number;
}

@Component({
  selector: 'drv-radiobutton',
  templateUrl: './radiobutton.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./radiobutton.scss'],
  imports: [AriaAttributeDirective],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RadiobuttonComponent implements OnChanges, OnInit, OnDestroy {
  private parent = inject(InputGroupComponent, { optional: true, host: true, skipSelf: true })!;
  private inputGroupService = inject(InputGroupService);
  private changeDetector = inject(ChangeDetectorRef);
  private configService = inject(ConfigService);

  readonly id = input<string>(`radiobutton-${nanoid()}`);
  readonly name = model<string>(undefined);
  readonly value = input<string | number>(undefined);
  readonly disabled = model<boolean>(false);
  readonly required = model<boolean>(undefined);
  readonly backgroundUI = model<BackgroundUI>('light');
  readonly ariaAttributes = model<AriaAttributes>({});
  readonly input = viewChild<ElementRef>('input');

  readonly valueChange = output<RadioChange>();
  /** @internal */
  @Output() readonly enabled: EventEmitter<RadiobuttonComponent> = new EventEmitter<RadiobuttonComponent>();

  @HostBinding('attr.id') protected externalId = '';

  protected checked = false;
  private destroyed$ = new Subject();

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const configService = this.configService;
    this.required.set(configService.config.formsRequiredDefault);
    if (this.parent) {
      this.parent.valueAccessor.disabled.subscribe(disabled => {
        this.disabled.set(disabled);
        this.changeDetector.detectChanges();
      });

      this.parent.valueAccessor.value.subscribe(v => {
        this.checked = v.includes(this.value() as string | number);
        this.changeDetector.detectChanges();
      });
    }
  }

  @HostBinding('class') get containerCssClasses(): string {
    return [
      'drv-radio-button',
      `drv-radio-button--${this.backgroundUI()}`,
      this.ariaAttributes().invalid ? 'drv-radio-button--error' : '',
    ]
      .filter(Boolean)
      .join(' ');
  }

  /** @internal */
  ngOnInit(): void {
    this.inputGroupService.registerChild({
      id: this.parent.id(),
      child: this,
      disabled: this.disabled(),
      checked: this.checked,
      value: this.value() as string | number,
    });

    this.parent.valueAccessor.value.subscribe(v => {
      this.checked = v.includes(this.value() as string | number);
      this.input().nativeElement.checked = this.checked;
      this.changeDetector.detectChanges();
    });

    if (this.parent) {
      this.name.set(this.parent.name());
      this.required.set(this.parent.required() as boolean);
      this.backgroundUI.set(this.parent.backgroundUI());

      this.setAriaAttributes({
        id: this.parent.id(),
        hasError: this.parent.hasError(),
        hasInfo: this.parent.hasInfo(),
        hasWarning: this.parent.hasWarning(),
      });

      this.inputGroupService.errors$.pipe(takeUntil(this.destroyed$)).subscribe(this.setAriaAttributes.bind(this));
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.disabled && this.parent) {
      this.inputGroupService.change({
        child: this,
        id: this.parent.id(),
        disabled: this.disabled(),
        checked: this.input().nativeElement.checked as boolean,
        value: this.value() as string | number,
      });
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    if (this.parent) {
      this.inputGroupService.unregisterChild({
        id: this.parent.id(),
        child: this,
      });
    }
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  protected onInputInteraction(event: Event): void {
    event.stopPropagation();

    this.valueChange.emit({
      source: this,
      value: this.value(),
    });

    this.checked = this.input().nativeElement.checked as boolean;

    this.inputGroupService.change({
      child: this,
      id: this.parent.id(),
      disabled: this.disabled(),
      checked: this.checked,
      value: this.value() as string | number,
    });
  }

  protected onInputClick(event: Event): void {
    event.stopPropagation();
  }

  private setAriaAttributes({ hasError, hasInfo, hasWarning, id: errorsId }): void {
    if (errorsId === this.parent.id()) {
      const ariaAttributes: AriaAttributes = {};

      if (hasError || hasInfo || hasWarning) {
        ariaAttributes.describedby = `${this.parent.id()}_errors`;
      }

      if (hasError) {
        ariaAttributes.invalid = true;
      }

      this.ariaAttributes.set(ariaAttributes);
      this.changeDetector.detectChanges();
    }
  }
}
