import {
  Component,
  ViewEncapsulation,
  OnInit,
  ElementRef,
  HostListener,
  ChangeDetectorRef,
  OnDestroy,
  booleanAttribute,
  input,
  inject,
  output,
  viewChild,
} from '@angular/core';
import { BreakpointObserver, BreakpointState } from '@angular/cdk/layout';
import { Observable, Subscription } from 'rxjs';
import type { Route } from '@angular/router';
import { NgClass } from '@angular/common';
import { getHotKeyHint } from 'keyux';

import { NavigationExtras, Router } from '@angular/router';
import { ConfigService } from '../../services/config.service';
import { getImageDimensions, trackByFn } from '../../utilities/utils';

import type { IconType } from '../../types/icons';

import { GridDirective } from '../grid/grid.directive';
import { RowDirective } from '../grid/row.directive';
import { ColDirective } from '../grid/col.directive';
import { TooltipComponent } from '../tooltip/tooltip.component';
import { ShortcutDirective } from '../../directives/keyboardshortcut.directive';
import { ButtonComponent } from '../button/button.component';
import { ButtonbarComponent } from '../buttonbar/buttonbar.component';
import { IconComponent } from '../icon/icon.component';
import { StatusComponent } from '../status/status.component';

export interface MainNavLinkItem {
  text: string;
  href?: string;
  active?: boolean;
  // createUrlTree expects any[] https://angular.dev/api/router/Router#createUrlTree
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  route?: any[];
  routeExtras?: NavigationExtras;
  callbackFn?: () => void;
  htmltarget?: string;
  htmltitle?: string;
  keyboardShortcut?: string;
}

export interface MetanavLinkItem extends MainNavLinkItem {
  icon?: IconType;
  iconOnly?: boolean;
}

export interface LangLinkItem extends MainNavLinkItem {
  languageCode: string;
}

@Component({
  selector: 'drv-header',
  templateUrl: './header.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./header.scss'],
  imports: [
    GridDirective,
    RowDirective,
    ColDirective,
    ButtonComponent,
    ButtonbarComponent,
    IconComponent,
    StatusComponent,
    TooltipComponent,
    ShortcutDirective,
    NgClass,
  ],
  host: {
    role: 'banner',
    class: 'drv-header',
    '[class.drv-header--management-mode]': 'this.isManagementMode()',
    '[attr.data-label]': 'this.isManagementMode() ? this.managementModeText() : null',
  },
})
export class HeaderComponent implements OnInit, OnDestroy {
  private router = inject(Router, { optional: true })!;
  private configService = inject(ConfigService);
  private breakpointObserver = inject(BreakpointObserver);
  private cdRef = inject(ChangeDetectorRef);

  readonly isLoggedIn = input(false, { transform: booleanAttribute });
  readonly loginPrefix = input('Angemeldet als ');
  readonly infotextSuffix = input('');
  readonly loginName = input('');
  readonly menuButtonLabel = input('');
  readonly logoutButtonLabel = input('');
  readonly loginButtonLabel = input('');
  readonly metaItems = input<MetanavLinkItem[]>([]);
  readonly mainItems = input<MainNavLinkItem[]>([]);
  readonly langItems = input<LangLinkItem[]>([]);
  readonly statusIcon = input<IconType>('clock');
  readonly statusText = input<string>(undefined);
  readonly ariaLabelNav = input<string>(undefined);
  readonly isManagementMode = input(false, { transform: booleanAttribute });
  readonly managementModeText = input('');
  readonly logoLink = input<string>(undefined);
  readonly logoLinkRoute = input<Route[] | string[]>(undefined);
  readonly logoLinkRouteExtras = input<NavigationExtras>(undefined);
  readonly logoAltText = input('Logo der Rentenversicherung ‚Äì Zur Startseite');
  readonly headerLogoPath = input.required<string>();
  readonly headerLogoSmallPath = input.required<string>();
  readonly headerLogoPathDark = input<string>(undefined);
  readonly headerLogoSmallPathDark = input<string>(undefined);
  readonly headerLogoTitle = input('Zur√ºck zur Startseite');
  readonly fullWidth = input(false, { transform: booleanAttribute });
  readonly withThemeToggle = input(false, { transform: booleanAttribute });
  readonly languageText = input('Language');
  readonly languageCloseBtnText = input('schlie√üen');
  readonly darkModeText = input('Dark Mode');
  readonly lightModeText = input('Light Mode');
  readonly darkThemeCSSClass = input('drv-theme--dark');
  readonly buttonClick = output<Event>();
  readonly itemClick = output<MetanavLinkItem | MainNavLinkItem | LangLinkItem>();
  readonly languageMenu = viewChild<ElementRef<HTMLElement>>('languageMenu');

  protected readonly breakpoint$: Observable<BreakpointState>;
  protected trackByFn = trackByFn;

  protected logoWidth: number;
  protected logoHeight: number;
  protected isExpanded = false;
  protected darkMode = false;
  protected iconSpritePath: string;
  protected largeScreenBreakpoint = false;
  protected langMenuOpen = false;
  // 'lib/styles/breakpoints/_breakpoints.scss' => $bp-lg: 1025px
  private breakpoint = '(min-width: 1025px)';
  private subscription: Subscription;
  private getImageDimensions = getImageDimensions;
  private showKeyboardshortcutTooltip: boolean;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const router = this.router;
    const configService = this.configService;

    router.events.subscribe(() => {
      this.isExpanded = false;
      this.langMenuOpen = false;
      document.documentElement.classList.remove('drv-prevent-scroll');
    });
    this.iconSpritePath = configService.config.spritePath;
    this.breakpoint$ = this.breakpointObserver.observe(this.breakpoint);
    this.showKeyboardshortcutTooltip = this.configService.config.showKeyboardshortCutAsTooltip;
  }

  protected get logoHref(): string {
    const logoLink = this.logoLink();
    if (logoLink) {
      return logoLink;
    }

    const logoLinkRoute = this.logoLinkRoute();
    return logoLinkRoute && this.router
      ? '.' + this.router.createUrlTree(logoLinkRoute, this.logoLinkRouteExtras() || {}).toString()
      : '/#';
  }

  protected get themeToggleClass(): string {
    return `drv-nav-link drv-dark-mode-toggle ${this.darkMode ? ' drv-dark-mode-toggle--dark' : ''}`;
  }

  @HostListener('window:click', ['$event.target']) protected handleWindowClick(target: HTMLElement): void {
    const languageMenu = this.languageMenu();
    if (!languageMenu?.nativeElement?.contains(target) && target !== languageMenu?.nativeElement) {
      this.langMenuOpen = false;
    }
  }

  /** @internal */
  ngOnInit(): void {
    const headerLogoPath = this.headerLogoPath();
    if (headerLogoPath) {
      void this.getImageDimensions(headerLogoPath).then(({ height, width }) => {
        this.logoWidth = width;
        this.logoHeight = height;
      });
    }

    window.setTimeout(() => {
      this.darkMode = document.body.classList.contains(this.darkThemeCSSClass());
    }, 10);

    this.subscription = this.breakpoint$.subscribe(() => {
      this.largeScreenBreakpoint = this.breakpointObserver.isMatched(this.breakpoint);
      this.cdRef.markForCheck();
    });
  }

  /** @internal */
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    document.documentElement.classList.remove('drv-prevent-scroll');
  }

  protected keyboardShortcutTooltip(item: MainNavLinkItem): string {
    if (item.keyboardShortcut && this.showKeyboardshortcutTooltip) {
      return `${getHotKeyHint(window, item.keyboardShortcut)}`;
    }
  }

  protected getHref(item: MetanavLinkItem | MainNavLinkItem): string {
    if (item.href) {
      return item.href;
    }
    return item.route && this.router
      ? '.' + this.router.createUrlTree(item.route, item.routeExtras || {}).toString()
      : '/#';
  }

  protected toggle(): void {
    this.isExpanded = !this.isExpanded;
    document.documentElement.classList.toggle('drv-prevent-scroll');
  }

  protected toggleLangMenu(): void {
    this.langMenuOpen = this.largeScreenBreakpoint ? !this.langMenuOpen : false;
  }

  protected onLogoClick(e: MouseEvent): void {
    const logoLinkRoute = this.logoLinkRoute();
    if (logoLinkRoute) {
      e.preventDefault();
      void this.router.navigate(logoLinkRoute, this.logoLinkRouteExtras() || {});
    }
  }

  protected toggleDarkMode(): void {
    this.darkMode = !this.darkMode;
    document.body.classList.toggle(this.darkThemeCSSClass());
  }

  protected itemClickHandler(e: MouseEvent, item: MetanavLinkItem | MainNavLinkItem | LangLinkItem): void {
    if (item.route) {
      e.preventDefault();
      void this.router.navigate(item.route, item.routeExtras || {});
    }

    this.itemClick.emit(item);

    if (item.callbackFn) {
      item.callbackFn();
    }
  }
}
