export type ThrottledFunction<T extends unknown[]> = (...args: T) => void;

const normalizeEventKey = (event: KeyboardEvent): string =>
  event.key === ' ' ? 'space' : event.key.replace(/(Arrow|ape)/, '').toLowerCase();

const isPrintable = (str: string): boolean => str.length === 1 && Boolean(str.match(/\S/));

const isEqual = (a: object, b: object): boolean => Object.keys(a).every(key => a[key] === b[key]);

const getFocusableElements = (parent: HTMLElement): HTMLElement[] => {
  const sel = 'button, [href]:not(use), input, select, textarea, [tabindex]:not([tabindex="-1"])';
  const elements: HTMLElement[] = Array.from(parent.querySelectorAll(sel));

  return elements.filter((el: HTMLElement) => {
    // Is descendant of inert element
    if (el.matches('[inert] *')) {
      return false;
    }
    // Is disabled or hidden
    if (['inert', 'hidden', 'disabled'].some(attr => el.getAttribute(attr) !== null)) {
      return false;
    }
    // Is not visible
    if (el.offsetParent === null) {
      return false;
    }

    return true;
  });
};

const getNextFocusableElement = (parent: HTMLElement, currentElement: HTMLElement): HTMLElement => {
  const focusableElements = getFocusableElements(parent);
  const idx = focusableElements.indexOf(currentElement);
  return focusableElements[idx + 1];
};

const getImageDimensions = (src: string): Promise<{ height: number; width: number }> => {
  if (!src) {
    return Promise.resolve({ height: 0, width: 0 });
  }
  return new Promise(resolve => {
    const img = new Image();
    img.onload = (): void => {
      resolve({
        height: img.height,
        width: img.width,
      });
    };

    img.src = src;
  });
};

/**
 * @param {string | boolean | null | undefined} value value to transform
 * @returns {boolean | undefined} converted value
 * @internal
 */
export function toBoolean(value: string | boolean | null | undefined): boolean | undefined {
  if (value === undefined || value === null) {
    return undefined;
  }
  if (typeof value === 'boolean') {
    return value;
  }
  if (typeof value === 'string') {
    return `${value}` !== 'false';
  }
}

const throttle = (mainFunction: ThrottledFunction<unknown[]>, delay: number): ThrottledFunction<unknown[]> => {
  let timerFlag = null;

  return (...args) => {
    if (timerFlag === null) {
      mainFunction(...args);
      timerFlag = setTimeout(() => {
        timerFlag = null;
      }, delay);
    }
  };
};

const trackByFn = (index: number): number => index;

const normalizeShortCut = (shortcutValue: string): string | undefined => {
  const keyOrder = ['meta', 'ctrl', 'alt', 'shift'];
  const shortcut = shortcutValue.replace('strg', 'ctrl').replace(/\s/g, '').toLowerCase();
  const keys = shortcut.split('+');

  let keysOrdered: string;

  if (keys.length > 1) {
    const modifier = keys.pop();

    if (keyOrder.includes(modifier)) {
      console.warn(`${shortcutValue} - shortcut is malformed.`);
      return;
    }

    if (keys.some(key => !keyOrder.includes(key))) {
      console.warn(`${shortcutValue} - Invalid shortcut. Only meta, ctrl, alt and shift are allowed.`);
      return;
    }

    keysOrdered = [
      ...keyOrder.reduce((acc: string[], key: string) => {
        if (keys.includes(key)) {
          return [...acc, key];
        }
        return acc;
      }, []),
      modifier,
    ].join('+');
  } else {
    keysOrdered = shortcut;

    if (keyOrder.includes(keysOrdered)) {
      console.warn(`${shortcutValue} - shortcut is malformed.`);
      return;
    }
  }

  return keysOrdered;
};

const getHotKeyFromtEvent = (event: KeyboardEvent): string => {
  const KEY_REPLACERS = {
    ' ': 'space',
    '+': 'plus',
  };

  let prefix = '';

  if (event.metaKey) prefix += 'meta+';
  if (event.ctrlKey) prefix += 'ctrl+';
  if (event.altKey) prefix += 'alt+';
  if (event.shiftKey) prefix += 'shift+';

  let code = prefix;
  code += KEY_REPLACERS[event.key] ?? event.key.toLowerCase();

  return code;
};

export {
  normalizeEventKey,
  isPrintable,
  isEqual,
  getFocusableElements,
  getNextFocusableElement,
  getImageDimensions,
  throttle,
  trackByFn,
  normalizeShortCut,
  getHotKeyFromtEvent,
};
