import {
  Component,
  Output,
  EventEmitter,
  ViewEncapsulation,
  OnChanges,
  SimpleChanges,
  model,
  input,
  inject,
  output,
} from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { Router, type NavigationExtras } from '@angular/router';
import { nanoid } from 'nanoid';

import { IconComponent } from '../icon/icon.component';
import { trackByFn } from '../../utilities/utils';
import { ButtonComponent } from '../button/button.component';
import { OverflowmenuComponent } from '../overflowmenu/overflowmenu.component';
import { TooltipComponent } from '../tooltip/tooltip.component';
import type { IconType } from '../../types/icons';

export interface NavigationItemAction {
  label: string;
  icon?: IconType;
  callbackFn?: () => void;
  // createUrlTree expects any[] https://angular.dev/api/router/Router#createUrlTree
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  route?: any[];
  routeExtras?: NavigationExtras;
}
export interface NavigationItem {
  text: string;
  icon?: IconType;
  href?: string;
  target?: '_self' | '_blank' | '_parent' | '_top';
  // createUrlTree expects any[] https://angular.dev/api/router/Router#createUrlTree
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  route?: any[];
  routeExtras?: NavigationExtras;
  callbackFn?: () => void;
  badgeNumber?: number;
  active?: boolean;
  actions?: NavigationItemAction[];
}

@Component({
  selector: 'drv-navigation-list',
  templateUrl: './navigation-list.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./navigation.scss'],
  imports: [NgClass, NgTemplateOutlet, IconComponent, ButtonComponent, OverflowmenuComponent, TooltipComponent],
  host: {
    class: 'drv-navigation-list-host',
  },
})
export class NavigationListComponent implements OnChanges {
  private router = inject(Router, { optional: true })!;

  readonly items = input<NavigationItem[]>([]);
  readonly actionsTriggerButtonLabel = input<string>(undefined);
  readonly showTooltips = model(false);

  readonly overFlowMenuClick = output<[number, number]>();
  @Output() itemClick = new EventEmitter<NavigationItem | NavigationItemAction>();
  @Output() itemChange = new EventEmitter();

  protected trackByFn = trackByFn;
  protected nanoid = nanoid();

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.items) {
      this.itemChange.emit();
    }
  }

  protected getHref(item: NavigationItem): string {
    if (item.href) {
      return item.href;
    }
    return item.route && this.router
      ? '.' + this.router.createUrlTree(item.route, item.routeExtras || {}).toString()
      : '/#';
  }

  protected overflowMenuClick(itemsIndex: number, actionIndex: number, btn: NavigationItemAction): void {
    this.overFlowMenuClick.emit([itemsIndex, actionIndex]);

    if (btn.route) {
      void this.router.navigate(btn.route, btn.routeExtras || {});
    }

    if (btn.callbackFn) {
      btn.callbackFn();
    }
  }

  protected itemClickHandler(e: MouseEvent, item: NavigationItem): void {
    if (item.route) {
      e.preventDefault();
      void this.router.navigate(item.route, item.routeExtras || {});
    }

    if (item.callbackFn) {
      if ((item.href && item.href === '#') || (!item.route && !item.href)) {
        e.preventDefault();
      }
      item.callbackFn();
    }

    this.itemClick.emit(item);
  }
}
