import { TableHeaderItem } from './thead.component';

export const getCellClass = (item: TableHeaderItem): string | null => {
  const { collapses, mainInfo, sticky, alignment } = item;
  const classes = [];

  if (collapses) {
    classes.push(`drv-table__cell--collapsible-${collapses}`);
  }

  if (mainInfo) {
    classes.push('drv-table__cell--main-info');
  }

  if (sticky) {
    classes.push('drv-table__cell--sticky');
  }

  if (alignment) {
    classes.push(`drv-table__cell--text-align-${alignment}`);
  }

  return classes.join(' ');
};
