import {
  Component,
  ViewEncapsulation,
  TemplateRef,
  ChangeDetectorRef,
  AfterContentInit,
  numberAttribute,
  input,
  inject,
  viewChild,
} from '@angular/core';

@Component({
  selector: 'drv-table-expandedrow',
  template: `
    <ng-template #templateref>
      <ng-content />
    </ng-template>
  `,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./table.scss'],
})
export class TableExpandedRowComponent implements AfterContentInit {
  private cdRef = inject(ChangeDetectorRef);

  readonly colspan = input.required<number, unknown>({ transform: numberAttribute });
  /** @internal */
  readonly rowTemplate = viewChild<TemplateRef<unknown>>('templateref');

  /** @internal */
  ngAfterContentInit(): void {
    this.cdRef.detectChanges();
  }
}
