import { Middleware } from '@floating-ui/core';
import { computePosition, autoPlacement, AutoPlacementOptions, Placement } from '@floating-ui/dom';

export const calculatePosition = (referenceElement: Element, floatingElement: HTMLElement): void => {
  const autoPlacementOptions: AutoPlacementOptions = {
    allowedPlacements: ['top-start', 'bottom-end'] as Placement[],
  };

  void computePosition(referenceElement, floatingElement, {
    placement: 'bottom-end' as Placement,
    middleware: [autoPlacement(autoPlacementOptions)] as Middleware[],
  }).then(({ x, y }) => {
    Object.assign(floatingElement.style, {
      left: `${x}px`,
      top: `${y}px`,
    });
  });
};
