import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

import { RadiobuttonComponent } from '../radiobutton/radiobutton.component';
import { CheckboxComponent } from '../checkbox/checkbox.component';

interface StatesSubject {
  id: string;
  disabled?: boolean;
  checked?: boolean;
  value?: string | number;
  child?: RadiobuttonComponent | CheckboxComponent;
}

interface ErrorSubject {
  id: string;
  hasError: boolean;
  hasInfo: boolean;
  hasWarning: boolean;
}

@Injectable({
  providedIn: 'root',
})
export class InputGroupService {
  private register = new Subject<StatesSubject>();
  private unregister = new Subject<StatesSubject>();
  private changes = new Subject<StatesSubject>();
  private errors = new Subject<ErrorSubject>();

  readonly register$ = this.register.asObservable();
  readonly unregister$ = this.unregister.asObservable();
  readonly changes$ = this.changes.asObservable();
  readonly errors$ = this.errors.asObservable();

  registerChild(obj: StatesSubject): void {
    this.register.next(obj);
  }

  unregisterChild(obj: StatesSubject): void {
    this.unregister.next(obj);
  }

  change(obj: StatesSubject): void {
    this.changes.next(obj);
  }

  setErrors(obj: ErrorSubject): void {
    this.errors.next(obj);
  }
}
