import {
  Component,
  AfterViewInit,
  HostBinding,
  HostListener,
  ViewEncapsulation,
  OnChanges,
  SimpleChanges,
  ElementRef,
  TemplateRef,
  OnDestroy,
  booleanAttribute,
  input,
  model,
  inject,
  viewChild,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import flatpickr from 'flatpickr';
import type { BaseOptions } from 'flatpickr/dist/types/options';
import { German } from 'flatpickr/dist/esm/l10n/de.js';

import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { FormitemComponent } from '../formitem/formitem.component';
import { ConfigService } from '../../services/config.service';
import { IconComponent } from '../icon/icon.component';
import { LabelComponent } from '../label/label.component';

export type MonthSelectorType = 'static' | 'dropdown';

export interface DateRangeLimit<D = DateOption> {
  from: D;
  to: D;
}
export type DateOption = Date | string | number;
export type DateLimit<D = DateOption> = D | DateRangeLimit<D> | ((date: Date) => boolean);

@Component({
  selector: 'drv-datepicker',
  templateUrl: './datepicker.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./datepicker.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [NgTemplateOutlet, IconComponent, LabelComponent, FormitemComponent],
  host: {
    class: 'drv-datepicker',
  },
})
export class DatepickerComponent extends FormitemComponent implements AfterViewInit, OnChanges, OnDestroy {
  private configService = inject(ConfigService);
  readonly valueAccessor = inject<ValueAccessorDirective<string>>(ValueAccessorDirective);
  private elementRef = inject(ElementRef);

  readonly disabled = model<boolean>(false);
  readonly required = model<boolean | unknown>(undefined);
  readonly native = input(false, { transform: booleanAttribute });
  readonly inline = input(false, { transform: booleanAttribute });
  readonly appointments = input(false, { transform: booleanAttribute });
  readonly allowInput = input(false, { transform: booleanAttribute });
  readonly rangeMode = input(false, { transform: booleanAttribute });
  readonly pickrLocale = input(German);
  readonly label = input<string>(undefined);
  readonly minDate = input<string | Date>(null);
  readonly maxDate = input<string | Date>(null);
  readonly dateFormat = input('d.m.Y');
  readonly altFormat = input('d.m.Y'); // Exactly the same as date format, but for the altInput field
  // Show the user a readable date (as per altFormat), but return something totally different to the server.
  readonly altInput = input(false, { transform: booleanAttribute });
  readonly disabledDates = input<DateLimit[]>([]);
  readonly nextMonthButtonLabel = input('N√§chster Monat');
  readonly prevMonthButtonLabel = input('Vorheriger Monat');
  readonly collapseMessages = model(false);
  readonly placeholder = input('TT.MM.JJJJ');
  readonly monthSelectorType = input<MonthSelectorType>('static');

  /** content of the toggletip, when to show */
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });

  readonly input = viewChild<ElementRef>('input');
  readonly inlineHolder = viewChild<ElementRef>('inlineHolder');

  @HostBinding('attr.id') protected externalId = '';

  @HostBinding('class') protected get containerCssClasses(): string {
    return [...this.cssClass().split(' ')].filter(Boolean).join(' ');
  }

  protected iconSpritePath: string;

  private instance: flatpickr.Instance = null;
  private changeableOptions: string[] = [
    'minDate',
    'maxDate',
    'dateFormat',
    'disabledDates', // 'disable'
    'allowInput',
  ];

  private innerValue?: string;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    super();
    const configService = this.configService;
    const valueAccessor = this.valueAccessor;

    this.iconSpritePath = configService.config.spritePath;
    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);
    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.innerValue = v;
      this.instance?.setDate(v, false, this.dateFormat());
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  protected get cssInputClass(): string[] {
    const defaultClasses = ['drv-text-input', 'drv-datepicker__input'];

    return [
      [...defaultClasses, this.inline() ? 'drv-datepicker__input--calendar' : ''].join(' '),
      [...defaultClasses, `drv-text-input--${this.backgroundUI()}`].join(' '),
    ];
  }

  protected get describedBy(): string | null {
    if (this.hasError() || this.hasInfo() || this.hasWarning()) {
      return `${this.id()}_error`;
    }

    return null;
  }

  @HostListener('focusout', ['$event']) protected onFocusout(event: FocusEvent): void {
    if (event.target === this.input().nativeElement) {
      this.valueAccessor.onBlur(event);
    }
  }

  /** @internal */
  ngAfterViewInit(): void {
    if (this.native()) {
      return;
    }

    const pickrOptions = {
      prevArrow: `
      <button type="button" class="drv-btn drv-btn-tertiary--light drv-btn--icon-left drv-btn--icon-only">
        <svg
          focusable="false"
          role="img"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          aria-label="${this.prevMonthButtonLabel()}"
        ><use xlink:href="${this.iconSpritePath}#arrow-left"></use></svg>
      </button>
      `,
      nextArrow: `
      <button type="button" class="drv-btn drv-btn-tertiary--light drv-btn--icon-left drv-btn--icon-only">
        <svg
          focusable="false"
          role="img"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          aria-label="${this.nextMonthButtonLabel()}"
        ><use xlink:href="${this.iconSpritePath}#arrow-right"></use></svg>
      </button>
      `,
      monthSelectorType: this.monthSelectorType(),
      shorthandCurrentMonth: false,
      ariaDateFormat: 'l, d. F Y',
      allowInput: this.appointments() ? false : this.allowInput(),
      minDate: this.minDate(),
      maxDate: this.maxDate(),
      dateFormat: this.dateFormat(),
      altFormat: this.altFormat(),
      altInput: this.altInput(),
      disable: this.disabledDates(),
      disableMobile: false,
      allowInvalidPreload: true,
    };

    const pickrLocale = this.pickrLocale();
    const rangeMode = this.rangeMode();
    if (pickrLocale) {
      Object.assign(pickrOptions, {
        locale: pickrLocale,
      });

      if (rangeMode) {
        pickrLocale.rangeSeparator = '‚ÄÉ‚Äî‚ÄÉ';
      }
    }

    const inline = this.inline();
    if (inline) {
      Object.assign(pickrOptions, {
        inline: true,
        wrap: false,
        allowInvalidPreload: false,
        appendTo: this.inlineHolder().nativeElement,
      });
    }

    if (rangeMode) {
      Object.assign(pickrOptions, {
        allowInput: false,
        mode: 'range',
      });
    }

    if (window?.matchMedia?.('(max-width: 600px)').matches) {
      Object.assign(pickrOptions, {
        shorthandCurrentMonth: true,
      });
    }

    if (this.innerValue) {
      Object.assign(pickrOptions, {
        defaultDate: this.innerValue,
      });
    }

    this.instance = flatpickr(
      inline ? this.input().nativeElement : this.elementRef.nativeElement.querySelector('.drv-datepicker__input'),
      pickrOptions
    );

    if (this.appointments()) {
      this.instance.calendarContainer.classList.add('drv-datepicker--appointments');
    }
    if (this.disabled() && inline) {
      this.instance.calendarContainer.inert = true;
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (this.instance) {
      Object.keys(changes)
        .filter(changeKey => this.changeableOptions.includes(changeKey))
        .forEach(inputKey => {
          if (inputKey === 'disabledDates') {
            this.instance.set('disable', this[inputKey]());
          } else {
            this.instance.set(inputKey as keyof BaseOptions, this[inputKey]());
          }
        });
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    if (this.instance) {
      this.instance?.destroy();
    }
  }

  protected setDisabledState(isDisabled: boolean): void {
    this.disabled.set(isDisabled);
    if (this.inline()) {
      if (this.instance?.calendarContainer) {
        this.instance.calendarContainer.inert = this.disabled();
      }
    }
  }

  protected inputChange({ target }): void {
    this.innerValue = target.value;
    this.valueAccessor.valueChange(target.value);
  }
}
