import { BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';

export interface Loading {
  id: string;
  visible?: boolean;
  asProgress?: boolean;
  progress?: number;
}

@Injectable({
  providedIn: 'root',
})
export class LoadingService {
  private _knobs = new BehaviorSubject<Loading>(null);
  readonly knobs = this._knobs.asObservable();

  updateKnobs(data: Loading): void {
    this._knobs.next(data);
  }
}
