import { Component, ViewEncapsulation, contentChildren, input } from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import { ItemlistItemComponent } from './itemlistitem.component';

@Component({
  selector: 'drv-itemlist',
  template: `
    @if (items().length > 0) {
      @for (item of items(); track item; let i = $index) {
        <div role="listitem" [class]="['drv-itemlist__item', item.hostClass() || '']">
          <ng-container [ngTemplateOutlet]="item.itemTemplate()"></ng-container>
        </div>
      }
    } @else {
      <ng-content />
    }
  `,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./itemlist.scss'],
  imports: [NgTemplateOutlet],
  host: {
    class: 'drv-itemlist',
    '[class]': 'this.hostClass() || null',
    '[class.drv-itemlist--scrollable]': 'this.isScrollable()',
    '[attr.role]': 'this.items().length > 0 ? "list" : null',
  },
})
export class ItemlistComponent {
  readonly isScrollable = input<boolean>(false);
  readonly hostClass = input<string>(undefined);

  readonly items = contentChildren(ItemlistItemComponent);
}
