import { Directive, ElementRef, Renderer2, OnInit, input, inject } from '@angular/core';
import { hotkeyKeyUX, startKeyUX, hotkeyMacCompat } from 'keyux';

import { normalizeShortCut } from '../utilities/utils';

const mac = hotkeyMacCompat();

startKeyUX(window, [hotkeyKeyUX([mac])]);

@Directive({
  selector: '[drv-keyshortcuts]',
})
export class ShortcutDirective implements OnInit {
  private elRef = inject(ElementRef);
  private renderer = inject(Renderer2);

  readonly shortcut = input<string>(undefined, { alias: 'drv-keyshortcuts' });

  ngOnInit(): void {
    const shortcutValue = this.shortcut();

    if (!shortcutValue) {
      return;
    }

    const keysOrdered: string = normalizeShortCut(shortcutValue);

    if (keysOrdered) {
      this.renderer.setAttribute(this.elRef.nativeElement, 'aria-keyshortcuts', keysOrdered);
    }
  }
}
