import {
  Component,
  ViewEncapsulation,
  ElementRef,
  AfterViewInit,
  AfterContentInit,
  input,
  output,
  ContentChildren,
  QueryList,
  viewChildren,
  viewChild,
  ViewChildren,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';

import { nanoid } from 'nanoid';

import { IconComponent } from '../icon/icon.component';
import { ButtonComponent } from '../button/button.component';
import type { BackgroundUI } from '../../types';
import { TabpanelComponent } from './tabpanel.component';
import { normalizeEventKey, trackByFn } from '../../utilities/utils';

export interface TabChange {
  to: number;
  from: number;
}

@Component({
  selector: 'drv-tablist',
  templateUrl: './tablist.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./tabs.scss'],
  imports: [NgTemplateOutlet, IconComponent, ButtonComponent],
  host: {
    class: 'drv-tabs',
    '[class.drv-tabs--color]': 'this.backgroundUI() === "color"',
    '[class.drv-tabs--light]': 'this.backgroundUI() === "light"',
    '[class.drv-tabs--no-padded-content]': '!this.paddedContent()',
  },
})
export class TablistComponent implements AfterViewInit, AfterContentInit {
  readonly backgroundUI = input<BackgroundUI>('light');
  readonly paddedContent = input(true);
  readonly tabChange = output<TabChange>();

  @ContentChildren(TabpanelComponent) protected panels: QueryList<TabpanelComponent>;
  @ViewChildren('section') protected sections: QueryList<ElementRef>;
  /** @internal */
  readonly tabs = viewChildren<ElementRef>('tab');
  /** @internal */
  readonly items = viewChildren<ElementRef>('item');
  /** @internal */
  readonly list = viewChild<ElementRef>('list');

  protected id: string;
  protected trackByFn = trackByFn;

  private scrollLeft: number;
  private maxScrollLeft: number;
  private isOverflowing = false;
  private intersections: number[] = [];

  constructor() {
    this.id = nanoid(10);
  }

  protected get prevButtonClass(): string {
    if (!this.isOverflowing) {
      return 'drv-tabs__btn--prev drv-tabs__btn--hidden';
    }

    return `drv-tabs__btn--prev ${this.scrollLeft === 0 ? 'drv-tabs__btn--hidden' : ''}`;
  }

  protected get nextButtonClass(): string {
    if (!this.list() || !this.isOverflowing) {
      return 'drv-tabs__btn--next drv-tabs__btn--hidden';
    }

    return `drv-tabs__btn--next ${this.scrollLeft < this.maxScrollLeft ? '' : 'drv-tabs__btn--hidden'}`;
  }

  protected get maskOpacity(): string {
    return [
      `--mask-opacity--left: ${this.scrollLeft === 0 ? '1' : '0'}`,
      `--mask-opacity--right: ${this.scrollLeft < this.maxScrollLeft ? '0' : '1'}`,
    ].join(';');
  }

  /** @internal */
  ngAfterContentInit(): void {
    this.setFirstPanelActive();

    this.panels.changes.subscribe(() => {
      this.setFirstPanelActive();
    });
  }

  /** @internal */
  ngAfterViewInit(): void {
    const panelsArray = this.panels;

    if (panelsArray.length) {
      window.setTimeout(() => {
        this.scrollLeft = this.list().nativeElement.scrollLeft;
        this.maxScrollLeft = this.list().nativeElement.scrollWidth - this.list().nativeElement.clientWidth;
      });

      if (window && 'IntersectionObserver' in window) {
        const options = {
          root: this.list().nativeElement,
          rootMargin: '0px',
          threshold: Array.from({ length: 21 }).map(
            (_, index) => Math.round((0.05 * index + Number.EPSILON) * 100) / 100
          ),
        };

        const observer = new IntersectionObserver(entries => {
          entries.forEach(({ target, intersectionRatio }) => {
            const idx = this.items().findIndex(item => item.nativeElement === target);
            this.intersections[idx] = intersectionRatio;
          });
        }, options);

        this.items().forEach(item => {
          observer.observe(item.nativeElement);
        });
      }

      if (window && 'ResizeObserver' in window) {
        const observer = new ResizeObserver(() => {
          this.isOverflowing = this.list().nativeElement.scrollWidth > this.list().nativeElement.clientWidth;
          this.maxScrollLeft = this.list().nativeElement.scrollWidth - this.list().nativeElement.clientWidth;
        });

        observer.observe(this.list().nativeElement);
      }
    }
  }

  protected setActive(index: number, focus = false): void {
    const from = this.panels.toArray().findIndex(p => p.active());

    this.panels.forEach(p => {
      p.active.set(false);
    });

    this.panels.toArray().at(index).active.set(true);

    if (focus) {
      this.tabs().at(index).nativeElement.focus();
    }

    this.tabChange.emit({
      to: index,
      from,
    } as TabChange);
  }

  protected handleClick(e: Event, panel: TabpanelComponent, index: number): void {
    e.preventDefault();

    if (panel.active()) {
      return;
    }

    this.setActive(index);
  }

  protected handleNavButtonClick(direction: 'left' | 'right'): void {
    const finder = (i: number): boolean => i < 1 && i !== 0;
    const nextVisible =
      direction === 'left'
        ? this.intersections.findIndex(finder)
        : this.intersections.length - 1 - [...this.intersections].reverse().findIndex(finder);

    window.requestAnimationFrame(() => {
      this.list().nativeElement.scrollLeft = Math.min(
        this.maxScrollLeft,
        this.items().at(nextVisible)?.nativeElement.offsetLeft
      );
    });
  }

  protected handleKeyDown(event: KeyboardEvent, index: number): void {
    const key = normalizeEventKey(event);

    const actions = {
      down(): void {
        this.sections.get(index).nativeElement.focus();
      },
      left(): void {
        event.preventDefault();
        this.setActive(Math.max(index - 1, 0), true);
      },
      right(): void {
        event.preventDefault();
        this.setActive(Math.min(index + 1, this.panels.length - 1), true);
      },
    };

    if (actions[key]) {
      actions[key].call(this);
    }
  }

  protected handleScroll(event: Event): void {
    this.scrollLeft = (event.target as HTMLElement).scrollLeft;
  }

  private setFirstPanelActive(): void {
    const panelsArray = this.panels.toArray();

    if (panelsArray.length && panelsArray.every(p => !p.active())) {
      this.panels.toArray().at(0)!.active.set(true);
    }
  }
}
