import { Directive, forwardRef, OnDestroy, Component } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subject } from 'rxjs';

type OnChangeFunction = (v: unknown) => void;
type OnTouchedFunction = (v: boolean | FocusEvent) => void;

@Directive({
  standalone: true,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ValueAccessorDirective),
      multi: true,
    },
  ],
})
export class ValueAccessorDirective<T> implements ControlValueAccessor, OnDestroy {
  private onChange: OnChangeFunction;
  private onTouched: OnTouchedFunction;
  private valueSubject = new Subject<T>();
  private disabledSubject = new Subject<boolean>();
  private host: Component;

  readonly value = this.valueSubject.asObservable();
  readonly disabled = this.disabledSubject.asObservable();

  onBlur(event: FocusEvent): void {
    this.onTouched?.(event);
  }

  /*
    By calling the complete method of the Subject class,
    we are telling the Subject to stop emitting values.
    This is important because if we don't do this, the Subject
    will keep emitting values even after the component is destroyed.
    This can lead to memory leaks and other issues.
  */
  ngOnDestroy(): void {
    this.valueSubject.complete();
    this.disabledSubject.complete();
  }

  valueChange(v: T): void {
    this.onChange?.(v);
  }

  touchedChange(v: boolean): void {
    this.onTouched?.(v);
  }

  writeValue(obj: T): void {
    this.valueSubject.next(obj);
  }

  registerOnChange(fn: OnChangeFunction): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: OnTouchedFunction): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabledSubject.next(isDisabled);
  }

  setHost(host: unknown): void {
    this.host = host;
  }
}
