import {
  Component,
  OnInit,
  ViewEncapsulation,
  HostListener,
  OnChanges,
  SimpleChanges,
  HostBinding,
  input,
  inject,
  output,
  viewChildren,
  model,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import { CdkPortalOutlet } from '@angular/cdk/portal';
import { nanoid } from 'nanoid';
import { Notification, NotificationService } from './notification.service';
import type { SymbolsType } from '../../types/symbols';
import type { NotificationVariant } from './notification.service';

import { ButtonComponent } from '../button/button.component';
import { normalizeEventKey, trackByFn } from '../../utilities/utils';
import { ConfigService } from '../../services/config.service';
import { SymbolsComponent } from '../symbols/symbols.component';

interface LocalNotification extends Notification {
  notificationId?: string;
}

export type NotificationType = 'toast' | 'inline' | 'banner';
export type NotificationToastPosition = 'bottom' | 'right';

@Component({
  selector: 'drv-notification',
  templateUrl: './notification.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./notification.scss'],
  imports: [NgTemplateOutlet, ButtonComponent, CdkPortalOutlet, SymbolsComponent],
})
export class NotificationComponent implements OnInit, OnChanges {
  private notificationService = inject(NotificationService);
  private configService = inject(ConfigService);

  readonly notificationId = input(`notification-${nanoid()}`);
  readonly type = model<NotificationType>('toast');
  readonly collapsible = input<boolean>(false);
  readonly inlineNotification = input<Notification>(undefined);
  readonly toastPosition = model<NotificationToastPosition>(undefined);
  readonly closeButtonLabel = input('Schlie√üen');
  readonly toggleButtonLabel = input('Hinweis ein-/ausblenden');
  readonly notificationClosed = output<string>();
  readonly buttons = viewChildren<ButtonComponent>('buttons');
  @HostBinding('attr.class') protected hostClass = 'drv-notification-host';

  protected notifications: LocalNotification[] = [];
  protected trackByFn = trackByFn;
  protected isOpen = true;
  private activeElement: HTMLElement | null = null;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const configService = this.configService;
    this.toastPosition.set(configService.config.notificationDefaultToastPosition);
  }

  protected get cssClass(): string {
    return [
      'drv-notification',
      this.type() === 'toast' ? `drv-notification--toast-${this.toastPosition()}` : null,
      `drv-notification--${this.type()}`,
    ]
      .filter(Boolean)
      .join(' ');
  }

  protected get ariaLive(): string {
    if (this.notifications.length && this.notifications.some(({ variant }) => variant === 'alert')) {
      return 'assertive';
    }

    return 'polite';
  }

  protected get isCollapsible(): boolean {
    return this.type() === 'toast' ? false : this.collapsible();
  }

  @HostListener('keydown', ['$event']) protected keydownHandler(e: KeyboardEvent): void {
    if (this.inlineNotification()) {
      return;
    }

    const key = normalizeEventKey(e);
    const { target, shiftKey } = e;

    if (key === 'tab') {
      if (this.activeElement && this.removableAlertCount() === 1) {
        e.preventDefault();
        this.activeElement.focus();
        return;
      }

      const buttons = this.buttons().filter((_, idx) => {
        const { variant, isRemoveAble = true } = this.notifications[idx];
        return variant === 'alert' && isRemoveAble;
      });

      if (!buttons.length) {
        return;
      }

      const [first] = buttons;
      // const [last] = buttons.reverse();

      const inputValue = first.input();
      if (target === inputValue.nativeElement && shiftKey) {
        e.preventDefault();
        inputValue.nativeElement.focus();
      }

      if (target === inputValue.nativeElement && !shiftKey) {
        e.preventDefault();
        inputValue.nativeElement.focus();
      }
    }

    if (key === 'enter') {
      window.setTimeout(this.focusFirstButton.bind(this), 25);
    }
  }

  /** @internal */
  ngOnInit(): void {
    if (this.type() === 'toast' && !this.inlineNotification()) {
      this.notificationService.notifications.subscribe(data => {
        this.notifications = data.filter(({ id }) => id === this.notificationId());
        this.addActiveElement();
      });
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    const type = this.type();
    if ((type === 'inline' || type === 'banner') && Object.keys(changes).includes('inlineNotification')) {
      this.notifications = [this.inlineNotification()];
    }
  }

  protected getIconType(variant: NotificationVariant): SymbolsType {
    return (
      {
        alert: 'status-error',
        warning: 'status-warning',
        success: 'status-success',
      }[variant] || 'status-info'
    );
  }

  protected isRemovable({ isRemoveAble = true }): boolean {
    return isRemoveAble;
  }

  protected getStyle({ needsFeedback = false, duration = 10, variant }): string | null {
    const type = this.type();
    if ((type === 'inline' || type === 'banner') && this.inlineNotification()) {
      return null;
    }

    return needsFeedback || variant === 'alert'
      ? '--drv-notification-ani-name: needsFeedback;'
      : `--drv-notification-duration: ${duration}s;`;
  }

  protected removeNotification(e: AnimationEvent | MouseEvent, id: string): void {
    const { type, animationName } = e as AnimationEvent;

    if (type === 'animationend' && animationName === 'slideUp') {
      return;
    }

    const typeValue = this.type();
    if ((typeValue === 'inline' || typeValue === 'banner') && this.inlineNotification()) {
      this.notifications = [];
      return;
    }

    this.notificationService.remove(id);
    this.notificationClosed.emit(id);
  }

  protected toggle(): void {
    this.isOpen = !this.isOpen;
  }

  private selector = (notification: Notification): boolean => {
    const { variant, isRemoveAble = true } = notification;
    return variant === 'alert' && isRemoveAble;
  };

  private hasRemovableAlert(): boolean {
    return this.notifications.some(this.selector);
  }

  private removableAlertCount(): number {
    return this.notifications.filter(this.selector).length;
  }

  private focusFirstButton(): void {
    const idx = this.notifications.findIndex(this.selector);

    if (idx !== -1) {
      const button = this.buttons().at(idx);
      button.input().nativeElement.focus();
    }
  }

  private addActiveElement(): void {
    const keyDownHandler = (e: KeyboardEvent): void => {
      const key = normalizeEventKey(e);

      if (key === 'tab' && this.removableAlertCount() > 0) {
        e.preventDefault();
        this.focusFirstButton();
      }
    };

    if (this.hasRemovableAlert() && !this.activeElement) {
      this.activeElement = document.activeElement as HTMLElement;
      this.activeElement.addEventListener('keydown', keyDownHandler);
      return;
    }

    if (this.activeElement && !this.hasRemovableAlert()) {
      this.activeElement.focus();
      this.activeElement.removeEventListener('keydown', keyDownHandler);
      this.activeElement = null;
      return;
    }
  }
}
