import { Component, ViewEncapsulation, AfterContentInit, input, inject, contentChildren } from '@angular/core';

import { NavigationExtras, Router } from '@angular/router';
import type { Route } from '@angular/router';

import { NavigationListComponent } from '../../components/navigation/navigation-list.component';
import { ButtonComponent } from '../../components/button/button.component';
import { NgTemplateOutlet } from '@angular/common';

@Component({
  selector: 'drv-application-navigation',
  templateUrl: './applicationnavigation.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./applicationnavigation.scss'],
  imports: [ButtonComponent, NgTemplateOutlet],
  host: {
    class: 'drv-application-navigation',
    '[class.drv-application-navigation--expanded]': 'this.isOpen',
    role: 'navigation',
  },
})
export class ApplicationNavigationComponent implements AfterContentInit {
  private router = inject(Router, { optional: true })!;

  readonly navs = contentChildren(NavigationListComponent);
  readonly navLabel = input.required<string>();
  readonly showLogo = input(false);
  readonly isExpandable = input(false);
  readonly expandButtonLabelOpen = input('Navigation √∂ffnen');
  readonly expandButtonLabelClose = input('Navigation schlie√üen');
  readonly logoLink = input<string>(undefined);
  readonly logoLinkRoute = input<Route[] | string[]>(undefined);
  readonly logoLinkRouteExtras = input<NavigationExtras>(undefined);
  readonly logoLinkTitle = input('Logo der Rentenversicherung ‚Äì Zur Startseite');
  readonly logoAltText = input('Logo der Rentenversicherung ‚Äì Zur Startseite');

  protected isOpen = false;

  get logoHref(): string {
    const logoLink = this.logoLink();
    if (logoLink) {
      return logoLink;
    }

    const logoLinkRoute = this.logoLinkRoute();
    return logoLinkRoute && this.router
      ? '.' + this.router.createUrlTree(logoLinkRoute, this.logoLinkRouteExtras() || {}).toString()
      : '/#';
  }

  ngAfterContentInit(): void {
    const navs = this.navs();
    if (navs.length) {
      navs.forEach(nav => nav.showTooltips.set(!this.isOpen));
    }
  }

  protected toggle(): void {
    this.isOpen = !this.isOpen;

    if (this.navs().length) {
      window.setTimeout(() => {
        this.navs().forEach(nav => nav.showTooltips.set(!this.isOpen));
      });
    }
  }

  protected onLogoClick(e: MouseEvent): void {
    const logoLinkRoute = this.logoLinkRoute();
    if (logoLinkRoute) {
      e.preventDefault();
      void this.router.navigate(logoLinkRoute, this.logoLinkRouteExtras() || {});
    }
  }
}
