import { Component, ViewEncapsulation, booleanAttribute, input, inject } from '@angular/core';
import type { Route } from '@angular/router';
import { NgClass } from '@angular/common';
import { PictogramsType } from '../../types/pictograms';

import { NavigationExtras, Router } from '@angular/router';
import { ConfigService } from '../../services/config.service';
import { trackByFn } from '../../utilities/utils';

import { IconlinkComponent, type IconLinkTarget } from '../iconlink/iconlink.component';
import { PictogramComponent } from '../pictogram/pictogram.component';

export type LinkTargetType = IconLinkTarget;

@Component({
  selector: 'drv-tile',
  templateUrl: './tile.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./tile.scss'],
  imports: [NgClass, IconlinkComponent, PictogramComponent],
  host: {
    class: 'drv-tile',
    '[attr.title]': 'null',
  },
})
export class TileComponent {
  private router = inject(Router, { optional: true })!;
  private configService = inject(ConfigService);

  readonly title = input.required<string>();
  readonly pictogramType = input.required<PictogramsType>();
  readonly items = input.required<string[]>();
  readonly href = input<string>(undefined);
  readonly route = input<Route[] | string[]>(undefined);
  readonly isExternal = input(false, { transform: booleanAttribute });
  readonly linkTarget = input<IconLinkTarget>('_self');
  readonly routeExtras = input<NavigationExtras>(undefined);
  readonly hideDescriptionTextOnSmall = input(false, { transform: booleanAttribute });

  protected iconSpritePath: string;
  protected trackByFn = trackByFn;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const configService = this.configService;

    this.iconSpritePath = configService.config.spritePath;
  }

  protected get getHref(): string {
    const href = this.href();
    if (href) {
      return href;
    }
    const route = this.route();
    return this.router && route ? '.' + this.router.createUrlTree(route, this.routeExtras() || {}).toString() : '/#';
  }

  protected onClickHandler(event: MouseEvent): void {
    const route = this.route();
    if (this.router && route) {
      event.preventDefault();
      void this.router.navigate(route, this.routeExtras());
    }
  }
}
