import {
  Component,
  Input,
  ChangeDetectorRef,
  HostBinding,
  HostListener,
  ViewEncapsulation,
  ElementRef,
  booleanAttribute,
  OnChanges,
  SimpleChanges,
  input,
  inject,
  output,
  viewChild,
  model,
  EventEmitter,
  Output,
  OnInit,
  OnDestroy,
} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { Subject, takeUntil } from 'rxjs';
import { nanoid } from 'nanoid';

import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';

import type { AriaAttributes, BackgroundUI } from '../../types';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';
import { InputGroupComponent } from '../inputgroup/inputgroup.component';
import { InputGroupService } from '../inputgroup/inputgroup.service';
export interface CheckboxChange {
  source: CheckboxComponent;
  value: string | number | unknown;
}

@Component({
  selector: 'drv-checkbox',
  templateUrl: './checkbox.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./checkbox.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [AriaAttributeDirective, FormsModule],
})
export class CheckboxComponent implements OnChanges, OnInit, OnDestroy {
  private parent = inject(InputGroupComponent, { optional: true, host: true, skipSelf: true })!;
  private inputGroupService = inject(InputGroupService);

  readonly changeDetector = inject(ChangeDetectorRef);
  readonly elementRef = inject(ElementRef);
  readonly valueAccessor = inject<ValueAccessorDirective<unknown>>(ValueAccessorDirective);

  readonly id = input<string>(`checkbox-${nanoid()}`);
  readonly name = model<string>(undefined);
  readonly disabled = model<boolean>(false);
  readonly required = model<boolean>(false);
  readonly value = input<string>(undefined);
  readonly labelHidden = input(false, { transform: booleanAttribute });
  readonly backgroundUI = model<BackgroundUI>('light');
  readonly ariaAttributes = model<AriaAttributes>({});
  readonly input = viewChild<ElementRef>('input');

  readonly checkboxChange = output<CheckboxChange>();
  /** @internal */
  readonly rendered = output<void>();
  /** @internal */
  @Output() readonly enabled: EventEmitter<CheckboxComponent> = new EventEmitter<CheckboxComponent>();
  /** @internal */
  @HostBinding('attr.id') externalId = '';

  private innerValue?: unknown;
  private labelClicked = false;
  private destroyed$ = new Subject();

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const valueAccessor = this.valueAccessor;

    valueAccessor.setHost(this);

    if (this.parent && this.parent instanceof InputGroupComponent) {
      this.parent.valueAccessor.disabled.subscribe(disabled => {
        this.disabled.set(disabled);
        this.changeDetector.detectChanges();
      });

      this.parent.valueAccessor.value.subscribe(v => {
        this.innerValue = Array.isArray(v) ? v.includes(this.value() as string | number) : null;
      });
    } else {
      valueAccessor.value.subscribe(v => {
        if (v !== this.innerValue) {
          this.innerValue = !!v;
        }
      });

      valueAccessor.disabled.subscribe(disabled => {
        this.disabled.set(disabled);
      });
    }
  }

  @HostBinding('attr.title') protected get title(): string {
    return this.labelHidden() ? this.elementRef.nativeElement.textContent : null;
  }

  @HostBinding('class') protected get containerCssClasses(): string {
    return [
      'drv-checkbox',
      `drv-checkbox--${this.backgroundUI()}`,
      this.ariaAttributes()?.invalid ? 'drv-checkbox--error' : '',
    ]
      .filter(Boolean)
      .join(' ');
  }

  get labelClasses(): string {
    return ['drv-checkbox__label', this.labelHidden() ? 'drv-checkbox__label--hidden' : null].filter(Boolean).join(' ');
  }

  @Input() set checked(value: boolean) {
    this.model = value;
  }

  get model(): unknown {
    return this.innerValue;
  }

  set model(value: unknown) {
    if (value !== this.innerValue) {
      this.innerValue = value;

      this.checkboxChange.emit({
        source: this,
        value,
      });

      if (this.parent && this.parent instanceof InputGroupComponent) {
        this.inputGroupService.change({
          child: this,
          id: this.parent.id(),
          disabled: this.disabled(),
          checked: this.innerValue as boolean,
          value: this.value() as string | number,
        });
      } else {
        this.valueAccessor.valueChange(value);
      }
    }
  }

  @HostListener('focusout', ['$event']) protected onFocusout(event: FocusEvent): void {
    const inputValue = this.input();
    if (event.target === inputValue.nativeElement) {
      inputValue.nativeElement.classList.remove('js-focus-visible');
      this.valueAccessor.onBlur(event);
    }
  }

  /** @internal */
  ngOnInit(): void {
    if (this.parent && this.parent instanceof InputGroupComponent) {
      this.backgroundUI.set(this.parent.backgroundUI());

      this.model = this.parent.innerValue().includes(this.value());

      this.inputGroupService.registerChild({
        id: this.parent.id(),
        child: this,
        disabled: this.disabled(),
        checked: this.model as boolean,
        value: this.value() as string | number,
      });

      this.setAriaAttributes({
        id: this.parent.id(),
        hasError: this.parent.hasError(),
        hasInfo: this.parent.hasInfo(),
        hasWarning: this.parent.hasWarning(),
      });

      this.inputGroupService.errors$.pipe(takeUntil(this.destroyed$)).subscribe(this.ariaAttributes.bind(this));
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.disabled && this.parent) {
      this.inputGroupService.change({
        child: this,
        id: this.parent.id(),
        disabled: this.disabled(),
        checked: this.innerValue as boolean,
        value: this.value() as string | number,
      });
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    if (this.parent) {
      this.inputGroupService.unregisterChild({
        id: this.parent.id(),
        child: this,
      });
    }
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  protected focusHandler({ target }): void {
    if (this.labelClicked) {
      return;
    }

    if (!target.matches(':focus-visible')) {
      target.classList.add('js-focus-visible');
    }
    this.labelClicked = true;
  }

  protected labelClick(): void {
    this.labelClicked = true;
  }

  private setAriaAttributes({ hasError, hasInfo, hasWarning, id: errorsId }): void {
    if (errorsId === this.parent.id()) {
      const ariaAttributes: AriaAttributes = {};

      if (hasError || hasInfo || hasWarning) {
        ariaAttributes.describedby = `${this.parent.id()}_errors`;
      }

      if (hasError) {
        ariaAttributes.invalid = true;
      }

      this.ariaAttributes.set(ariaAttributes);
      this.changeDetector.detectChanges();
    }
  }
}
