import { Component, ViewEncapsulation, input, inject, OnInit, Renderer2 } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { ConfigService } from '../../services/config.service';

import { FieldsetComponent } from '../fieldset/fieldset.component';
import { FieldsetService } from '../fieldset/fieldset.service';

import { normalizeShortCut, getHotKeyFromtEvent } from '../../utilities/utils';

export interface FieldsetShortcut {
  forward: string;
  backward: string;
}

@Component({
  selector: 'drv-root',
  template: '<ng-content />',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./root.scss'],
  host: {
    class: 'drv-root',
  },
})
export class RootComponent implements OnInit {
  readonly fieldsetShortCuts = input<FieldsetShortcut>();

  private configService = inject(ConfigService);
  private useReducedMotion: boolean;
  private document = inject<Document>(DOCUMENT);
  private renderer = inject(Renderer2);
  private fieldsetService = inject(FieldsetService);
  private fieldsets = new Set<FieldsetComponent>();
  private activeFieldset: FieldsetComponent;

  constructor() {
    const configService = this.configService;
    this.useReducedMotion = configService.config.reducedMotion;
  }

  ngOnInit(): void {
    if (this.useReducedMotion) {
      this.renderer.addClass(this.document.documentElement, 'drv-reduced-motion');
    }

    if (this.fieldsetShortCuts()) {
      this.fieldsetService.fieldset$.subscribe(fieldset => {
        if (this.fieldsets.has(fieldset)) {
          return;
        }
        fieldset.fieldsetElement().nativeElement.tabIndex = 0;
        this.fieldsets.add(fieldset);
      });

      this.renderer.listen(this.document.documentElement, 'keydown', this.handleKeyboardShortcuts.bind(this));
    }
  }

  private handleKeyboardShortcuts(event: KeyboardEvent): void {
    if (this.fieldsets.size === 0) {
      return;
    }

    const forward = normalizeShortCut(this.fieldsetShortCuts().forward);
    const backward = normalizeShortCut(this.fieldsetShortCuts().backward);
    const fieldsets = Array.from(this.fieldsets);
    const currentIndex = fieldsets.findIndex(fieldset => fieldset === this.activeFieldset);
    const eventKey = getHotKeyFromtEvent(event);

    if (forward && eventKey === forward) {
      const nextIndex = currentIndex + 1;
      this.activeFieldset = fieldsets[nextIndex] || fieldsets[0];
    }

    if (backward && eventKey === backward) {
      const nextIndex = currentIndex - 1;
      this.activeFieldset = fieldsets[nextIndex] || fieldsets[fieldsets.length - 1];
    }

    if (this.activeFieldset) {
      event.preventDefault();
      this.activeFieldset.fieldsetElement().nativeElement.focus();
    }
  }
}
