import { Subject } from 'rxjs';
import { Injectable } from '@angular/core';

export type ModalRole = 'alertdialog' | 'dialog';
export interface Modal {
  id?: string;
  panelId?: string;
  open: boolean;
}

@Injectable({
  providedIn: 'root',
})
export class ModalService {
  private modalData: Modal[] = [];
  private _modals = new Subject<Modal>();
  readonly modals = this._modals.asObservable();

  public unRegisterModal(id: string): void {
    const modal = this.modalData.find(({ id: modalId, panelId }) => modalId === id || panelId === id);

    if (modal) {
      this.modalData = this.modalData.filter(({ id: modalId, panelId }) => modalId !== id && panelId !== id);
    }
  }

  public updateModal(data: Modal): Promise<void> {
    if (!data.id && !data.panelId) {
      return;
    }

    const modal = this.modalData.find(({ id, panelId }) => id === data.id || panelId === data.panelId);

    if (!modal) {
      this.modalData.push(data);
      this._modals.next(data);
      return;
    }

    if (modal.open !== data.open) {
      modal.open = data.open;
      this._modals.next(data);
    }

    return new Promise(resolve => window.setTimeout(resolve, 10));
  }
}
