import { Component, OnDestroy, OnInit, ViewEncapsulation, input, inject } from '@angular/core';
import { Subscription } from 'rxjs';

import { GridDirective } from '../grid/grid.directive';
import { RowDirective } from '../grid/row.directive';
import { ColDirective } from '../grid/col.directive';
import { LoadingService } from './loading.service';
import { NgTemplateOutlet } from '@angular/common';

@Component({
  selector: 'drv-loading',
  templateUrl: './loading.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./loading.scss'],
  imports: [GridDirective, RowDirective, ColDirective, NgTemplateOutlet],
  host: {
    class: 'drv-loading',
    '[attr.aria-busy]': 'this.isBusy',
    'aria-live': 'polite',
  },
})
export class LoadingComponent implements OnDestroy, OnInit {
  private loadingService = inject(LoadingService);

  readonly loadingId = input.required<string>();
  readonly accessibleText = input<string>(undefined); // TODO v8 - should be required
  readonly loadingText = input<string>(undefined);

  protected isBusy = false;
  protected asProgress = true;

  private progress = 0;
  private subscription: Subscription;

  protected get ariaBusy(): boolean {
    return this.isBusy;
  }

  protected get cssClasses(): string {
    const cssClasses = ['drv-loading'];

    if (this.asProgress) {
      cssClasses.push('drv-loading--progress');

      if (this.progress === 100) {
        cssClasses.push('drv-loading--progress-finished');
      }
    }

    return cssClasses.join(' ');
  }

  protected get loadingProgress(): string {
    if (!this.asProgress) {
      return null;
    }

    return `--drv-loading-progress: ${Math.round((Math.max(0, Math.min(100, this.progress)) / 100) * 235)}px`;
  }

  protected get progressPercentage(): string {
    return this.asProgress ? `${this.progress} %` : '';
  }

  /** @internal */
  ngOnInit(): void {
    this.subscription = this.loadingService.knobs.subscribe(data => {
      if (data) {
        const { id, visible, progress = 0, asProgress } = data;

        if (id === this.loadingId()) {
          this.isBusy = visible;
          this.progress = progress;
          this.asProgress = asProgress;

          document.documentElement.classList[this.isBusy ? 'add' : 'remove']('drv-prevent-scroll');
        }
      }
    });
  }

  /** @internal */
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    document.documentElement.classList.remove('drv-prevent-scroll');
  }

  protected hide(e: AnimationEvent): void {
    const { type, animationName } = e;

    if (type === 'animationend' && animationName === 'dashout') {
      this.isBusy = false;
      document.documentElement.classList.remove('drv-prevent-scroll');
      // prevent flickering
      window.setTimeout(() => {
        this.asProgress = false;
        this.progress = 0;
      }, 250);
    }
  }
}
