import {
  Component,
  AfterViewInit,
  forwardRef,
  ViewEncapsulation,
  AfterContentInit,
  OnDestroy,
  booleanAttribute,
  ElementRef,
  input,
  viewChild,
  ContentChildren,
  QueryList,
} from '@angular/core';
import { Subject, takeUntil, switchMap, startWith } from 'rxjs';

import { type IconType } from '../../types/icons';
import { NavigationItem } from '../../types';
import { NavigationListComponent } from './navigation-list.component';
import { IconComponent } from '../icon/icon.component';

interface ActiveNavigationItem {
  label?: string;
  text?: string;
  icon?: IconType;
  badgeNumber?: number;
}

@Component({
  selector: 'drv-navigation',
  templateUrl: './navigation.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./navigation.scss'],
  imports: [IconComponent],
  host: {
    class: 'drv-navigation-host',
    '(window:resize)': 'handleWindowResize()',
  },
})
export class NavigationComponent implements AfterViewInit, AfterContentInit, OnDestroy {
  readonly navLabel = input.required<string>();
  readonly navButtonLabel = input('Navigation √∂ffnen');
  readonly showToggleButton = input(true, { transform: booleanAttribute });
  readonly toggleButtonText = input(true, { transform: booleanAttribute });
  readonly button = viewChild<ElementRef>('button');
  @ContentChildren(forwardRef(() => NavigationListComponent), { descendants: true })
  private _navs: QueryList<NavigationListComponent>;

  protected isOpen = false;
  protected activeItem: ActiveNavigationItem;

  private destroyed$ = new Subject();

  protected handleWindowResize(): void {
    this.sideMenuCalc();
  }

  /** @internal */
  ngAfterContentInit(): void {
    if (!this.toggleButtonText()) {
      return;
    }

    this._navs.changes
      .pipe(startWith([undefined]))
      .pipe(
        switchMap(changes => {
          this.updateNavs();
          return changes;
        }),
        takeUntil(this.destroyed$)
      )
      .subscribe();
  }

  /** @internal */
  ngAfterViewInit(): void {
    this.sideMenuCalc();
  }

  /** @internal */
  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
    document.documentElement.classList.remove('drv-prevent-scroll');
  }

  protected toggle(): void {
    this.isOpen = !this.isOpen;
    document.documentElement.classList.toggle('drv-prevent-scroll');
  }

  protected sideMenuCalc(): void {
    const button = this.button().nativeElement;

    if (!button) {
      return;
    }

    const { y, height } = button.getBoundingClientRect();

    document.documentElement.style.setProperty('--side-menu-btn-top', `${y / 16}rem`);
    document.documentElement.style.setProperty('--side-menu-btn-height', `${height / 16}rem`);
    document.documentElement.style.setProperty('--window-inner-height', `${window.innerHeight}px`);
  }

  private updateNavs(): void {
    this._navs.forEach(nav => {
      nav.itemChange.pipe(takeUntil(this.destroyed$)).subscribe(() => {
        nav.items().forEach(this.setNavActiveItem.bind(this));
      });
      nav.itemClick.pipe(takeUntil(this.destroyed$)).subscribe(item => {
        this.isOpen = false;
        this.activeItem = item;
      });
    });
  }

  private setNavActiveItem(item: NavigationItem): void {
    if (item.active) {
      this.activeItem = item;
    }
  }
}
