import {
  Component,
  ViewEncapsulation,
  booleanAttribute,
  input,
  ElementRef,
  viewChild,
  inject,
  OnInit,
} from '@angular/core';
import { nanoid } from 'nanoid';

import { FieldsetService } from './fieldset.service';

export type FieldsetLegendStyle = 'underlined' | 'none';

@Component({
  selector: 'drv-fieldset',
  templateUrl: './fieldset.component.html',
  styleUrl: './fieldset.scss',
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'drv-fieldset',
    '[attr.id]': 'this.id()',
  },
})
export class FieldsetComponent implements OnInit {
  readonly disabled = input(false, { transform: booleanAttribute });
  readonly fieldsetTitle = input('');
  readonly legendStyle = input<FieldsetLegendStyle>('underlined');
  readonly fieldsetElement = viewChild<ElementRef>('fieldset');
  readonly id = input<string>(`fieldset-${nanoid()}`);

  private fieldsetService = inject(FieldsetService);

  protected get legendCssClasses(): string {
    return ['drv-legend', `drv-legend--${this.legendStyle()}`].filter(Boolean).join(' ');
  }

  ngOnInit(): void {
    this.fieldsetService.setFieldset(this);
  }
}
