import { Injectable, ElementRef } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class TreeViewService {
  private anchors = new Subject<ElementRef>();
  private currentPage = new Subject<ElementRef>();
  readonly anchors$ = this.anchors.asObservable();
  readonly currentPage$ = this.currentPage.asObservable();

  focusAnchor(anchor: ElementRef): void {
    this.anchors.next(anchor);
  }

  setCurrentPage(anchor: ElementRef): void {
    if (anchor) {
      this.currentPage.next(anchor);
    }
  }
}
