import {
  Component,
  ElementRef,
  ViewEncapsulation,
  HostListener,
  AfterContentInit,
  AfterViewInit,
  TemplateRef,
  ViewContainerRef,
  OnDestroy,
  input,
  inject,
  contentChildren,
  viewChild,
} from '@angular/core';
import { DOCUMENT, NgTemplateOutlet } from '@angular/common';
import { TemplatePortal, DomPortalOutlet } from '@angular/cdk/portal';

import { computePosition, offset, flip } from '@floating-ui/dom';
import { trackByFn, getNextFocusableElement, getFocusableElements, normalizeEventKey } from '../../utilities/utils';

import type { IconType } from '../../types/icons';
import { ButtonComponent, type IconOnlyType, type IconPositionType } from '../button/button.component';

export type OverflowmenuPlacing = 'end' | 'start';
export type OverflowmenuBtnVariant = 'secondary' | 'tertiary';

@Component({
  selector: 'drv-overflowmenu',
  templateUrl: './overflowmenu.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./overflowmenu.scss'],
  imports: [ButtonComponent, NgTemplateOutlet],
  host: {
    class: 'drv-overflowmenu',
    '[class.drv-overflowmenu--place-start]': 'this.placing() === "start"',
    '[class.drv-overflowmenu--place-end]': 'this.placing() === "end"',
  },
})
export class OverflowmenuComponent implements AfterContentInit, AfterViewInit, OnDestroy {
  private elementRef = inject(ElementRef);
  private viewContainerRef = inject(ViewContainerRef);
  private document = inject<Document>(DOCUMENT);

  readonly placing = input<OverflowmenuPlacing>('end');
  readonly triggerButtonLabel = input.required<string>();
  readonly triggerButtonVariant = input<OverflowmenuBtnVariant>('secondary');
  readonly triggerButtonIcon = input<IconType>('overflow');
  readonly triggerButtonIconOnly = input<IconOnlyType>('only');
  readonly triggerButtonIconPosition = input<IconPositionType>('left');

  readonly buttons = contentChildren(ButtonComponent);
  readonly toggleButton = viewChild(ButtonComponent, { read: ElementRef });
  readonly menuContent = viewChild<ElementRef<HTMLElement>>('overflowMenuContent');
  readonly templatePortalContent = viewChild<TemplateRef<unknown>>('content');

  protected isOpen = false;
  protected trackByFn = trackByFn;

  private templatePortal: TemplatePortal<unknown>;
  private bodyPortalHost: DomPortalOutlet;
  private nextElement: HTMLElement;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const document = this.document;

    this.bodyPortalHost = new DomPortalOutlet(document.body);
  }

  @HostListener('window:click', ['$event.target']) protected handleWindowClick(target: HTMLElement): void {
    if (!this.elementRef?.nativeElement?.contains(target)) {
      this.toggle(false);
    }
  }

  /** @internal */
  ngAfterContentInit(): void {
    this.buttons().forEach(button => {
      const definedHandler = button.onClickHandler.bind(button);
      button.onClickHandler = (event: MouseEvent): void => {
        this.isOpen = false;
        if (definedHandler) {
          definedHandler(event);
        }
      };
      button.cssClass.set('drv-overflowmenu__btn');
      button.kind.set('secondary');
      button.iconPosition.set('left');
    });
  }

  /** @internal */
  ngAfterViewInit(): void {
    this.templatePortal = new TemplatePortal(this.templatePortalContent(), this.viewContainerRef);
  }

  /** @internal */
  ngOnDestroy(): void {
    this.bodyPortalHost.detach();
  }

  protected toggle(open?: boolean): void {
    this.isOpen = open !== undefined ? open : !this.isOpen;

    if (this.isOpen) {
      this.bodyPortalHost.attach(this.templatePortal);
      setTimeout(() => this.calculatePosition(), 10);
      return;
    }

    this.bodyPortalHost.detach();
  }

  protected focusOutButton(): void {
    const menuContent = this.menuContent();
    if (this.isOpen && menuContent?.nativeElement) {
      getFocusableElements(menuContent.nativeElement).shift()?.focus();
    }
  }

  /*
   * Why keyDown and not focusOut?
   * As the overflow-menu is appended as last child of the body,
   * the last button of the menu is the last focusable element of the body.
   * When the last focusable element of the body is blurred the address bar of the browser is focused.
   * This is not preventable with the focusOut event but with the keyDown event.
   */
  protected keyDown(event: KeyboardEvent): void {
    const key = normalizeEventKey(event);
    const elements = getFocusableElements(this.menuContent().nativeElement);
    const lastElement = elements.pop();
    const firstElement = elements.shift();

    if (key === 'tab') {
      if (event.target === lastElement && !event.shiftKey) {
        event.preventDefault();
        this.nextElement = getNextFocusableElement(
          this.document.body,
          this.toggleButton().nativeElement.firstElementChild as HTMLElement
        );

        if (this.nextElement === firstElement) {
          (this.toggleButton().nativeElement.firstElementChild as HTMLElement).focus();
          return;
        }

        this.nextElement?.focus();
        this.isOpen = false;
        return;
      }

      if (event.target === firstElement && event.shiftKey) {
        event.preventDefault();
        (this.toggleButton().nativeElement.firstElementChild as HTMLElement).focus();
      }
    }
  }

  private calculatePosition(): void {
    const menuContent = this.menuContent();
    const toggleButton = this.toggleButton();
    if (!menuContent?.nativeElement || !toggleButton?.nativeElement?.firstElementChild) {
      return;
    }

    void computePosition(toggleButton.nativeElement.firstElementChild, menuContent.nativeElement, {
      placement: 'bottom-end',
      middleware: [
        offset({
          crossAxis: 0,
          mainAxis: 8,
        }),
        flip(),
      ],
    }).then(({ x, y }) => {
      Object.assign(this.menuContent().nativeElement.style, {
        left: `${x}px`,
        top: `${y}px`,
        position: 'absolute',
        transform: 'none',
      });
    });
  }
}
