import { Component, ViewEncapsulation, input } from '@angular/core';

export type EmptyStatePictogramPosition = 'top' | 'side';

@Component({
  selector: 'drv-emptystate',
  templateUrl: './emptystate.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./emptystate.scss'],
  host: {
    class: 'drv-emptystate',
    '[class.drv-emptystate--top]': 'this.pictogramPosition() === "top"',
    '[class.drv-emptystate--side]': 'this.pictogramPosition() === "side"',
  },
})
export class EmptyStateComponent {
  readonly pictogramPosition = input<EmptyStatePictogramPosition>(undefined);
}
