import { Component, ViewEncapsulation, input } from '@angular/core';
import { IconComponent } from '../icon/icon.component';
import type { IconType } from '../../types/icons';

export type StatusLayout = 'block' | 'inline';
export type StatusVariant = 'default' | 'condensed';
export type StatusBgColor = 'ui-01' | 'ui-02' | 'ui-04' | 'ui-05' | 'ui-09' | 'alert' | 'warning' | 'success';

@Component({
  selector: 'drv-status',
  templateUrl: './status.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./status.scss'],
  imports: [IconComponent],
  host: {
    '[class]': 'this.containerCssClasses',
  },
})
export class StatusComponent {
  readonly layout = input<StatusLayout>(undefined);
  readonly variant = input<StatusVariant>(undefined);
  readonly bgColor = input<StatusBgColor>(undefined);
  readonly icon = input<IconType>(undefined);
  readonly iconLabel = input<string>('');

  protected get containerCssClasses(): string {
    const cssClasses = ['drv-status'];

    for (const prop of [this.variant(), this.layout(), this.bgColor()].filter(Boolean)) {
      cssClasses.push(`drv-status--${prop}`);
    }

    return cssClasses.join(' ');
  }
}
