import {
  Component,
  OnChanges,
  ViewEncapsulation,
  SimpleChanges,
  numberAttribute,
  booleanAttribute,
  input,
  output,
} from '@angular/core';

import { trackByFn } from '../../utilities/utils';
import { ButtonComponent } from '../button/button.component';

export type PaginationVariant = 'default' | 'no-line' | 'compact';

type MapFunction = (_: number, index: number) => number;

@Component({
  selector: 'drv-pagination',
  templateUrl: './pagination.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./pagination.scss'],
  imports: [ButtonComponent],
  host: {
    '[class]': 'this.pageMax() === 0 ? "" : "drv-pagination"',
    '[class.drv-pagination--no-line]': 'this.variant() === "no-line"',
    '[class.drv-pagination--compact]': 'this.variant() === "compact"',
    '[attr.aria-label]': 'this.paginationLabel()',
    '[attr.role]': 'navigation',
  },
})
export class PaginationComponent implements OnChanges {
  readonly variant = input<PaginationVariant>('default');
  readonly pageText = input('Seite');
  readonly prevPageText = input('zur vorherigen ' + this.pageText);
  readonly nextPageText = input('zur n√§chsten ' + this.pageText);
  readonly firstPageText = input('Zum Anfang');
  readonly lastPageText = input('Zum Ende');
  readonly pageFromLabel = input('von');
  readonly pageMax = input(0, { transform: numberAttribute });
  readonly currentPage = input(0, { transform: numberAttribute });
  readonly firstAndLastButton = input(false, { transform: booleanAttribute });
  readonly pageSelect = output<number>();
  readonly paginationLabel = input.required<string>();

  protected activePage = 0;
  protected trackByFn = trackByFn;

  protected get pages(): number[] {
    const emptyArray = (length: number): undefined[] => Array.from({ length });
    // eslint-disable-next-line prettier/prettier
    const fillArray = (offset: number): MapFunction => (_: number, index: number) => index + offset;

    if (this.pageMax() <= 9) {
      return emptyArray(this.pageMax()).map(fillArray(1));
    }

    let replacedEdges = 0;
    if (this.activePage > 4) {
      replacedEdges++;
    }
    if (this.activePage < this.pageMax() - 4) {
      replacedEdges++;
    }

    const calculatedOffset = Math.min(this.pageMax() - 7, Math.max(1, this.activePage - 3));
    return emptyArray(9 - replacedEdges).map(fillArray(calculatedOffset));
  }

  protected get disabledPrev(): boolean {
    return this.activePage === 1;
  }

  protected get disabledNext(): boolean {
    return this.activePage === this.pageMax();
  }

  protected get showMorePagesBackwards(): boolean {
    return this.pageMax() > 9 && this.activePage > 4;
  }

  protected get showMorePagesForward(): boolean {
    return this.pageMax() > 9 && this.activePage < this.pageMax() - 4;
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (Object.keys(changes).includes('currentPage')) {
      this.activePage = this.currentPage();
    }
  }

  protected selectPage(event: MouseEvent, page: number): void {
    event.preventDefault();
    this.activePage = page;
    this.pageSelect.emit(this.activePage);
  }

  protected prev(event: Event): void {
    event.preventDefault();
    this.activePage = Math.max(1, this.activePage - 1);
    this.pageSelect.emit(this.activePage);
  }

  protected next(event: Event): void {
    event.preventDefault();
    this.activePage = Math.min(this.pageMax(), this.activePage + 1);
    this.pageSelect.emit(this.activePage);
  }

  protected first(event: Event): void {
    event.preventDefault();
    this.activePage = 1;
    this.pageSelect.emit(this.activePage);
  }

  protected last(event: Event): void {
    event.preventDefault();
    this.activePage = this.pageMax();
    this.pageSelect.emit(this.activePage);
  }
}
