export const formatValid = (vsnr: string, withoutChecksum = false): boolean => {
  if (!vsnr) {
    return false;
  }

  if (withoutChecksum) {
    // 11-digits without checksum
    return /^(0[23489]|[15][0-9]|2[01345689]|[37][89]|4[23489]|6[01345689]|8[0129])([0-8][0-9]|[9][0-7])(00|0[1-9]|1[012])\d{2}[A-Z]\d{2}$/.test(
      vsnr
    );
  } else {
    // 12-digits with checksum
    return /^(0[23489]|[15][0-9]|2[01345689]|[37][89]|4[23489]|6[01345689]|8[0129])([0-8][0-9]|[9][0-7])(00|0[1-9]|1[012])\d{2}[A-Z]\d{3}$/.test(
      vsnr
    );
  }
};

