// Accepts only 12-digit vsnr with checksum
export const checksumValid = (vsnr: string): boolean => {
  if (!vsnr || vsnr.length !== 12) {
    return false;
  }

  // Transform letter in VSNR to 2-digit alphabetical position, e.g. "A" is "01" and "K" is "11"
  const letterAlphabeticalPosition = (vsnr.charCodeAt(8) - 64).toString().padStart(2, '0');
  const vsnrDigitsOnly = vsnr.replace(/[A-Z]/, letterAlphabeticalPosition);
  const vsnrChecksum = parseInt(vsnrDigitsOnly[12], 10);

  // Test whether any letter characters left
  if (/^[0-9]+$/.test(vsnrDigitsOnly)) {
    // Verify checksum
    return calculateChecksum(vsnrDigitsOnly) === vsnrChecksum;
  } else {
    return false;
  }
};

export const calculateChecksum = (vsnr: string): number => {
  // Multipliers for checksum calculation are subject to the VSNR validation specification, thus hard-coded.
  const MULTIPLIERS = [2, 1, 2, 5, 7, 1, 2, 1, 2, 1, 2, 1];
  const result = MULTIPLIERS.reduce((sum, multiplier, i) => {
    const interim = getSumOfDigits(multiplier * parseInt(vsnr[i], 10));
    return sum + interim;
  }, 0);

  // Result is the modulo of the sum of digits of each multiplied digit
  return result % 10;
};

export const formatVSNR = (vsnr: string): string => {
  const formatted = vsnr.replace(/([0-9]{1,2})\s?([0-9X]{6})\s?([A-Za-z]{1})\s?(\d{2,3})/, '$1 $2 $3 $4');
  return formatted.toLocaleUpperCase();
};

const getSumOfDigits = (digits: number): number =>
  digits < 10 ? digits : Math.floor(getSumOfDigits(digits / 10)) + (digits % 10);
