import {
  Component,
  HostListener,
  HostBinding,
  ViewEncapsulation,
  ElementRef,
  TemplateRef,
  OnChanges,
  SimpleChanges,
  booleanAttribute,
  input,
  model,
  inject,
  output,
  viewChild,
  viewChildren,
} from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { DeviceDetectorService } from 'ngx-device-detector';
import scrollIntoView from 'scroll-into-view-if-needed';

import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { ConfigService } from '../../services/config.service';

import { type AriaAttributes } from '../../types';
import { normalizeEventKey, isPrintable, trackByFn, throttle } from '../../utilities/utils';
import { FormitemComponent } from '../formitem/formitem.component';
import { calculatePosition } from '../dropdown/shared';
import { ButtonComponent } from '../button/button.component';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';
import { ToggletipComponent } from '../toggletip/toggletip.component';

export interface DrvComboboxOption {
  value: string;
}

export type ComboboxAutoCompleteOptions = 'list' | 'both' | 'none';

interface ComboBoxAriaAttributes extends AriaAttributes {
  [key: string]: unknown;
  expanded?: boolean | 'true' | 'false';
  hasPopUp?: 'listbox' | 'menu' | 'tree' | 'grid' | 'dialog';
  autocomplete?: ComboboxAutoCompleteOptions;
  controls?: string;
  required?: boolean | 'true' | 'false';
}

interface ComboBoxOptionTemplate extends DrvComboboxOption {
  selected?: boolean;
  hidden?: boolean;
}

@Component({
  selector: 'drv-combobox',
  templateUrl: './combobox.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['../dropdown/dropdown.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [NgClass, NgTemplateOutlet, FormitemComponent, ButtonComponent, AriaAttributeDirective, ToggletipComponent],
  host: {
    class: 'drv-dropdown-host',
  },
})
export class ComboboxComponent extends FormitemComponent implements OnChanges {
  private configService = inject(ConfigService);
  private deviceService = inject(DeviceDetectorService);
  readonly valueAccessor = inject<ValueAccessorDirective<string>>(ValueAccessorDirective);

  readonly options = input<DrvComboboxOption[]>([]);
  readonly label = model.required<string>();
  readonly id = model.required<string>();
  readonly placeholder = model<string>(undefined);
  readonly selectedText = input('Option(en) ausgew√§hlt');
  readonly disabled = model<boolean>(false);
  readonly required = model<boolean | unknown>(undefined);
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHiddenLabel = model(false);
  readonly hideToggleButton = input(false, { transform: booleanAttribute });
  readonly noResultsText = input('Keine Eintr√§ge gefunden');
  readonly toggleButtonLabel = input('Liste √∂ffnen');
  readonly autoComplete = input<ComboboxAutoCompleteOptions>('list');
  readonly ariaAttributes = input<AriaAttributes>({});
  readonly closed = output<null>();
  readonly open = output<null>();
  readonly input = viewChild<ElementRef<HTMLInputElement>>('input');
  readonly listbox = viewChild<ElementRef<HTMLElement>>('listbox');
  readonly container = viewChild<ElementRef<HTMLElement>>('container');
  readonly listitems = viewChildren<ElementRef>('listitem');

  protected isOpen = false;
  protected templateOptions: ComboBoxOptionTemplate[];
  protected trackByFn = trackByFn;
  protected keydownInput = throttle(this.keydownInputUnthrottled.bind(this), 100);
  protected filterOptions = throttle(this.filterOptionsUnthrottled.bind(this), 100);

  private innerValue?: string;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    super();
    const configService = this.configService;
    const valueAccessor = this.valueAccessor;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);
    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.innerValue = v;

      if (this.innerValue) {
        this.templateOptions.forEach(option => {
          option.selected = this.innerValue === option.value;
        });
      }

      window.setTimeout(() => {
        this.input().nativeElement.value = this.innerValue || '';
      }, 10);
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  @HostBinding('attr.role') protected get hostRole(): string | null {
    return this.deviceService.isDesktop() ? 'application' : null;
  }

  @HostBinding('class') protected get containerCssClasses(): string {
    return [...this.cssClass().split(' ')].filter(Boolean).join(' ');
  }

  protected get inputAriaAttributes(): ComboBoxAriaAttributes {
    const expanded = this.isOpen;

    return {
      ...this.ariaAttributes(),
      labelledby: this.id(),
      expanded,
      hasPopUp: 'listbox',
      autocomplete: this.autoComplete(),
      controls: `${this.id()}_listbox`,
      invalid: this.hasError() ? 'true' : this.ariaAttributes().invalid || 'false',
      required: this.required() as boolean,
      describedby: [
        this.ariaAttributes().describedby || undefined,
        this.hasError() || this.hasWarning() || this.hasInfo() ? `${this.id()}_errors` : undefined,
      ]
        .filter(Boolean)
        .join(' '),
    };
  }

  protected get cssBgClass(): string {
    const backgroundUI = this.backgroundUI();
    return ['drv-text-input drv-combobox__input', backgroundUI ? `drv-text-input--${backgroundUI}` : null]
      .filter(Boolean)
      .join(' ');
  }

  protected get activeDescendant(): string {
    if (!this.isOpen) {
      return null;
    }

    const id = this.templateOptions
      .filter(({ selected }) => selected)
      .map(opt => `drv-dropdown-value_${this.id()}-${this.templateOptions.indexOf(opt)}`)
      .pop();

    return id || null;
  }

  protected get isMobile(): boolean {
    return !this.deviceService.isDesktop();
  }

  protected get resultCount(): number {
    return this.templateOptions.filter(opt => !opt.hidden).length;
  }

  protected get noResults(): boolean {
    return this.templateOptions.length === 1 && this.templateOptions[0].value === this.noResultsText();
  }

  protected get showButton(): boolean {
    if (this.hideToggleButton()) {
      return false;
    }

    return this.templateOptions.length > 0;
  }

  get value(): string {
    return this.innerValue;
  }

  set value(value: string) {
    if (value !== this.innerValue) {
      this.innerValue = value;

      if (this.innerValue) {
        this.templateOptions.forEach(option => {
          option.selected = this.innerValue === option.value;
        });
      }

      this.valueAccessor.valueChange(value);
    }
  }

  @HostListener('window:click', ['$event.target']) protected handleWindowClick(target: HTMLElement): void {
    if (!this.container()?.nativeElement?.contains(target) && this.isOpen) {
      this.isOpen = false;
      this.closed.emit(null);
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.options) {
      const { previousValue } = changes.options;

      if (!previousValue) {
        this.templateOptions = this.options().map(this.optionMapper);
        return;
      }

      if (previousValue.length === 0 && this.input()?.nativeElement === document.activeElement) {
        this.toggle();
      }

      this.filterList();
    }
  }

  protected buttonClickHandler(): void {
    if (this.isOpen) {
      this.toggle(false);
      return;
    }

    this.toggle(true);
    this.filterList();
  }

  protected clickInput(): void {
    this.toggle(true);
    this.filterList();
  }

  protected selectOption(current: ComboBoxOptionTemplate, next: ComboBoxOptionTemplate): void {
    if (current) {
      current.selected = false;
    }

    if (next) {
      next.selected = true;
      const idx = this.templateOptions.indexOf(next);
      const element = this.listitems().at(idx)?.nativeElement;

      if (element) {
        window.requestAnimationFrame(() => scrollIntoView(element, { scrollMode: 'if-needed', block: 'nearest' }));
      }
    }
  }

  protected markSearchTerm(text: string): string {
    const inputValue = this.input();
    if (!inputValue) {
      return text;
    }

    const value = inputValue.nativeElement.value.toLowerCase();

    if (!value || !text.toLowerCase().includes(value)) {
      return text;
    }

    const index = text.toLowerCase().indexOf(value);
    const before = text.substring(0, index);
    const match = text.substring(index, index + value.length);
    const after = text.substring(index + value.length);

    return `${before}<b>${match}</b>${after}`;
  }

  protected blurHandler(event: FocusEvent): void {
    window.setTimeout(() => {
      if (!this.container()?.nativeElement?.contains(document.activeElement)) {
        this.toggle(false);
        this.valueAccessor.onBlur(event);
      }
    }, 250);
  }

  protected select(event: MouseEvent, index: number): void {
    const option = this.templateOptions[index];

    if (option) {
      event.stopPropagation();
      const inputValue = this.input();
      inputValue.nativeElement.value = option.value;
      this.value = option.value;
      this.toggle(false);
      inputValue.nativeElement.focus();
    }
  }

  protected toggle(isOpen?: boolean): void {
    this.isOpen = isOpen === undefined ? !this.isOpen : isOpen;

    window.requestAnimationFrame(() => {
      if (!this.isOpen) {
        this.templateOptions.forEach(option => (option.selected = false));
        this.closed.emit(null);
      } else {
        this.calculatePosition();
        this.open.emit(null);
      }
    });
  }

  protected setValue(): void {
    const input = this.input().nativeElement;
    const option = this.templateOptions.find(opt => opt.selected);

    if (option) {
      this.value = option.value;
      const inputValue = this.input();
      inputValue.nativeElement.value = option.value;
      inputValue.nativeElement.setSelectionRange(option.value.length, option.value.length);
      return;
    }

    this.value = input.value;
  }

  protected setValueFromSelect(event: Event): void {
    const target = event.target as HTMLInputElement;
    const { value } = target;
    this.value = value;
  }

  private filterList(setValue = true): void {
    this.templateOptions = this.options().map(this.optionMapper);

    this.templateOptions.forEach(option => {
      const { value: optionValue } = option;
      const value = (this.input().nativeElement as HTMLInputElement).value.toLowerCase();
      option.hidden = !optionValue.toLowerCase().startsWith(value);
    });

    const inputValue = this.input();
    if (this.templateOptions.every(opt => opt.hidden)) {
      this.templateOptions = [
        {
          value: this.noResultsText(),
          selected: false,
          hidden: false,
        },
      ];
    } else if (this.autoComplete() === 'both' && inputValue.nativeElement.value !== '') {
      const firstOption = this.templateOptions.find(opt => !opt.hidden);

      if (!firstOption || !setValue) {
        return;
      }

      const length = inputValue.nativeElement.value.length;
      firstOption.selected = true;
      this.setValue();
      inputValue.nativeElement.setSelectionRange(length, firstOption?.value.length || 0);
    }
  }

  private optionMapper(option: DrvComboboxOption): ComboBoxOptionTemplate {
    return {
      ...option,
      selected: false,
      hidden: false,
    };
  }

  private calculatePosition(): void {
    calculatePosition(this.input().nativeElement, this.listbox().nativeElement);
  }

  private keydownInputUnthrottled(event: KeyboardEvent): void {
    const key = normalizeEventKey(event);
    const filteredOptions = this.templateOptions.filter(opt => !opt.hidden);
    const selected = filteredOptions.find(opt => opt.selected);
    const idx = selected ? filteredOptions.indexOf(selected) : -1;

    let next, prev;

    next = idx > -1 ? Math.min(idx + 1, filteredOptions.length - 1) : 0;
    prev = idx > -1 ? Math.max(idx - 1, 0) : filteredOptions.length - 1;

    if (next === idx) {
      next = 0;
    }

    if (prev === idx) {
      prev = filteredOptions.length - 1;
    }

    const actions = {
      enter(): void {
        this.toggle(false);
        this.setValue();

        if (!this.noResults) {
          this.filterList(false);
        }
      },
      esc(): void {
        this.toggle(false);
      },
      down(): void {
        if (this.noResults) {
          return;
        }

        if (!this.isOpen) {
          this.toggle(true);
        }
        this.selectOption(selected, filteredOptions[next]);

        if (this.autoComplete === 'both') {
          this.setValue();
        }
      },
      up(): void {
        if (this.noResults) {
          return;
        }

        this.selectOption(selected, filteredOptions[prev]);

        if (this.autoComplete === 'both') {
          this.setValue();
        }
      },
    };

    if (actions[key]) {
      event.preventDefault();
      actions[key].call(this);
    }
  }

  private filterOptionsUnthrottled(event: KeyboardEvent): void {
    const key = normalizeEventKey(event);
    const isPrintableKey = isPrintable(key);

    if (['up', 'down', 'enter', 'esc'].includes(key)) {
      return;
    }

    if (this.autoComplete() === 'none') {
      return;
    }

    if (['delete', 'backspace'].includes(key) || isPrintableKey) {
      this.toggle(!!(event.target as HTMLInputElement).value);
      this.filterList(isPrintableKey);
    }
  }
}
