import {
  Component,
  HostListener,
  forwardRef,
  ViewEncapsulation,
  TemplateRef,
  HostBinding,
  OnInit,
  OnDestroy,
  booleanAttribute,
  model,
  input,
  inject,
  viewChild,
} from '@angular/core';
import {
  NG_VALUE_ACCESSOR,
  ControlValueAccessor,
  FormControl,
  NG_VALIDATORS,
  Validator,
  ControlContainer,
  AbstractControl,
  FormGroupDirective,
  FormsModule,
} from '@angular/forms';
import { MaskitoOptions } from '@maskito/core';
import { MaskitoDirective } from '@maskito/angular';
import { Subject, takeUntil } from 'rxjs';
import { nanoid } from 'nanoid';
import { FormitemComponent } from '../formitem/formitem.component';
import { LabelComponent } from '../label/label.component';
import { type AriaAttributes } from '../../types';
import { checksumValid, formatValid, calculateChecksum, formatVSNR, checkAreaCode } from './insurancenr.helper';
import { ConfigService } from '../../services/config.service';
import type { SelectionRange } from '@maskito/core/src/lib/types';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';

type CallbackFunction = () => void;

@Component({
  selector: 'drv-insurancenumber',
  templateUrl: './insurancenumber.component.html',
  styleUrl: '../textinput/textinput.scss',
  encapsulation: ViewEncapsulation.None,
  imports: [FormsModule, FormitemComponent, LabelComponent, AriaAttributeDirective, MaskitoDirective],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => InsuranceNumberComponent),
      multi: true,
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => InsuranceNumberComponent),
      multi: true,
    },
  ],
  host: {
    class: 'drv-insurancenumber',
  },
})
// eslint-disable-next-line prettier/prettier
export class InsuranceNumberComponent extends FormitemComponent implements ControlValueAccessor, Validator, OnInit, OnDestroy {
  private controlContainer = inject(ControlContainer, { optional: true, host: true, skipSelf: true })!;
  private configService = inject(ConfigService);

  readonly id = input<string>(`insnr-${nanoid()}`);
  readonly name = input<string>(undefined);
  readonly label = input('Versicherungsnummer');
  readonly placeholder = input<string>('12-stellige Versicherungsnummer');
  readonly disabled = model<boolean>(false);
  readonly required = model<boolean>(undefined);
  readonly readonly = input(false, { transform: booleanAttribute });
  readonly ariaAttributes = input<AriaAttributes>({});
  /** ensures that users type VSNR according to predefined format */
  readonly strict = model(false);
  /** content of the toggletip */
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });
  // additional error messages
  readonly validationErrorMessage = input('Bitte eine g√ºltige 12-stellige Versicherungsnummer eingeben.');
  readonly requiredErrorMessage = input('Die Versicherungsnummer muss ausgef√ºllt werden.');
  readonly checksumErrorMessage = input('Die Pr√ºfziffer f√ºr die Versicherungsnummer ist ung√ºltig.');
  readonly areacodeErrorMessage = input('Die Bereichsnummer ist ung√ºltig.');
  readonly formControlName = input<string>(undefined);

  readonly input = viewChild<{
    nativeElement: HTMLInputElement;
  }>('input');
  @HostBinding('attr.id') protected externalId = '';

  @HostBinding('class') protected get containerCssClasses(): string {
    return [...this.cssClass().split(' ')].filter(Boolean).join(' ');
  }

  // Maskito options for vsnr masking XXDDMMYYFSSP where
  // X = range
  // D = day
  // M = month
  // Y = year
  // F = initial of last name
  // S = serial
  // P = checksum
  // i.e. 24010101T236 or 24 010101 T 236 (with whitespaces)
  readonly maskitoOptions: MaskitoOptions = {
    mask: [
      /\d/,
      /\d/,
      ' ',
      /[0-9X]/,
      /[0-9X]/,
      /[0-9X]/,
      /[0-9X]/,
      /[0-9X]/,
      /[0-9X]/,
      ' ',
      /[A-Za-z]/,
      ' ',
      /\d/,
      /\d/,
      /\d/,
    ],
    postprocessors: [
      ({ value, selection }): { value: string; selection: SelectionRange } => ({
        value: value.toUpperCase(),
        selection,
      }),
    ],
  };

  protected maxLength = 15;

  private innerValue?: string;
  private control: AbstractControl;

  // Placeholders for callbacks for the Control Value Accessor
  private onTouchedCallback?: () => void;
  private onChangeCallback?: (_: string) => void;
  private destroyed$ = new Subject();

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    super();
    const configService = this.configService;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);
  }

  protected get inputAriaAttributes(): AriaAttributes {
    const describedby: string[] = [];

    const ariaAttributes = this.ariaAttributes();
    if (ariaAttributes.describedby) {
      describedby.push(ariaAttributes.describedby);
    }

    const hasError = this.hasError();
    if (hasError || this.hasWarning() || this.hasInfo()) {
      describedby.push(`${this.id()}_errors`);
    }

    return {
      ...ariaAttributes,
      describedby: [...new Set(describedby)].join(' '),
      invalid: hasError ? 'true' : ariaAttributes.invalid || 'false',
    };
  }

  protected get cssInputClass(): string {
    const cssClasses = ['drv-text-input', `drv-text-input--${this.backgroundUI()}`];
    return cssClasses.join(' ');
  }

  get value(): string {
    return this.innerValue;
  }

  set value(value: string) {
    if (value !== this.innerValue) {
      this.innerValue = value;
      this.onChangeCallback?.(value);
    }
  }

  @HostListener('focusout', ['$event.target']) protected onFocusout(target: HTMLElement): void {
    if (target === this.input().nativeElement) {
      this.onBlur();
    }
  }

  // Validation logic for provider NG_VALIDATORS will
  // Logic for NG_VALIDATORS provider, returns validation result to parent form
  /**
   * @param {FormControl} control FormControl
   * @returns {Record<string, unknown> | null} Validation result
   * @internal
   */
  validate(control: FormControl): Record<string, unknown> | null {
    const value: string = control.value;

    const required = this.required();
    if (required && value.replace(/\s+/g, '').length === 0) {
      this.hasError.set(this.control.touched || this.control.dirty);
      this.errorText.set([{ message: this.requiredErrorMessage() }]);
      return required ? { required: 'vsnr required' } : null;
    }

    const vsnr = value.replace(/\s+/g, '').toUpperCase();

    if (vsnr.length > 0 && vsnr.length < 11) {
      this.hasError.set(true);
      this.errorText.set([{ message: this.validationErrorMessage() }]);
      return { minLength: 'vsnr minlength' };
    }

    if ([11, 12].includes(vsnr.length) && !checkAreaCode(vsnr)) {
      this.hasError.set(true);
      this.errorText.set([{ message: this.areacodeErrorMessage() }]);
      return { invalid: 'vsnr areacode invalid' };
    }

    if (
      (vsnr.length === 11 && !formatValid(vsnr, true)) ||
      (vsnr.length === 12 && !formatValid(vsnr)) ||
      vsnr.length > 12
    ) {
      this.hasError.set(true);
      this.errorText.set([{ message: this.validationErrorMessage() }]);
      return { invalid: 'vsnr invalid' };
    }

    if (vsnr.length === 12 && !checksumValid(vsnr)) {
      this.hasError.set(true);
      this.errorText.set([{ message: this.checksumErrorMessage() }]);
      return { invalid: 'vsnr checksum invalid' };
    }

    this.hasError.set(false);
    return null;
  }

  /** @internal */
  ngOnInit(): void {
    const formControlName = this.formControlName();
    if (this.controlContainer && formControlName) {
      const form = this.controlContainer as FormGroupDirective;
      this.control = form.control.get(formControlName);

      form.ngSubmit.pipe(takeUntil(this.destroyed$)).subscribe(() => {
        if (this.required() && this.control.value.length === 0) {
          this.hasError.set(true);
        }
      });
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  /** @internal */
  onBlur(): void {
    const vsnr = this.innerValue?.replace(/ /g, '').toUpperCase();

    if (this.control?.updateOn === 'blur') {
      if (this.required() && vsnr.length === 0) {
        this.hasError.set(true);
        this.errorText.set([{ message: this.requiredErrorMessage() }]);
      }
    }

    if (vsnr?.length === 11 && formatValid(vsnr, true)) {
      const letterAlphabeticalPosition = (vsnr.charCodeAt(8) - 64).toString().padStart(2, '0');
      const vsnrDigitsOnly = vsnr.replace(/[A-Z]/, letterAlphabeticalPosition);
      const newChecksum = calculateChecksum(vsnrDigitsOnly);

      this.value = formatVSNR(`${this.value}${newChecksum}`);
      this.onChangeCallback?.(this.value);
    }

    if (vsnr?.length === 12 && formatValid(vsnr, false)) {
      this.value = formatVSNR(this.value);
      this.onChangeCallback?.(this.value);
    }

    // Set touched on blur
    this.onTouchedCallback?.();
  }

  /**
   * @param {string} value Value to write
   * @internal
   */
  writeValue(value: string): void {
    if (value !== this.innerValue) {
      this.innerValue = value;
    }
  }

  /**
   * @param {CallbackFunction} fn Callback function
   * @internal
   */
  registerOnChange(fn: CallbackFunction): void {
    this.onChangeCallback = fn;
  }

  /**
   * @param {CallbackFunction} fn Callback function
   * @internal
   */
  registerOnTouched(fn: CallbackFunction): void {
    this.onTouchedCallback = fn;
  }

  /**
   * @param {boolean} isDisabled Is disabled
   * @internal
   */
  setDisabledState(isDisabled: boolean): void {
    this.disabled.set(isDisabled);
  }

  protected onInputChange(value: string): void {
    this.value = value.toUpperCase();
  }
}
