import { AfterViewInit, Component, ViewEncapsulation, contentChildren } from '@angular/core';

import { merge } from 'rxjs';
import { AccordionComponent } from '../../components/accordion/accordion.component';
import { AriaAccordionItemComponent } from './aria-accordionitem.component';
import { ButtonComponent, type AriaAttributes } from '../../components/button/button.component';

@Component({
  selector: 'drv-aria-accordion',
  templateUrl: '../../components/accordion/accordion.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['../../components/accordion/accordion.scss'],
  imports: [ButtonComponent],
})
export class AriaAccordionComponent extends AccordionComponent implements AfterViewInit {
  readonly ariaItems = contentChildren(AriaAccordionItemComponent);

  private expandedAriaItems: boolean[] = [];

  /**
   * @returns {AriaAttributes} aria attributes
   * @internal
   */
  get buttonAriaAttributes(): AriaAttributes {
    return {
      pressed: this.expandedAriaItems.every(Boolean) ? true : this.expandedAriaItems.some(Boolean) ? 'mixed' : false,
      label: this.expandedAriaItems.every(Boolean) ? this.toggleAllTextClose() : this.toggleAllTextOpen(),
    };
  }

  /** @internal */
  toggleAll(): void {
    this.ariaItems().forEach(otherItem => {
      otherItem.markForCheck();
      otherItem.isOpen = this.buttonAriaAttributes.pressed === false || this.buttonAriaAttributes.pressed === 'mixed';
    });

    this.expandedAriaItems = this.ariaItems().map(({ isOpen: isOpenInput }) => {
      const isOpen = isOpenInput;
      return isOpen;
    });
  }

  ngAfterViewInit(): void {
    this.initAriaWatchers();
    this.expandedAriaItems = this.ariaItems().map(({ isOpen: isOpenInput }) => {
      const isOpen = isOpenInput;
      return isOpen;
    });
  }

  private closeOtherArias(item: AriaAccordionItemComponent): void {
    this.ariaItems()
      .filter(otherItem => otherItem !== item)
      .forEach(otherItem => {
        otherItem.markForCheck();
        otherItem.isOpen = false;
      });
  }

  private initAriaWatchers(): void {
    const toggles = this.ariaItems().map(({ toggled }) => toggled);

    merge(...toggles).subscribe((target: AriaAccordionItemComponent) => {
      this.expandedAriaItems = this.ariaItems().map(({ isOpen: isOpenInput }) => {
        const isOpen = isOpenInput;
        return isOpen;
      });
      if (target.isOpen && !this.multiselectable) {
        this.closeOtherArias(target);
      }
    });
  }
}
