import {
  Component,
  HostBinding,
  HostListener,
  ViewEncapsulation,
  TemplateRef,
  booleanAttribute,
  forwardRef,
  AfterContentInit,
  ElementRef,
  input,
  model,
  inject,
  output,
  viewChild,
  contentChild,
} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { MaskitoOptions } from '@maskito/core';
import { MaskitoDirective } from '@maskito/angular';
import { FormitemComponent } from '../formitem/formitem.component';
import { ButtonComponent } from '../button/button.component';
import { LabelComponent } from '../label/label.component';
import { ComboboxComponent } from '../combobox/combobox.component';
import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { ConfigService } from '../../services/config.service';
import { type AriaAttributes } from '../../types';
import { type IconOnlyType } from '../button/button.component';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';
import { ShortcutDirective } from '../../directives/keyboardshortcut.directive';

export type SearchbarVariant = 'default' | 'mini' | 'icon-only';

@Component({
  selector: 'drv-searchbar',
  templateUrl: './searchbar.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./searchbar.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [
    FormsModule,
    ButtonComponent,
    FormitemComponent,
    LabelComponent,
    MaskitoDirective,
    AriaAttributeDirective,
    ShortcutDirective,
  ],
  host: {
    class: 'drv-searchbar',
    '[class.drv-searchbar--mini]': 'variant() === "mini"',
    '[class.drv-searchbar--icon-only]': 'variant() === "icon-only"',
  },
})
export class SearchbarComponent extends FormitemComponent implements AfterContentInit {
  readonly valueAccessor = inject<ValueAccessorDirective<string>>(ValueAccessorDirective);
  private configService = inject(ConfigService);

  readonly label = input.required<string>();
  readonly buttonText = input.required<string>();
  readonly buttonTextClear = input.required<string>();
  readonly placeholder = input<string>(undefined);
  readonly disabled = model<boolean>(false);
  readonly required = model<boolean>(undefined);
  readonly buttonDisabled = input(false, { transform: booleanAttribute });
  readonly variant = input<SearchbarVariant>('default');
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });
  readonly maskOptions = input<MaskitoOptions>(undefined);
  readonly keyboardShortcut = input<string>(undefined);
  readonly input = viewChild<ElementRef<HTMLInputElement>>('input');

  readonly searched = output<string>();
  readonly combobox = contentChild(forwardRef(() => ComboboxComponent));
  @HostBinding('attr.id') protected externalId = '';

  private innerValue?: string;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    super();
    const valueAccessor = this.valueAccessor;
    const configService = this.configService;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.innerValue = v;
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);

      const combobox = this.combobox();
      if (combobox) {
        combobox.disabled.set(disabled);
      }
    });
  }

  @HostBinding('class') protected get containerCssClasses(): string {
    return [...this.cssClass().split(' ')].filter(Boolean).join(' ');
  }

  protected get cssWrapperClass(): string {
    return [
      'drv-searchbar__wrapper',
      this.hasError() ? 'drv-searchbar__wrapper--error' : null,
      this.disabled() ? 'drv-searchbar__wrapper--disabled' : null,
    ]
      .filter(Boolean)
      .join(' ');
  }

  protected get cssBgClass(): string {
    return ['drv-text-input drv-searchbar__input', `drv-text-input--${this.backgroundUI()}`].filter(Boolean).join(' ');
  }

  protected get inputAriaAttributes(): AriaAttributes {
    if (this.hasError() || this.hasInfo() || this.hasWarning()) {
      return {
        describedby: `${this.id()}_error`,
        invalid: true,
      };
    }

    return {};
  }

  protected get iconOnly(): IconOnlyType {
    return this.variant() === 'default' ? 'only-md' : 'only';
  }

  get value(): string {
    return this.innerValue;
  }

  set value(value: string) {
    this.valueAccessor.valueChange(value);
    this.innerValue = value;
  }

  @HostListener('focusout', ['$event']) protected onFocusout(event: FocusEvent): void {
    if (this.combobox()) {
      return;
    }

    if (event.target === this.input().nativeElement) {
      this.valueAccessor.onBlur(event);
    }
  }

  /** @internal */
  ngAfterContentInit(): void {
    const combobox = this.combobox();
    if (combobox) {
      combobox.id.set(this.id());
      combobox.label.set(this.label());
      combobox.placeholder.set(this.placeholder());
      combobox.visuallyHiddenLabel.set(true);
      combobox.collapseMessages.set(true);
    }
  }

  protected clickHandler(): void {
    const combobox = this.combobox();
    if (combobox) {
      this.searched.emit(combobox.input().nativeElement.value);
      return;
    }

    this.searched.emit(this.input().nativeElement.value);
  }

  protected clickClearHandler(): void {
    this.value = '';
    this.input().nativeElement.focus();
  }
}
