import { Component, ViewEncapsulation, booleanAttribute, input, output } from '@angular/core';

import { IconComponent } from '../icon/icon.component';
@Component({
  selector: 'drv-details',
  templateUrl: './details.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./details.scss'],
  imports: [IconComponent],
  host: {
    class: 'drv-details-host',
    '[attr.title]': 'null',
  },
})
export class DetailsComponent {
  readonly isOpen = input(false, { transform: booleanAttribute });
  readonly title = input.required<string>();
  readonly toggled = output<boolean>();

  protected toggleHandler(e: Event): void {
    const { target }: { target: EventTarget } = e;
    this.toggled.emit((target as HTMLDetailsElement).open);
  }
}
