import { FocusOrigin, FocusMonitor } from '@angular/cdk/a11y';
import { AfterViewInit, Directive, OnDestroy, input, inject, QueryList, ContentChildren } from '@angular/core';
import { FormControlName, FormGroup, FormGroupDirective } from '@angular/forms';
import { Subscription } from 'rxjs';
import { NativeElementRepositoryService } from '../services/nativeelementrepository.service';

export interface FormAutoFocusConfig {
  onInit?: FocusFirstType;
  onSubmit?: FocusFirstWithErrorType;
  focusOrigin?: FocusOrigin;
}

export type FocusFirstType = 'none' | 'focusFirst' | 'focusFirstRequired';
export type FocusFirstWithErrorType = FocusFirstType | 'focusFirstWithError';

const defaultConfig: FormAutoFocusConfig = {
  onSubmit: 'focusFirstWithError',
  focusOrigin: 'keyboard',
};

@Directive({
  selector: '[drv-form-auto-focus]',
  providers: [NativeElementRepositoryService],
})
export class FormAutoFocusDirective implements AfterViewInit, OnDestroy {
  private readonly formGroupDirective = inject(FormGroupDirective);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly repository = inject(NativeElementRepositoryService);

  readonly formGroup = input.required<FormGroup>();
  readonly formAutoFocusConfig = input.required<FormAutoFocusConfig>();

  @ContentChildren(FormControlName, { descendants: true }) formControls!: QueryList<FormControlName>;

  private readonly subscriptions: Subscription = new Subscription();

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    this.registerOnSubmitHook();
  }

  get config(): FormAutoFocusConfig {
    return { ...defaultConfig, ...this.formAutoFocusConfig() };
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.repository.init(this.formControls, this.formGroupDirective);
      this.switchFocus(this.config.onInit);
    });
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  focusFirst(): void {
    this.focusControl(this.repository.findFirst());
  }

  focusFirstRequired(): void {
    this.focusControl(this.repository.findFirstRequired());
  }

  submittedFocusFirstWithError(): void {
    this.focusControl(this.repository.submittedFindFirstWithError());
  }

  private registerOnSubmitHook(): void {
    this.subscriptions.add(
      this.formGroupDirective.ngSubmit.subscribe(() => {
        this.switchFocus(this.config.onSubmit);
      })
    );
  }

  private switchFocus(focus: FocusFirstType | FocusFirstWithErrorType): void {
    const actionMap = {
      focusFirst: this.focusFirst,
      focusFirstRequired: this.focusFirstRequired,
      focusFirstWithError: this.submittedFocusFirstWithError,
    };

    if (actionMap[focus]) {
      actionMap[focus].call(this);
    }
  }

  private focusControl(control: FormControlName | undefined): void {
    if (!control) {
      return;
    }

    const found = this.repository.findByControlName(control.name);

    if (found) {
      const element = this.repository.getNativeElement(found);

      if (!element) {
        return;
      }

      this.focusElement(element);
    }
  }

  private focusElement(element: HTMLElement): void {
    this.focusMonitor.focusVia(element, this.config.focusOrigin);
  }
}
