import {
  AfterViewInit,
  Component,
  ViewEncapsulation,
  input,
  contentChildren,
  Input,
  booleanAttribute,
} from '@angular/core';
import { merge } from 'rxjs';
import { AccordionItemComponent } from './accordionitem.component';
import type { AriaAttributes } from '../button/button.component';
import { ButtonComponent } from '../button/button.component';

@Component({
  selector: 'drv-accordion',
  templateUrl: './accordion.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./accordion.scss'],
  imports: [ButtonComponent],
  host: {
    class: 'drv-accordion',
  },
})
export class AccordionComponent implements AfterViewInit {
  @Input({ transform: booleanAttribute }) multiselectable = true;
  @Input({ transform: booleanAttribute }) showToggleAllButton = false;

  readonly toggleAllTextOpen = input('Alle √∂ffnen');
  readonly toggleAllTextClose = input('Alle schlie√üen');
  readonly headline = input('');
  readonly items = contentChildren(AccordionItemComponent);

  private expandedItems: boolean[] = [];

  protected get buttonAriaAttributes(): AriaAttributes {
    return {
      pressed: this.expandedItems.every(Boolean) ? true : this.expandedItems.some(Boolean) ? 'mixed' : false,
      label: this.expandedItems.every(Boolean) ? this.toggleAllTextClose() : this.toggleAllTextOpen(),
    };
  }

  ngAfterViewInit(): void {
    this.initWatchers();
    this.expandedItems = this.items().map(({ isOpen: isOpenInput }) => {
      const isOpen = isOpenInput;
      return isOpen;
    });
  }

  protected toggleAll(): void {
    this.items().forEach(otherItem => {
      otherItem.markForCheck();
      otherItem.isOpen = this.buttonAriaAttributes.pressed === false || this.buttonAriaAttributes.pressed === 'mixed';
    });

    this.expandedItems = this.items().map(({ isOpen: isOpenInput }) => {
      const isOpen = isOpenInput;
      return isOpen;
    });
  }

  private closeOthers(item: AccordionItemComponent): void {
    this.items()
      .filter(otherItem => otherItem !== item)
      .forEach(otherItem => {
        otherItem.markForCheck();
        otherItem.isOpen = false;
      });
  }

  private initWatchers(): void {
    const toggles = this.items().map(({ toggled }) => toggled);

    merge(...toggles).subscribe((target: AccordionItemComponent) => {
      this.expandedItems = this.items().map(({ isOpen: isOpenInput }) => {
        const isOpen = isOpenInput;
        return isOpen;
      });
      if (target.detailsElement().nativeElement.open && !this.multiselectable) {
        this.closeOthers(target);
      }
    });
  }
}
