import { booleanAttribute, Component, inject, input, output, ViewEncapsulation } from '@angular/core';
import { NavigationExtras, Route, Router } from '@angular/router';
import { IconComponent } from '../../components/icon/icon.component';
import { IconLinkTarget } from '../../components/iconlink/iconlink.component';

@Component({
  selector: 'drv-teaser',
  encapsulation: ViewEncapsulation.None,
  imports: [IconComponent],
  templateUrl: './teaser.component.html',
  styleUrl: './teaser.component.scss',
  host: {
    class: 'drv-teaser',
  },
})
export class TeaserComponent {
  private router = inject(Router, { optional: true })!;

  readonly title = input.required<string>();
  readonly teaserText = input<string>(undefined);
  readonly linkText = input.required<string>();
  readonly externalLinkText = input<string>('(√ñffnet im neuen Fenster)');
  readonly href = input<string>(undefined);
  readonly target = input<IconLinkTarget>(undefined);
  readonly route = input<Route[] | string[]>(undefined);
  readonly routeExtras = input<NavigationExtras>(undefined);
  readonly isExternalLink = input(false, { transform: booleanAttribute });
  readonly itemClick = output<MouseEvent>();

  protected get getHref(): string {
    const href = this.href();
    if (href) {
      return href;
    }
    const route = this.route();
    return this.router && route ? '.' + this.router.createUrlTree(route, this.routeExtras() || {}).toString() : '/#';
  }

  protected onClickHandler(event: PointerEvent | MouseEvent): void {
    const route = this.route();
    if (this.router && route) {
      void this.router.navigate(route, this.routeExtras());
    }

    if (!this.href()) {
      event.preventDefault();
    }

    this.itemClick.emit(event);
  }
}
