import { Component, ViewEncapsulation, input, output, viewChildren, numberAttribute } from '@angular/core';
import { IconlinkComponent, IconLinkData, type IconLinkTarget } from '../iconlink/iconlink.component';
import { NavigationExtras } from '@angular/router';

import { ItemlistComponent } from '../itemlist/itemlist.component';
import { ItemlistItemComponent } from '../itemlist/itemlistitem.component';
import { trackByFn } from '../../utilities/utils';
import type { IconType } from '../../types/icons';

export type LinklistColumns = 1 | 2 | 3 | 4;
export type LinklistType = 'button' | 'anchor';
export type LinklistPositions = 'left' | 'right';
export interface LinklistData {
  icon: IconType;
  text: string;
  target?: IconLinkTarget;
  href?: string;
  // createUrlTree expects any[] https://angular.dev/api/router/Router#createUrlTree
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  route?: any[];
  routeExtras?: NavigationExtras;
  downloadAble?: boolean;
  disabled?: boolean;
  subtitle?: string;
  data?: IconLinkData;
}

@Component({
  selector: 'drv-linklist',
  templateUrl: './linklist.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./linklist.scss'],
  imports: [IconlinkComponent, ItemlistItemComponent],
  host: {
    class: 'drv-linklist',
    '[attr.role]': 'this.data().length > 0 ? "list" : null',
    '[style.--_drv-linklist-columns]': 'this.columns() > 1 ? this.columns() : null',
    '[class.drv-linklist--left]': 'this.iconPosition() === "left"',
    '[class.drv-linklist--right]': 'this.iconPosition() === "right"',
  },
})
export class LinklistComponent extends ItemlistComponent {
  readonly columns = input<number, LinklistColumns>(1, { transform: numberAttribute });
  readonly data = input<LinklistData[]>([]);
  readonly type = input<LinklistType>('anchor');
  readonly iconPosition = input<LinklistPositions>('right');
  readonly itemClick = output<{
    item: LinklistData;
    event: MouseEvent;
  }>();

  readonly _links = viewChildren(IconlinkComponent);

  protected trackByFn = trackByFn;

  protected onClickHandler(item: LinklistData, event: MouseEvent): void {
    event.stopPropagation();
    this.itemClick.emit({ item, event });
  }
}
