import {
  Component,
  TemplateRef,
  ViewEncapsulation,
  Renderer2,
  OnInit,
  OnDestroy,
  OnChanges,
  SimpleChanges,
  booleanAttribute,
  AfterViewInit,
  input,
  model,
  Input,
  inject,
  output,
  viewChild,
  ElementRef,
} from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import type { Route } from '@angular/router';
import { NgTemplateOutlet } from '@angular/common';

import { computePosition, flip, offset, shift } from '@floating-ui/dom';
import { getHotKeyHint } from 'keyux';

import type { IconType } from '../../types/icons';
import type { BackgroundUI } from '../../types';
import { type PlacementType } from '../tooltip/tooltip.component';
import { ConfigService } from '../../services/config.service';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';
import { DataAttributeDirective } from '../../directives/dataattributes.directive';
import { ShortcutDirective } from '../../directives/keyboardshortcut.directive';
import { IconComponent } from '../icon/icon.component';
import { normalizeShortCut } from '../../utilities/utils';

export type AriaPressedType = boolean | 'mixed';
export interface AriaAttributes {
  [key: string]: unknown;
  hasPopUp?: boolean;
  describedBy?: string;
  labelledBy?: string;
  label?: string;
  expanded?: boolean;
  pressed?: AriaPressedType;
  controls?: string;
}

export type IconPositionType = 'left' | 'right';
export type IconOnlyType = 'only' | 'only-xlg' | 'only-lg' | 'only-md' | 'only-sm';
export type FullSizeOptionType = 'full' | 'full-xlg' | 'full-lg' | 'full-md' | 'full-sm';
export type ButtonTypeType = 'button' | 'submit' | 'anchor' | 'input' | 'reset';
export type ButtonKindType = 'primary' | 'secondary' | 'tertiary';
export type ClickFunction = (event: MouseEvent | unknown) => void;

@Component({
  selector: 'drv-button',
  templateUrl: './button.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./button.scss', '../tooltip/tooltip.scss'],
  imports: [AriaAttributeDirective, IconComponent, DataAttributeDirective, ShortcutDirective, NgTemplateOutlet],
  host: {
    '[class]': 'this.hostClass()',
    '[attr.data-expanded]': 'this.ariaAttributes()?.expanded ?? null',
  },
})
export class ButtonComponent implements OnDestroy, OnInit, OnChanges, AfterViewInit {
  @Input({ transform: booleanAttribute }) tooltip = false;
  private router = inject(Router, { optional: true })!;
  private renderer = inject(Renderer2);
  private configService = inject(ConfigService);

  readonly cssClass = model('');
  readonly type = input<ButtonTypeType>('button');
  readonly disabled = input(false, { transform: booleanAttribute });
  readonly label = input.required<string>();
  readonly kind = model<ButtonKindType>('primary');
  readonly href = input<string>(undefined);
  readonly target = input<'_self' | '_blank' | '_parent' | '_top'>(undefined);
  readonly route = input<Route[] | string[]>(undefined);
  readonly routeExtras = input<NavigationExtras>(undefined);
  readonly backgroundUI = model<BackgroundUI>('light');
  readonly iconPosition = model<IconPositionType>(undefined);
  readonly iconOnly = input<IconOnlyType>(undefined);
  readonly iconType = input<IconType>(undefined);
  readonly isLoading = input(false, { transform: booleanAttribute });
  readonly fullSizeOption = input<FullSizeOptionType>(undefined);
  readonly iconCssClass = input('');
  readonly tooltipPosition = input<PlacementType>('top');
  readonly dataAttributes = input<Record<string, unknown>>({});
  readonly ariaAttributes = model<AriaAttributes>({
    hasPopUp: null,
    labelledBy: null,
    label: null,
    expanded: null,
    pressed: null,
    controls: null,
  });
  readonly keyboardShortcut = input<string>(undefined);
  readonly keyboardShortcutText = input<string>(undefined);
  readonly miniButton = input(false, { transform: booleanAttribute });
  readonly buttonClick = output<MouseEvent>();
  readonly hostClass = input('drv-button');
  /** @internal */
  readonly input = viewChild<ElementRef>('input');
  /** @internal */
  readonly buttonTemplate = viewChild<TemplateRef<unknown>>('templateref');
  readonly popovertarget = input<string>(undefined);
  readonly popovertargetaction = input<string>(undefined);

  private tooltipElement: HTMLElement;
  private toolTipOptions: Record<string, unknown>;
  private showTooltip: () => void;
  private hideTooltip: () => void;
  private showKeyboardshortcutTooltip: boolean;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    this.showKeyboardshortcutTooltip = this.configService.config.showKeyboardshortCutAsTooltip;
  }

  protected get combinedClasses(): string {
    const cssClasses = ['drv-btn', `drv-btn-${this.kind()}--${this.backgroundUI()}`, ...this.cssClass().split(' ')];

    if (this.isLoading()) {
      cssClasses.push('drv-btn--loading');
    }

    const fullSizeOption = this.fullSizeOption();
    if (fullSizeOption) {
      cssClasses.push(`drv-btn--size-${fullSizeOption}`);
    }

    if (this.iconType()) {
      cssClasses.push(`drv-btn--icon-${this.iconPosition() || 'left'}`);

      const iconOnly = this.iconOnly();
      if (iconOnly) {
        cssClasses.push(`drv-btn--icon-${iconOnly}`);

        if (this.miniButton()) {
          cssClasses.push('drv-btn--size-mini');
        }
      }
    }

    return cssClasses.join(' ');
  }

  protected get iconClassNames(): string {
    return ['drv-btn__icon', this.iconCssClass()].filter(Boolean).join(' ');
  }

  protected get getHref(): string {
    const href = this.href();
    if (href) {
      return href;
    }
    const route = this.route();
    return this.router && route ? '.' + this.router.createUrlTree(route, this.routeExtras() || {}).toString() : '/#';
  }

  protected get keyboardShortcutTextHint(): string {
    return `${getHotKeyHint(window, normalizeShortCut(this.keyboardShortcutText()))}`;
  }

  readonly click = input<ClickFunction>(() => {
    // TODO: check if this is needed
  });

  /** @internal */
  ngOnInit(): void {
    if (this.keyboardShortcut()) {
      this.tooltip = this.showKeyboardshortcutTooltip;
    }

    if (this.tooltip) {
      this.toolTipOptions = {
        placement: this.tooltipPosition(),
        middleware: [
          offset({
            mainAxis: 8,
          }),
          shift({ crossAxis: true }),
          flip(),
        ],
      };
      this.createTooltipElement();
    }
  }

  /** @internal */
  ngAfterViewInit(): void {
    if (this.tooltip) {
      this.bindEvents();

      if (this.keyboardShortcut()) {
        this.tooltipElement.innerText += ` ${getHotKeyHint(window, this.input().nativeElement.getAttribute('aria-keyshortcuts'))}`;
      }
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (Object.keys(changes).includes('label') && this.tooltipElement) {
      this.tooltipElement.innerText = this.label();
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    this.unbindEvents();
    if (this.tooltipElement) {
      this.tooltipElement.parentNode.removeChild(this.tooltipElement);
    }
  }

  /**
   * @param {MouseEvent} event MouseEvent
   * @internal
   */
  onClickHandler(event: MouseEvent): void {
    if (!this.href() && this.type() !== 'submit') {
      event.stopPropagation();
    }

    const route = this.route();
    if (this.router && route) {
      event.preventDefault();
      void this.router.navigate(route, this.routeExtras());
    }

    this.buttonClick.emit(event);
    this.click()(event);
  }

  private createTooltipElement(): void {
    this.tooltipElement = document.createElement('span');
    this.tooltipElement.classList.add('drv-tooltip__box');
    this.tooltipElement.innerText = this.iconOnly() ? this.label() : '';
    this.tooltipElement.hidden = true;
    document.body.appendChild(this.tooltipElement);
  }

  private mouseHandler(hide = false) {
    return (): void => {
      if (!this.tooltipElement) {
        return;
      }

      if (hide) {
        this.tooltipElement.style.opacity = '0';
        return;
      }

      void computePosition(this.input().nativeElement, this.tooltipElement, this.toolTipOptions).then(({ x, y }) => {
        Object.assign(this.tooltipElement.style, {
          opacity: 1,
          left: `${x}px`,
          top: `${y}px`,
          width: 'max-content',
        });
      });
    };
  }

  private bindEvents(): void {
    this.showTooltip = this.renderer.listen(this.input().nativeElement, 'mouseenter', this.mouseHandler());
    this.hideTooltip = this.renderer.listen(this.input().nativeElement, 'mouseleave', this.mouseHandler(true));
    this.showTooltip = this.renderer.listen(this.input().nativeElement, 'focusin', this.mouseHandler());
    this.hideTooltip = this.renderer.listen(this.input().nativeElement, 'focusout', this.mouseHandler(true));
  }

  private unbindEvents(): void {
    if (this.hideTooltip) {
      this.hideTooltip();
    }
    if (this.showTooltip) {
      this.showTooltip();
    }
    if (this.tooltipElement) {
      this.tooltipElement.style.opacity = '0';
    }
  }
}
