import {
  Component,
  ViewEncapsulation,
  OnInit,
  booleanAttribute,
  input,
  inject,
  output,
  SimpleChanges,
  OnChanges,
  AfterViewInit,
  viewChild,
  ElementRef,
  OnDestroy,
} from '@angular/core';

import {
  createCalendar,
  createViewWeek,
  createViewMonthGrid,
  createViewMonthAgenda,
  createViewDay,
  viewMonthGrid,
  viewMonthAgenda,
} from '@schedule-x/calendar';

import { ButtonComponent } from '../../components/button/button.component';
import { ButtonbarComponent } from '../../components/buttonbar/buttonbar.component';
import { SegmentedButtonComponent } from '../../components/segmentedbutton/segmentedbutton.component';

import { DeviceDetectorService } from 'ngx-device-detector';
import { createCurrentTimePlugin } from '@schedule-x/current-time';
import { createEventsServicePlugin } from '@schedule-x/events-service';
import { createCalendarControlsPlugin } from '@schedule-x/calendar-controls';
import { format, parse, addDay, addMonth } from '@formkit/tempo';

import { type CalendarApp, type CalendarEvent, type CalendarConfig } from '@schedule-x/calendar';

export type { CalendarEvent };
export type { DateRange };
export type DefaultCalendarView = 'month-grid' | 'week' | 'day';

interface DateRange {
  start: string;
  end: string;
}

@Component({
  selector: 'drv-calendar',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.scss'],
  imports: [ButtonComponent, ButtonbarComponent, SegmentedButtonComponent],
  host: {
    class: 'drv-calendar',
    '[class.drv-calendar--weekend-styled]': 'this.hasWeekendStyled()',
  },
})
export class CalendarComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {
  private deviceService = inject(DeviceDetectorService);

  readonly events = input<CalendarEvent[]>(undefined);
  readonly calendarName = input('default');
  readonly dayStartsAt = input('06:00');
  readonly dayEndsAt = input('20:00');
  readonly defaultView = input<DefaultCalendarView>('month-grid');
  readonly minDate = input<string>(undefined);
  readonly maxDate = input<string>(undefined);
  readonly hasWeekendStyled = input(true, { transform: booleanAttribute });
  readonly eventClicked = output<CalendarEvent>();
  readonly rangeUpdated = output<DateRange>();
  readonly container = viewChild<ElementRef>('container');

  protected locale = 'de-DE'; // TODO: make it configurable/@Input
  protected calendarApp: CalendarApp;
  protected activeView = 0;
  protected controls: ReturnType<typeof createCalendarControlsPlugin>;
  protected eventsService: ReturnType<typeof createEventsServicePlugin>;
  protected currentTime: ReturnType<typeof createCurrentTimePlugin>;
  protected activeDate: string;
  protected calendarTitle: string;

  private pluginDateFormat = 'YYYY-MM-DD';
  private titleFormat = 'MMMM YYYY';
  private dateFormat = 'DD.MM.YYYY';
  private calendarConfig: CalendarConfig;

  constructor() {
    this.controls = createCalendarControlsPlugin();
    this.eventsService = createEventsServicePlugin();
    this.currentTime = createCurrentTimePlugin();
    this.activeDate = format(new Date(), this.dateFormat);
  }

  protected get monthView(): string {
    return this.deviceService.isDesktop() ? viewMonthGrid.name : viewMonthAgenda.name;
  }

  ngOnInit(): void {
    this.calendarConfig = {
      locale: this.locale,
      defaultView: this.defaultView(),
      dayBoundaries: {
        start: this.dayStartsAt(),
        end: this.dayEndsAt(),
      },
      minDate: this.minDate(),
      maxDate: this.maxDate(),

      weekOptions: {
        gridHeight: 1500,
      },
      callbacks: {
        onEventClick: this.onEventClickHandler.bind(this),
        onRangeUpdate: this.onRangeUpdate.bind(this),
        onRender: this.onRender.bind(this),
      },
      calendars: {
        [this.calendarName()]: {
          colorName: this.calendarName(),
          lightColors: {
            main: '#083163',
            container: '#e1e6ec',
            onContainer: '#000',
          },
          darkColors: {
            main: '#e1e6ec',
            container: '#083163',
            onContainer: '#fff',
          },
        },
        ['holiday']: {
          colorName: 'holiday',
          lightColors: {
            main: '#677124',
            container: '#E0E670',
            onContainer: '#000',
          },
          darkColors: {
            main: '#E0E670',
            container: '#677124',
            onContainer: '#fff',
          },
        },
      },
      events: this.events(),
      views: [createViewWeek(), createViewDay()],
    };
  }

  ngAfterViewInit(): void {
    window.setTimeout(() => {
      this.calendarConfig.views.push(this.deviceService.isDesktop() ? createViewMonthGrid() : createViewMonthAgenda());
      this.calendarApp = createCalendar(this.calendarConfig, [this.currentTime, this.controls, this.eventsService]);
      this.calendarApp.render(this.container().nativeElement);
    }, 10);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.events && !changes.events.firstChange) {
      this.eventsService.set(changes.events.currentValue);
    }
  }

  ngOnDestroy(): void {
    this.calendarApp.destroy();
  }

  protected onViewChange(index: number): void {
    const views = ['day', 'week', this.monthView];

    if (views[index]) {
      this.controls.setView(views[index]);
    }
  }

  protected jumpToToday(): void {
    this.controls.setDate(format(new Date(), this.pluginDateFormat, this.locale));
    this.activeDate = format(new Date(), this.dateFormat, this.locale);
  }

  protected onDateChange(): void {
    const parsed = parse(this.activeDate, this.dateFormat);
    this.controls.setDate(format(parsed, this.pluginDateFormat, this.locale));
  }

  protected onRender(): void {
    const range: DateRange = this.controls.getRange();
    this.onRangeUpdate(range);
    this.activeView = ['day', 'week', this.monthView].indexOf(this.controls.getView());
  }

  protected onRangeUpdate(range: DateRange): void {
    const view = this.controls.getView();
    if (view === 'week') {
      const start = new Date(range.start);
      const end = new Date(range.end);
      const startMonth = start.getMonth();
      const endMonth = end.getMonth();
      const startYear = start.getFullYear();
      const endYear = end.getFullYear();

      if (startMonth === endMonth && startYear === endYear) {
        this.calendarTitle = format(start, this.titleFormat, this.locale);
      } else if (startMonth !== endMonth && startYear === endYear) {
        this.calendarTitle = `${format(start, 'MMMM', this.locale)} - ${format(end, 'MMMM', this.locale)} ${startYear}`;
      } else {
        this.calendarTitle = `${format(start, this.titleFormat, this.locale)} - ${format(end, this.titleFormat, this.locale)}`;
      }
    } else if (['day', this.monthView].includes(view)) {
      const activeDate = this.controls.getDate();
      const parsed = parse(activeDate, this.pluginDateFormat);
      this.calendarTitle = format(parsed, this.titleFormat, this.locale);
    }
    this.rangeUpdated.emit(range);
  }

  protected navigate(direction = 1): void {
    const view = this.controls.getView();
    const fn =
      {
        day: addDay,
        week: addDay,
        [this.monthView]: addMonth,
      }[view] || addDay;
    const step =
      {
        day: 1,
        week: 7,
        [this.monthView]: 1,
      }[view] || 1;

    const activeDate = this.controls.getDate();
    const parsed = parse(activeDate, this.pluginDateFormat);
    const newDate = fn(parsed, step * direction);

    this.controls.setDate(format(newDate, this.pluginDateFormat, this.locale));
    this.activeDate = format(newDate, this.dateFormat, this.locale);
  }

  private onEventClickHandler(event: CalendarEvent): void {
    this.eventClicked.emit(event);
  }
}
