import {
  Component,
  HostBinding,
  ViewEncapsulation,
  TemplateRef,
  numberAttribute,
  booleanAttribute,
  ElementRef,
  model,
  input,
  inject,
  viewChild,
} from '@angular/core';
import { FormitemComponent } from '../formitem/formitem.component';
import { LabelComponent } from '../label/label.component';
import { AriaAttributes } from '../../types';
import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { ConfigService } from '../../services/config.service';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';

export type SliderComponentVariant = 'default' | 'small';

@Component({
  selector: 'drv-slider',
  templateUrl: './slider.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./slider.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [FormitemComponent, LabelComponent, AriaAttributeDirective],
  host: {
    class: 'drv-slider',
    '[class.drv-slider--small]': 'variant() === "small"',
  },
})
export class SliderComponent extends FormitemComponent {
  readonly valueAccessor = inject<ValueAccessorDirective<string>>(ValueAccessorDirective);
  private configService = inject(ConfigService);

  readonly name = input.required<string>();
  readonly label = input.required<string>();
  readonly min = input.required<number, unknown>({ transform: numberAttribute });
  readonly max = input.required<number, unknown>({ transform: numberAttribute });
  readonly disabled = model<boolean>(false);
  readonly required = model<boolean>(undefined);
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });
  readonly ariaAttributes = input<AriaAttributes>({});
  readonly variant = input<SliderComponentVariant>('default');
  readonly minLabel = input<string>(undefined);
  readonly maxLabel = input<string>(undefined);
  readonly step = input<number, unknown>(undefined, { transform: numberAttribute });
  readonly hideTextInput = input(false, { transform: booleanAttribute });
  readonly input = viewChild<ElementRef>('input');

  @HostBinding('attr.id') protected externalId = '';
  readonly numberInput = viewChild<ElementRef>('numberInput');

  private innerValue?: string | number;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    super();
    const valueAccessor = this.valueAccessor;
    const configService = this.configService;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.innerValue = v;
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  @HostBinding('class') protected get containerCssClasses(): string {
    return [...this.cssClass().split(' ')].filter(Boolean).join(' ');
  }

  protected get cssWrapperClass(): string {
    return ['drv-slider__wrapper'].filter(Boolean).join(' ');
  }

  protected get cssInputClass(): string {
    return ['drv-slider__range', `drv-slider__range--${this.backgroundUI()}`].filter(Boolean).join(' ');
  }

  protected get cssNumberInputClass(): string {
    return ['drv-text-input', `drv-text-input--${this.backgroundUI()}`, 'drv-slider__input'].filter(Boolean).join(' ');
  }

  protected get cssVars(): string {
    return [`--drv-slider-val:${this.value}`, `--drv-slider-min:${this.min()}`, `--drv-slider-max:${this.max()}`].join(
      ';'
    );
  }

  protected get inputAriaAttributes(): AriaAttributes {
    return {
      ...this.ariaAttributes(),
      invalid: this.hasError() ? 'true' : this.ariaAttributes().invalid || 'false',
      describedby: [
        this.ariaAttributes().describedby || undefined,
        this.hasError() || this.hasWarning() || this.hasInfo() ? `${this.id()}_errors` : undefined,
      ]
        .filter(Boolean)
        .join(' '),
    };
  }

  protected get getMaxValueLength(): number {
    return this.max().toString().length;
  }

  get value(): string | number {
    return this.innerValue;
  }

  set value(value: string) {
    this.valueAccessor.valueChange(value);
    this.innerValue = value;
  }

  protected inputHandler({ target }): void {
    const inputValue = this.input();
    inputValue.nativeElement.value = target.valueAsNumber;
    this.value = inputValue.nativeElement.valueAsNumber;
  }

  protected rangeHandler({ target }): void {
    const numberInput = this.numberInput();
    if (numberInput) {
      numberInput.nativeElement.value = target.valueAsNumber;
    }

    this.value = target.valueAsNumber;
  }

  protected onFocusout(event: FocusEvent): void {
    this.valueAccessor.onBlur(event);
  }
}
