import {
  Component,
  Input,
  ViewEncapsulation,
  TemplateRef,
  ChangeDetectorRef,
  AfterContentInit,
  input,
  inject,
  output,
  viewChild,
} from '@angular/core';

@Component({
  selector: 'drv-table-expandrow',
  template: `
    <ng-template #templateref>
      <ng-content />
    </ng-template>
  `,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./table.scss'],
})
export class TableExpandRowComponent implements AfterContentInit {
  readonly cdRef = inject(ChangeDetectorRef);

  readonly buttonLabel = input('Zeile aufklappen');
  readonly selected = input(false);
  readonly expandChange = output<boolean>();
  // eslint-disable-next-line @angular-eslint/no-output-native
  readonly dblclick = output<Event>();

  /** @internal */
  readonly rowTemplate = viewChild<TemplateRef<unknown>>('templateref');

  private _expanded = false;

  get expanded(): boolean {
    return this._expanded;
  }

  @Input() set expanded(value: boolean) {
    this._expanded = value;
    this.expandChange.emit(value);
  }

  /** @internal */
  ngAfterContentInit(): void {
    this.cdRef.detectChanges();
  }
}
