import {
  Component,
  HostListener,
  HostBinding,
  ViewEncapsulation,
  ElementRef,
  TemplateRef,
  OnChanges,
  SimpleChanges,
  OnDestroy,
  ChangeDetectorRef,
  booleanAttribute,
  model,
  input,
  inject,
  output,
  viewChild,
  viewChildren,
  contentChild,
} from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { Subscription } from 'rxjs';
import { DeviceDetectorService } from 'ngx-device-detector';
import scrollIntoView from 'scroll-into-view-if-needed';

import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { ConfigService } from '../../services/config.service';

import { type AriaAttributes } from '../../types';
import { normalizeEventKey, trackByFn, isEqual } from '../../utilities/utils';
import { FormitemComponent } from '../formitem/formitem.component';
import { OptionGroupComponent } from './optiongroup.component';
import { DropdownService } from './dropdown.service';
import { calculatePosition } from './shared';
import { IconComponent } from '../icon/icon.component';
import { ToggletipComponent } from '../toggletip/toggletip.component';
import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';

type DropdownAriaAttributes = AriaAttributes & {
  expanded?: boolean | 'true' | 'false';
  activedescendant?: string;
  required?: boolean | 'true' | 'false';
};

export interface DrvDropdownOption {
  label?: string;
  value?: string;
  disabled?: boolean;
}

interface DrvDropdownOptionTemplate extends DrvDropdownOption {
  selected?: boolean;
  hasFocus?: boolean;
}

interface DrvDropdownOptionGroupTemplate {
  groupLabel?: string;
  disabled?: boolean;
  options: number[];
}

@Component({
  selector: 'drv-dropdown',
  templateUrl: './dropdown.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./dropdown.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [NgClass, NgTemplateOutlet, FormitemComponent, IconComponent, ToggletipComponent, AriaAttributeDirective],
  host: {
    class: 'drv-dropdown-host',
  },
})
export class DropdownComponent extends FormitemComponent implements OnChanges, OnDestroy {
  private configService = inject(ConfigService);
  private deviceService = inject(DeviceDetectorService);
  private dropdownService = inject(DropdownService);
  private cdRef = inject(ChangeDetectorRef);
  readonly valueAccessor = inject<ValueAccessorDirective<DrvDropdownOption[]>>(ValueAccessorDirective);

  readonly options = input<DrvDropdownOption[]>([]);
  readonly label = input.required<string>();
  readonly placeholder = input<string>(undefined);
  readonly selectedText = input('Optionen ausgew√§hlt');
  readonly selectedSuffixText = input('ausgew√§hlt');
  readonly multiple = input(false, { transform: booleanAttribute });
  readonly disabled = model<boolean>(false);
  readonly required = model<boolean | unknown>(undefined);
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });
  readonly ariaAttributes = input<AriaAttributes>({});
  readonly closed = output<null>();
  readonly setValueOnBlur = input(false, { transform: booleanAttribute });

  readonly input = viewChild<ElementRef>('trigger');
  readonly selectElement = viewChild<ElementRef>('selectElement');
  readonly list = viewChild<ElementRef>('list');
  readonly listbox = viewChild<ElementRef>('listbox');
  readonly container = viewChild<ElementRef>('container');
  readonly listitems = viewChildren<ElementRef>('listitem');
  readonly templateRef = contentChild(TemplateRef);

  protected groups: Set<OptionGroupComponent> = new Set<OptionGroupComponent>();
  protected templateOptions: DrvDropdownOptionTemplate[] = [];
  protected templateGroups: DrvDropdownOptionGroupTemplate[];
  protected isOpen = false;
  protected trackByFn = trackByFn;

  private innerValue: DrvDropdownOption[] = [];
  private typeAheadTerm = '';
  private tyepAheadTimeout: ReturnType<typeof setTimeout>;
  private subscription: Subscription;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    super();
    const configService = this.configService;
    const valueAccessor = this.valueAccessor;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);
    this.subscription = this.dropdownService.group$.subscribe(({ group, id }) => {
      if (group && id && id === this.id()) {
        this.groups.add(group);
        this.buildOptions();
      }
    });

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.innerValue = v;

      if (Array.isArray(this.innerValue) && this.innerValue.length > 0) {
        this.templateOptions.forEach(tOpt => {
          tOpt.selected = this.innerValue.some(val => {
            const { ...rest } = tOpt;
            return isEqual(val, rest);
          });
        });
        this.cdRef.detectChanges();
      }
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  @HostBinding('attr.role') protected get hostRole(): string | null {
    return this.deviceService.isDesktop() ? 'application' : null;
  }

  @HostBinding('class') protected get containerCssClasses(): string {
    return [...this.cssClass().split(' ')].filter(Boolean).join(' ');
  }

  protected get triggerButtonAriaAttributes(): DropdownAriaAttributes {
    return {
      ...this.ariaAttributes(),
      labelledby: `${this.id()} drv-dropdown-btn_${this.id()}`,
      expanded: this.isOpen ? 'true' : 'false',
      controls: `drv-dropdown-list_${this.id()}`,
      activedescendant: this.activeDescendant || undefined,
      required: this.required() as boolean,
      describedby: [
        this.ariaAttributes().describedby || undefined,
        this.hasError() || this.hasWarning() || this.hasInfo() ? `${this.id()}_errors` : undefined,
      ]
        .filter(Boolean)
        .join(' '),
    };
  }

  protected get triggerButtonCssClasses(): string[] {
    return [
      'drv-select',
      `drv-select--${this.backgroundUI()}`,
      'drv-dropdown__trigger',
      this.activeDescendantCount > 0 ? 'drv-dropdown__trigger--selected' : '',
      !this.value?.length ? 'drv-dropdown__trigger--placeholder' : '',
    ].filter(Boolean);
  }

  protected get triggerButtonText(): string {
    if (!this.value?.length) {
      return this.placeholder();
    }

    if (this.multiple() && this.value?.length > 1) {
      return `${this.value?.length} ${this.selectedText()}`;
    }

    const selectedOption = this.templateOptions.find(({ selected }) => selected);
    return selectedOption?.label.trim() || '';
  }

  protected get activeDescendant(): string | null {
    if (!this.isOpen) {
      return null;
    }

    const id = this.templateOptions
      .filter(({ hasFocus }) => hasFocus)
      .map(opt => `drv-dropdown-value_${this.id()}-${this.templateOptions.indexOf(opt)}`)
      .pop();

    return id || null;
  }

  protected get activeDescendantCount(): number {
    return this.templateOptions.filter(({ hasFocus }) => hasFocus).length;
  }

  protected get isMobile(): boolean {
    return !this.deviceService.isDesktop();
  }

  get value(): DrvDropdownOption[] {
    return this.innerValue;
  }

  set value(value: DrvDropdownOption[]) {
    if (value !== this.innerValue) {
      this.innerValue = value;
      this.valueAccessor.valueChange(value);
    }
  }

  @HostListener('window:click', ['$event.target']) protected handleWindowClick(target: Element): void {
    if (!this.container()?.nativeElement?.contains(target) && this.isOpen) {
      this.isOpen = false;
      this.closed.emit(null);
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.options) {
      this.buildOptions();
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  protected selectFocusout(event): void {
    this.valueAccessor.onBlur(event);
  }

  protected buttonFocusout(event): void {
    const { relatedTarget } = event;
    const focusedElement = this.templateOptions.findIndex(({ hasFocus }) => hasFocus);

    if (focusedElement > -1) {
      this.templateOptions[focusedElement].hasFocus = false;

      if (this.setValueOnBlur()) {
        this.selectOption(focusedElement);
      }
    }

    if (relatedTarget === null || !this.container().nativeElement.contains(relatedTarget)) {
      this.toggle(false, false);
    }
    this.valueAccessor.onBlur(event);
  }

  protected buttonClickHandler(): void {
    this.toggle(!this.isOpen);
  }

  protected select(index: number): void {
    if (!this.multiple()) {
      this.templateOptions.forEach(opt => (opt.selected = false));
      this.templateOptions[index].selected = true;
      this.toggle(false);
    } else {
      this.templateOptions[index].selected = !this.templateOptions[index].selected;
    }

    this.setValue();
  }

  protected setValueFromSelect(event: Event): void {
    const target = event.target as HTMLSelectElement;
    const { options, selectedIndex } = target;

    if (!this.multiple()) {
      this.templateOptions.forEach(opt => (opt.selected = false));
      const index = Math.max(selectedIndex - 1, 0);

      if (selectedIndex !== 0) {
        this.templateOptions[index].selected = true;
      }
    } else {
      const selectedValues = Array.from(options)
        .map(({ selected, value }) => (selected ? value : null))
        .filter(Boolean);

      this.templateOptions.forEach((opt, index) => {
        opt.selected = selectedValues.includes(index.toString());
      });
    }

    this.setValue();
  }

  protected keydownButton(event: KeyboardEvent): void {
    const { altKey } = event;
    const key = normalizeEventKey(event);
    const idx = this.templateOptions.findIndex(({ hasFocus }) => hasFocus);

    if (this.selectFromTypeAhead(idx, event)) {
      return;
    }

    const { prev, next } = this.getNeighbourIndex(idx);

    const actions = {
      enter(): void {
        this.selectOption(idx);
        this.toggle();
      },
      esc(): void {
        this.toggle(false);
      },
      down(): void {
        if (!this.isOpen) {
          this.toggle(true);
          return;
        }

        this.focusOption(idx, next);
      },
      up(): void {
        if (altKey && this.isOpen) {
          this.selectOption(idx);
          this.toggle(false);
          return;
        }

        if (!this.isOpen) {
          this.toggle(true);
          return;
        }

        this.focusOption(idx, prev);
      },
      space(): void {
        this.selectOption(idx);

        if (!this.multiple) {
          this.toggle();
        } else if (!this.isOpen) {
          this.toggle(true);
        }
      },
    };

    if (actions[key]) {
      event.preventDefault();
      actions[key].call(this);
    }
  }

  protected toggle(isOpen?: boolean, focusInput = true): void {
    this.isOpen = isOpen === undefined ? !this.isOpen : isOpen;

    window.requestAnimationFrame(() => {
      if (this.isOpen) {
        const firstOptionIndex = this.templateOptions.findIndex(option => option.selected);
        const element = this.listitems().at(firstOptionIndex)?.nativeElement;

        if (element) {
          window.requestAnimationFrame(() => scrollIntoView(element, { scrollMode: 'if-needed', block: 'nearest' }));
        }

        this.calculatePosition();
      } else {
        if (focusInput) {
          this.input().nativeElement.focus();
        }
        this.closed.emit(null);
      }
    });
  }

  private selectFromTypeAhead(idx: number, event: KeyboardEvent): boolean {
    const key = normalizeEventKey(event);

    if (key.match(/^[a-z,\d]{1}$/) !== null) {
      window.clearTimeout(this.tyepAheadTimeout);

      this.typeAheadTerm += key;
      this.tyepAheadTimeout = setTimeout(() => {
        this.typeAheadTerm = '';
      }, 1000);

      const match = this.listitems().findIndex(({ nativeElement }) => {
        if (nativeElement.getAttribute('aria-disabled') === 'true') {
          return false;
        }
        const text = nativeElement.textContent.trim().toLowerCase();
        return text.startsWith(this.typeAheadTerm);
      });

      if (match > -1) {
        event.preventDefault();
        this.toggle(true);
        this.focusOption(idx, match);
        return true;
      }

      return false;
    }

    return false;
  }

  private selectOption(idx: number): void {
    if (!this.isOpen || idx === -1) {
      return;
    }

    if (!this.multiple()) {
      const selectedOption = this.templateOptions.find(({ selected }) => selected);
      if (selectedOption) {
        selectedOption.selected = false;
      }
      this.templateOptions[idx].selected = true;
    } else {
      this.templateOptions[idx].selected = !this.templateOptions[idx].selected;
    }

    this.setValue();
  }

  private getNeighbourIndex(idx: number): Record<'prev' | 'next', number> {
    let next = idx > -1 ? Math.min(idx + 1, this.templateOptions.length - 1) : 0;
    let prev = idx > -1 ? Math.max(idx - 1, 0) : this.templateOptions.length - 1;

    while (this.templateOptions[next] && this.templateOptions[next].disabled) {
      next = next + 1;
    }

    while (this.templateOptions[prev] && this.templateOptions[prev].disabled) {
      prev = prev - 1;
    }

    if (next === idx || next > this.templateOptions.length - 1) {
      next = 0;

      while (this.templateOptions[next] && this.templateOptions[next].disabled) {
        next = next + 1;
      }
    }

    if (prev === idx || prev < 0) {
      prev = this.templateOptions.length - 1;

      while (this.templateOptions[prev] && this.templateOptions[prev].disabled) {
        prev = prev - 1;
      }
    }

    return { prev, next };
  }

  private focusOption(current: number, next: number): void {
    if (current > -1) {
      this.templateOptions[current].hasFocus = false;
    }

    this.templateOptions[next].hasFocus = true;

    const element = this.listitems().at(next)?.nativeElement;

    if (element) {
      window.requestAnimationFrame(() => scrollIntoView(element, { scrollMode: 'if-needed', block: 'nearest' }));
    }
  }

  private setValue(): void {
    this.value = this.templateOptions
      .filter(({ selected }) => selected)
      .map(option => {
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        const { selected, hasFocus, ...rest } = option;
        return rest;
      });
  }

  private optionMapper(option: DrvDropdownOption): DrvDropdownOptionTemplate {
    return {
      ...option,
      selected: false,
      hasFocus: false,
    };
  }

  private buildOptions(): void {
    this.templateOptions = this.options().map(this.optionMapper);
    this.templateGroups = [{ options: this.templateOptions.map((_, i) => i) }];

    let idx = this.templateOptions.length - 1;

    this.templateOptions = [
      ...this.templateOptions,
      ...Array.from(this.groups)
        .map(group => group.options())
        .flat()
        .map(this.optionMapper),
    ];

    this.templateGroups = [
      ...this.templateGroups,
      ...Array.from(this.groups).map(group => ({
        groupLabel: group.label(),
        disabled: group.disabled(),
        options: group.options().map(() => ++idx),
      })),
    ];
  }

  private calculatePosition(): void {
    calculatePosition(this.input().nativeElement, this.listbox().nativeElement);
  }
}
