import {
  Component,
  ViewEncapsulation,
  OnChanges,
  SimpleChanges,
  ElementRef,
  AfterViewInit,
  input,
  inject,
  output,
  viewChild,
  viewChildren,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import scrollIntoView from 'scroll-into-view-if-needed';
import { DeviceDetectorService } from 'ngx-device-detector';
import { nanoid } from 'nanoid';
import { computePosition, offset, flip } from '@floating-ui/dom';

import { normalizeEventKey, trackByFn } from '../../utilities/utils';

import { ButtonComponent } from '../button/button.component';
import type {
  IconOnlyType as ButtonIconOnlyType,
  IconPositionType as ButtonIconPositionType,
  AriaAttributes as ButtonAriaAttributes,
} from '../button/button.component';
import type { IconType } from '../../types/icons';
import { IconComponent } from '../icon/icon.component';
import type { OverflowmenuBtnVariant, OverflowmenuPlacing } from '../overflowmenu/overflowmenu.component';

export type OverflowselectPlacing = 'end' | 'start';
export type OverflowselectBtnVariant = 'secondary' | 'tertiary';
export interface OverflowselectOption {
  label?: string;
  value?: string;
  disabled?: boolean;
  selected?: boolean;
  icon?: IconType;
}

interface OverflowselectOptionTemplate extends OverflowselectOption {
  hasFocus?: boolean;
}

type OverflowselectAriaAttributes = ButtonAriaAttributes & {
  activedescendant?: string;
  label?: string;
};

@Component({
  selector: 'drv-overflowselect',
  templateUrl: './overflowselect.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['../overflowmenu/overflowmenu.scss'],
  imports: [NgTemplateOutlet, IconComponent, ButtonComponent],
  host: {
    class: 'drv-overflowmenu',
    '[class.drv-overflowmenu--place-start]': 'this.placing() === "start"',
    '[class.drv-overflowmenu--place-end]': 'this.placing() === "end"',
    '[attr.role]': 'this.hostRole',
  },
})
export class OverflowselectComponent implements OnChanges, AfterViewInit {
  private elementRef = inject(ElementRef);
  private deviceService = inject(DeviceDetectorService);

  readonly placing = input<OverflowmenuPlacing>('end');
  readonly triggerButtonLabel = input.required<string>();
  readonly triggerButtonVariant = input<OverflowmenuBtnVariant>('tertiary');
  readonly triggerButtonIcon = input<IconType>('chevron-down');
  readonly triggerButtonIconOnly = input<ButtonIconOnlyType>('only-sm');
  readonly triggerButtonIconPosition = input<ButtonIconPositionType>('left');
  readonly options = input<OverflowselectOption[]>([]);
  readonly ariaAttributes = input<ButtonAriaAttributes>({});

  readonly selectionChange = output<OverflowselectOption>();

  readonly button = viewChild<ButtonComponent>('trigger');
  readonly listbox = viewChild<ElementRef>('listbox');
  readonly listitems = viewChildren<ElementRef>('listitem');

  protected isOpen = false;
  protected templateOptions: OverflowselectOptionTemplate[] = [];
  protected id = `overflowselect-${nanoid()}`;
  protected trackByFn = trackByFn;

  private typeAheadTerm = '';
  private tyepAheadTimeout: ReturnType<typeof setTimeout>;

  protected get hostRole(): string | null {
    return this.deviceService.isDesktop() ? 'application' : null;
  }

  protected get triggerButtonAriaAttributes(): OverflowselectAriaAttributes {
    return {
      ...this.ariaAttributes(),
      label: this.triggerButtonLabel(),
      expanded: this.isOpen,
      controls: `drv-overflowselect-listbox_${this.id}`,
      activedescendant: this.activeDescendant || undefined,
    };
  }

  protected get activeDescendant(): string | null {
    if (!this.isOpen) {
      return null;
    }

    const id = this.templateOptions
      .filter(({ hasFocus }) => hasFocus)
      .map(opt => `drv-dropdown-value_${this.id}-${this.templateOptions.indexOf(opt)}`)
      .pop();

    return id || null;
  }

  protected get buttonLabel(): string {
    const selected = this.templateOptions.find(option => option.selected);
    return selected?.label || this.triggerButtonLabel();
  }

  protected get buttonIcon(): IconType {
    const selected = this.templateOptions.find(option => option.selected);
    return selected?.icon || this.triggerButtonIcon();
  }

  protected get isMobile(): boolean {
    return !this.deviceService.isDesktop();
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.options) {
      this.templateOptions = this.options().map(option => ({
        ...option,
        hasFocus: false,
      }));
    }
  }

  /** @internal */
  ngAfterViewInit(): void {
    this.button()?.input().nativeElement.setAttribute('role', 'combobox');
  }

  protected toggle(isOpen?: boolean, focusInput = true): void {
    this.isOpen = isOpen === undefined ? !this.isOpen : isOpen;

    setTimeout(() => {
      if (this.isOpen) {
        const firstOptionIndex = this.templateOptions.findIndex(option => option.selected);
        const element = this.listitems().at(firstOptionIndex)?.nativeElement;

        if (element) {
          scrollIntoView(element, { scrollMode: 'if-needed', block: 'nearest' });
        }

        this.calculatePosition();
      } else {
        if (focusInput) {
          this.button().input().nativeElement.focus();
        }
      }
    }, 10);
  }

  protected buttonFocusout(event: FocusEvent): void {
    const { relatedTarget } = event;
    const focusedElement = this.templateOptions.findIndex(({ hasFocus }) => hasFocus);

    if (focusedElement > -1) {
      this.templateOptions[focusedElement].hasFocus = false;
      this.selectOption(focusedElement);
    }

    if (relatedTarget === null || !this.elementRef.nativeElement.contains(relatedTarget)) {
      this.toggle(false, false);
    }
  }

  protected buttonClickHandler(): void {
    this.toggle(!this.isOpen);
  }

  protected keydownButton(event: KeyboardEvent): void {
    const { altKey } = event;
    const key = normalizeEventKey(event);
    const idx = this.templateOptions.findIndex(({ hasFocus }) => hasFocus);

    if (this.selectFromTypeAhead(idx, event)) {
      return;
    }

    const { prev, next } = this.getNeighbourIndex(idx);

    const actions = {
      enter(): void {
        this.selectOption(idx);
        this.toggle();
      },
      esc(): void {
        this.toggle(false);
      },
      down(): void {
        if (!this.isOpen) {
          this.toggle(true);
        }

        this.focusOption(idx, next);
      },
      up(): void {
        if (altKey && this.isOpen) {
          this.selectOption(idx);
          this.toggle(false);
          return;
        }

        if (!this.isOpen) {
          this.toggle(true);
        }

        this.focusOption(idx, prev);
      },
      space(): void {
        this.selectOption(idx);

        if (!this.isOpen) {
          this.toggle(true);
        }
      },
    };

    if (actions[key]) {
      event.preventDefault();
      actions[key].call(this);
    }
  }

  protected select(index: number): void {
    this.templateOptions.forEach(opt => (opt.selected = false));
    this.templateOptions[index].selected = true;
    this.toggle(false);
    this.selectionChange.emit(this.options()[index]);
  }

  protected setValueFromSelect(event: Event): void {
    const target: HTMLSelectElement = event.target as HTMLSelectElement;
    const { selectedIndex } = target;
    this.selectionChange.emit(this.options()[selectedIndex - 1]);
  }

  private getNeighbourIndex(idx: number): Record<'prev' | 'next', number> {
    let next = idx > -1 ? Math.min(idx + 1, this.templateOptions.length - 1) : 0;
    let prev = idx > -1 ? Math.max(idx - 1, 0) : this.templateOptions.length - 1;

    while (this.templateOptions[next] && this.templateOptions[next].disabled) {
      next = next + 1;
    }

    while (this.templateOptions[prev] && this.templateOptions[prev].disabled) {
      prev = prev - 1;
    }

    if (next === idx || next > this.templateOptions.length - 1) {
      next = 0;

      while (this.templateOptions[next] && this.templateOptions[next].disabled) {
        next = next + 1;
      }
    }

    if (prev === idx || prev < 0) {
      prev = this.templateOptions.length - 1;

      while (this.templateOptions[prev] && this.templateOptions[prev].disabled) {
        prev = prev - 1;
      }
    }

    return { prev, next };
  }

  private selectFromTypeAhead(idx: number, event: KeyboardEvent): boolean {
    const key = normalizeEventKey(event);

    if (key.match(/^[a-z,\d]{1}$/) !== null) {
      window.clearTimeout(this.tyepAheadTimeout);

      this.typeAheadTerm += key;
      this.tyepAheadTimeout = setTimeout(() => {
        this.typeAheadTerm = '';
      }, 1000);

      const match = this.listitems().findIndex(({ nativeElement }) => {
        if (nativeElement.getAttribute('aria-disabled') === 'true') {
          return false;
        }
        const text = nativeElement.textContent.trim().toLowerCase();
        return text.startsWith(this.typeAheadTerm);
      });

      if (match > -1) {
        event.preventDefault();
        this.toggle(true);
        this.focusOption(idx, match);
        return true;
      }

      return false;
    }

    return false;
  }

  private calculatePosition(): void {
    void computePosition(this.button().input().nativeElement, this.listbox().nativeElement, {
      placement: 'bottom-end',
      middleware: [
        offset({
          crossAxis: 0,
          mainAxis: 8,
        }),
        flip(),
      ],
    }).then(({ x, y }) => {
      Object.assign(this.listbox().nativeElement.style, {
        left: `${x}px`,
        top: `${y}px`,
        // position: 'absolute',
        transform: 'none',
      });
    });
  }

  private focusOption(current: number, next: number): void {
    if (current > -1) {
      this.templateOptions[current].hasFocus = false;
    }

    this.templateOptions[next].hasFocus = true;

    const element = this.listitems().at(next)?.nativeElement;

    if (element) {
      scrollIntoView(element, { scrollMode: 'if-needed', block: 'nearest' });
    }
  }

  private selectOption(idx: number): void {
    if (!this.isOpen || idx === -1) {
      return;
    }

    const selectedOption = this.templateOptions.find(option => option.selected);

    if (selectedOption) {
      selectedOption.selected = false;
    }

    this.templateOptions[idx].selected = true;
    this.selectionChange.emit(this.options()[idx]);
  }
}
