import {
  AfterViewInit,
  Component,
  HostListener,
  HostBinding,
  ViewEncapsulation,
  TemplateRef,
  booleanAttribute,
  ElementRef,
  model,
  input,
  inject,
  viewChild,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import { FormitemComponent } from '../formitem/formitem.component';
import { ConfigService } from '../../services/config.service';
import { LabelComponent } from '../label/label.component';
import { EmptyStateComponent } from '../emptystate/emptystate.component';
import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { IconComponent } from '../icon/icon.component';
import { PictogramComponent } from '../pictogram/pictogram.component';

export type UploadFieldVariantType = 'basic' | 'dropzone';

interface FileEventTarget {
  target: { files: FileList };
}
type FileEvent = Event & FileEventTarget;

@Component({
  selector: 'drv-uploadfield',
  templateUrl: './uploadfield.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./upload.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [
    NgTemplateOutlet,
    FormitemComponent,
    LabelComponent,
    EmptyStateComponent,
    IconComponent,
    PictogramComponent,
  ],
  host: {
    class: 'drv-uploadfield',
  },
})
export class UploadFieldComponent extends FormitemComponent implements AfterViewInit {
  readonly valueAccessor = inject<ValueAccessorDirective<FileList>>(ValueAccessorDirective);
  private configService = inject(ConfigService);

  readonly label = input.required<string>();
  readonly name = input<string>(undefined);
  readonly required = model<boolean>(undefined);
  readonly disabled = model<boolean>(false);
  readonly variant = input<UploadFieldVariantType>('basic');
  readonly btnLabel = input('Datei w√§hlen');
  readonly dropzoneText = input<string>(undefined);
  readonly multiple = input(false, { transform: booleanAttribute });
  readonly accept = input<string>(undefined);
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });

  readonly input = viewChild<ElementRef>('input');
  readonly wrapper = viewChild<ElementRef>('wrapper');
  @HostBinding('attr.id') protected externalId = '';

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    super();
    const valueAccessor = this.valueAccessor;
    const configService = this.configService;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      const inputValue = this.input();
      if (!v && inputValue) {
        inputValue.nativeElement.value = '';
      }
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  @HostBinding('class') protected get containerCssClasses(): string {
    return [...this.cssClass().split(' ')].filter(Boolean).join(' ');
  }

  protected get wrapperClass(): string {
    return [
      'drv-upload-field',
      `drv-upload-field--${this.variant()}`,
      this.hasError() ? 'drv-upload-field--error' : undefined,
      this.disabled() ? 'drv-upload-field--disabled' : undefined,
    ]
      .filter(Boolean)
      .join(' ');
  }

  protected get describedBy(): string | null {
    if (this.hasError() || this.hasInfo() || this.hasWarning()) {
      return `${this.id()}_error`;
    }

    return null;
  }

  @HostListener('focusout', ['$event']) protected onFocusout(event: FocusEvent): void {
    this.valueAccessor.onBlur(event);
  }

  /** @internal */
  ngAfterViewInit(): void {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const _this = this;
    const handleObject = {
      handleEvent(event: Event): void {
        if (event.type in this) {
          event.preventDefault();
          event.stopPropagation();
          this[event.type](event);
        }
      },
      dragenter(): void {
        document.body.classList.add('dragged');
      },
      dragleave(): void {
        document.body.classList.remove('dragged');
      },
      dragover(): void {
        this.dragenter();
      },
      drop(event: DragEvent): void {
        document.body.classList.remove('dragged');
        _this.valueAccessor.valueChange(event.dataTransfer.files);
      },
    };

    if (this.variant() === 'dropzone') {
      ['dragenter', 'dragleave', 'dragover', 'drop'].forEach(event => {
        this.wrapper().nativeElement.addEventListener(event, handleObject);
      });
    }
  }

  protected fileChange(event: Event | FileEvent): void {
    const {
      target: { files },
    } = event as FileEvent;

    this.valueAccessor.valueChange(files);
  }

  protected fileCancel(event: Event | FileEvent): void {
    const {
      target: { files },
    } = event as FileEvent;

    if (files.length > 0) {
      this.valueAccessor.valueChange(files);
    }
  }
}
