import { Component, OnInit, ViewEncapsulation, ElementRef, input, inject, viewChild } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

import { nanoid } from 'nanoid';
import { ModalService } from '../modal/modal.service';
import { ModalComponent, ModalContentDirective } from '../modal/modal.component';
import { ButtonComponent } from '../button/button.component';
import { NgTemplateOutlet } from '@angular/common';

interface VideoProps {
  poster: string;
  width: number;
  height: number;
  type: string;
  src: string;
}

interface EmbedProps {
  src: string;
  width: number;
  height: number;
  loading?: 'lazy' | 'eager';
  title: string;
}

interface ImageProps {
  src: string;
  width: number;
  height: number;
  loading?: 'lazy' | 'eager';
  alt: string;
  lightbox: boolean;
  srcset?: string[];
  sizes?: string;
}

@Component({
  selector: 'drv-media',
  templateUrl: './media.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./media.scss'],
  imports: [ModalComponent, ModalContentDirective, ButtonComponent, NgTemplateOutlet],
})
export class MediaComponent implements OnInit {
  private modalService = inject(ModalService);

  readonly variant = input<'default' | 'caption-right' | 'caption-left'>('default');
  readonly aspectRatio = input('16/9');
  readonly video = input<VideoProps>(undefined);
  readonly embed = input<EmbedProps>(undefined);
  readonly image = input<ImageProps>(undefined);
  readonly caption = input<string>(undefined);
  readonly duration = input<string>(undefined);
  readonly date = input<string>(undefined);
  readonly copyright = input<string>(undefined);

  readonly modalOpenButtonText = input<string>(undefined);
  readonly modalCloseButtonText = input<string>(undefined);
  readonly videoPlayButtonLabel = input<string>(undefined);

  readonly videoElement = viewChild<ElementRef>('videoElement');

  protected modalId: string;
  protected isOpen = false;
  protected sanitizer: DomSanitizer;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const sanitizer = inject(DomSanitizer);

    this.modalId = `media-model-${nanoid()}`;
    this.sanitizer = sanitizer;
  }

  protected get cssClass(): string {
    return ['drv-media', `drv-media--${this.variant()}`].filter(Boolean).join(' ');
  }

  protected get cssWrapperClass(): string {
    const arr = ['drv-media__wrapper'];

    if (this.embed() && !this.image() && !this.video()) {
      arr.push('drv-media__wrapper--embed');
    }

    return arr.join(' ');
  }

  protected get aspectRatioStyle(): string | null {
    const aspectRatio = this.aspectRatio();
    return aspectRatio === '16/9' ? null : `--aspect-ratio: ${aspectRatio}`;
  }

  protected get responsiveImageProps(): string[] {
    const image = this.image();
    if (image.srcset && image.srcset.length > 0) {
      return [image.srcset.join(', '), image.sizes || `(max-width: ${image.width}px) 100vw, ${image.width}px`];
    }

    return [];
  }

  /** @internal */
  ngOnInit(): void {
    this.modalService.modals.subscribe(data => {
      if (data) {
        const { id, open } = data;
        if (id === this.modalId) {
          this.isOpen = open;
        }
      }
    });
  }

  protected playVideo({ target }): void {
    const videoElement = this.videoElement();
    if (videoElement) {
      videoElement.nativeElement.play();
      videoElement.nativeElement.controls = true;
      target.parentNode.removeChild(target);
    }
  }

  protected lightBoxClickHandler(): void {
    this.isOpen = !this.isOpen;
    void this.modalService.updateModal({
      id: this.modalId,
      open: this.isOpen,
    });
  }
}
