import { Component, ViewEncapsulation, booleanAttribute, input, inject } from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import { NavigationExtras, Router } from '@angular/router';

import { trackByFn } from '../../utilities/utils';

export interface BreadcrumbItemInterface {
  title: string;
  href?: string;
  // createUrlTree expects any[] https://angular.dev/api/router/Router#createUrlTree
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  route?: any[];
  routeExtras?: NavigationExtras;
}

@Component({
  selector: 'drv-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./breadcrumb.scss'],
  imports: [NgTemplateOutlet],
})
export class BreadcrumbComponent {
  private router = inject(Router, { optional: true })!;

  readonly breadcrumbItems = input<BreadcrumbItemInterface[]>(undefined);
  readonly breadcrumbAriaLabel = input('Breadcrumb');
  readonly breadcrumbBackLinkOnly = input(false, { transform: booleanAttribute });

  protected trackByFn = trackByFn;

  protected getHref(item: BreadcrumbItemInterface): string {
    if (item.href) {
      return item.href;
    }
    return item.route && this.router
      ? '.' + this.router.createUrlTree(item.route, item.routeExtras || {}).toString()
      : '/#';
  }

  protected onClickHandler(item: BreadcrumbItemInterface, e: MouseEvent): void {
    if (item.route) {
      e.preventDefault();
      void this.router.navigate(item.route, item.routeExtras || {});
    }
  }
}
