import {
  Component,
  AfterViewInit,
  ChangeDetectionStrategy,
  ViewEncapsulation,
  ElementRef,
  input,
  viewChild,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import { nanoid } from 'nanoid';
import { AccordionItemComponent } from '../../components/accordion/accordionitem.component';
import { IconComponent } from '../../components/icon/icon.component';
import { SymbolsComponent } from '../../components/symbols/symbols.component';

@Component({
  selector: 'drv-aria-accordionitem',
  templateUrl: './aria-accordionitem.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  imports: [NgTemplateOutlet, IconComponent, SymbolsComponent],
  host: {
    class: 'drv-accordion__item',
    '[attr.data-open]': 'this.open',
  },
})
export class AriaAccordionItemComponent extends AccordionItemComponent implements AfterViewInit {
  /** @internal */
  readonly btnElement = viewChild<ElementRef>('detailsElement');
  readonly itemId = input(`drv-aria-accordionitem-${nanoid()}`);
  readonly headingLevel = input<'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6'>('h3');

  get open(): boolean | null {
    return this.isOpen;
  }

  /** @internal */
  emitToggle(): void {
    const ariaExpanded = this.btnElement().nativeElement.getAttribute('aria-expanded');
    this.isOpen = ariaExpanded !== 'true';
    this.toggled.emit(this);
  }
}
