import {
  Component,
  ViewEncapsulation,
  forwardRef,
  ElementRef,
  AfterViewInit,
  OnInit,
  input,
  model,
  inject,
  output,
  contentChild,
  viewChild,
} from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import type { Route } from '@angular/router';
import { NgTemplateOutlet } from '@angular/common';

import type { SymbolsType } from '../../types/symbols';
import { ButtonComponent } from '../button/button.component';
import { SymbolsComponent } from '../symbols/symbols.component';
import { SubProgressnavComponent } from './subprogressnav.component';
import { ProgressnavService } from './progressnav.service';
import { ConfigService } from '../../services/config.service';

@Component({
  selector: 'drv-progressnavnode',
  templateUrl: './progressnavnode.component.html',
  encapsulation: ViewEncapsulation.None,
  imports: [NgTemplateOutlet, ButtonComponent, SymbolsComponent],
  host: {
    class: 'drv-progressnav__item',
  },
})
export class ProgressnavNodeComponent implements AfterViewInit, OnInit {
  private router = inject(Router, { optional: true })!;
  private progressnavService = inject(ProgressnavService);
  private configService = inject(ConfigService);

  readonly name = input<string>(undefined);
  readonly url = input<string>(undefined);
  readonly route = input<Route[] | string[]>(undefined);
  readonly routeExtras = input<NavigationExtras>(undefined);
  readonly completed = model<boolean>(undefined);
  readonly active = model<boolean>(undefined);
  readonly current = model(false);
  readonly isExpanded = model(false);
  readonly error = model(false);
  readonly disabled = model<boolean | string | boolean | null | undefined>(undefined);
  readonly expandChange = output<boolean>();
  readonly itemClick = output<MouseEvent>();
  readonly labelCompleted = input<string>(undefined);
  readonly labelNotCompleted = input<string>(undefined);
  readonly toggleButtonLabel = input('Navigation ein/ausblenden');
  readonly subnav = contentChild(forwardRef(() => SubProgressnavComponent));
  readonly anchor = viewChild<ElementRef>('anchor');

  /** @internal */
  public icon: SymbolsType;

  protected get getIconType(): SymbolsType {
    if (this.error()) {
      return 'status-error';
    }

    if (this.completed()) {
      return 'status-success';
    }

    if (this.current() || this.active()) {
      return 'status-in-progress';
    }

    return 'status-open';
  }

  /** @internal */
  ngOnInit(): void {
    if (this.disabled() === undefined && !this.current() && !this.active() && !this.completed()) {
      this.disabled.set(this.configService.config.progressnavDefaultDisabled);
    }
  }

  /** @internal */
  ngAfterViewInit(): void {
    if (this.current()) {
      window.queueMicrotask(() => {
        this.progressnavService.setCurrentPage(this.anchor());
      });
    }

    this.progressnavService.currentPage$.subscribe(anchor => {
      const subnav = this.subnav();
      if (subnav && subnav.items().some(item => item.anchor().nativeElement === anchor.nativeElement)) {
        this.isExpanded.set(true);
        const subItem = subnav.items().find(item => item.anchor().nativeElement === anchor.nativeElement);
        window.queueMicrotask(() => {
          this.subnav().isExpanded.set(this.isExpanded());
          if (subItem.error()) {
            this.error.set(true);
          }
        });
        this.progressnavService.setCurrentPage(this.anchor());
      }
    });
  }

  protected getHref(): string {
    const url = this.url();
    if (url) {
      return url;
    }
    const route = this.route();
    return route && this.router ? '.' + this.router.createUrlTree(route, this.routeExtras() || {}).toString() : '/#';
  }

  protected nodeClickHandler(event: MouseEvent): void {
    this.itemClick.emit(event);

    const route = this.route();
    if (route) {
      void this.router.navigate(route, this.routeExtras() || {});
    }
  }

  protected iconClickHandler(e: MouseEvent): void {
    if (this.subnav()) {
      this.isExpanded.set(!this.isExpanded());
      window.queueMicrotask(() => {
        this.subnav().isExpanded.set(this.isExpanded());
        this.expandChange.emit(this.isExpanded());
      });
    }

    e.stopPropagation();
    e.preventDefault();
  }
}
