import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

import { FieldsetComponent } from './fieldset.component';

@Injectable({
  providedIn: 'root',
})
export class FieldsetService {
  private fieldset = new Subject<FieldsetComponent>();
  readonly fieldset$ = this.fieldset.asObservable();

  setFieldset(component: FieldsetComponent): void {
    this.fieldset.next(component);
  }
}
