import { Component, ViewEncapsulation, booleanAttribute, input, inject, output } from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import type { IconType } from '../../types/icons';
import { NavigationExtras, Router } from '@angular/router';
import type { Route } from '@angular/router';
import { HumanFileSizePipe } from '../../pipes/humanfilesize.pipe';
import { IconComponent } from '../icon/icon.component';

export type IconLinkTarget = '_blank' | '_self' | '_parent' | '_top';
export type IconLinkSize = 'default' | 'small';
export type IconLinkVariant = 'inline' | 'block';
export type IconLinkType = 'button' | 'anchor';

export interface IconLinkData {
  id?: string;
  type?: string;
  size?: string;
  info?: string;
}

@Component({
  selector: 'drv-iconlink',
  templateUrl: './iconlink.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./iconlink.scss'],
  imports: [NgTemplateOutlet, HumanFileSizePipe, IconComponent],
  host: {
    class: 'drv-iconlink',
  },
})
export class IconlinkComponent {
  private router = inject(Router, { optional: true })!;

  readonly cssClass = input<string>(undefined);
  readonly href = input<string>(undefined);
  readonly route = input<Route[] | string[]>(undefined);
  readonly routeExtras = input<NavigationExtras>(undefined);
  readonly subtitle = input<string>(undefined);
  readonly externalLinkText = input<string>(undefined);
  readonly type = input<IconLinkType>('anchor');
  readonly icon = input<IconType>('arrow-right');
  readonly variant = input<IconLinkVariant>('inline');
  readonly size = input<IconLinkSize>('default');
  readonly target = input<IconLinkTarget>(undefined);
  readonly data = input<IconLinkData>(undefined);
  readonly downloadAble = input(false, { transform: booleanAttribute });
  readonly disabled = input(false, { transform: booleanAttribute });
  readonly itemClick = output<MouseEvent>();

  protected cssPrefix = 'drv-icon-link';

  protected get elementCssClass(): string {
    return [
      this.cssPrefix,
      `${this.cssPrefix}--${this.variant()}`,
      `${this.cssPrefix}--size-${{ default: 'body-01', small: 'label-01' }[this.size()]}`,
      this.cssClass(),
    ]
      .filter(Boolean)
      .join(' ');
  }

  protected get getHref(): string {
    const href = this.href();
    if (href) {
      return href;
    }
    const route = this.route();
    return this.router && route ? '.' + this.router.createUrlTree(route, this.routeExtras() || {}).toString() : '/#';
  }

  protected onClickHandler(event: PointerEvent | MouseEvent): void {
    const route = this.route();
    if (this.router && route) {
      void this.router.navigate(route, this.routeExtras());
    }

    if (!this.href()) {
      event.preventDefault();
    }

    this.itemClick.emit(event);
  }
}
