import { Component, ViewEncapsulation, input } from '@angular/core';
import { KeyValuePipe } from '@angular/common';
import { trackByFn } from '../../utilities/utils';

export type SummaryVariantType = 'smaller' | 'equal';
export type SummaryMainInfoHighlight = 'label' | 'value';
export type SummaryLayoutType = 'block' | 'columns' | 'grid';

@Component({
  selector: 'drv-summary',
  templateUrl: './summary.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./summary.scss'],
  imports: [KeyValuePipe],
  host: {
    class: 'drv-summary',
    '[attr.title]': 'null',
  },
})
export class SummaryComponent {
  readonly title = input<string>(undefined);
  readonly items = input<Record<string, string>>({});
  readonly variant = input<SummaryVariantType>('equal');
  readonly layout = input<SummaryLayoutType>('block');
  readonly mainInfoHighlight = input<SummaryMainInfoHighlight>('value');

  protected trackByFn = trackByFn;

  get cssClasses(): string {
    return [
      'drv-description',
      `drv-description--${this.variant()}`,
      `drv-description--${this.layout()}`,
      this.mainInfoHighlight() === 'label' ? 'drv-description--main-info-label' : '',
    ].join(' ');
  }

  protected unsorted(): number {
    return 0;
  }
}
