import {
  Component,
  ViewEncapsulation,
  OnInit,
  AfterViewInit,
  input,
  inject,
  model,
  contentChildren,
} from '@angular/core';
import { ButtonComponent } from '../button/button.component';
import { ButtonbarComponent } from '../buttonbar/buttonbar.component';
import { ConfigService } from '../../services/config.service';
import { getImageDimensions } from '../../utilities/utils';
import { GridDirective } from '../grid/grid.directive';
import { RowDirective } from '../grid/row.directive';
import { ColDirective } from '../grid/col.directive';
import { StatusComponent } from '../status/status.component';
import { InsuranceNumberFormatPipe } from '../insurancenumber/insurancenumber.pipe';

export type ApplicationHeaderLayout = 'multiline' | 'singleline';

@Component({
  selector: 'drv-applicationheader',
  templateUrl: './applicationheader.component.html',
  imports: [
    ButtonComponent,
    GridDirective,
    RowDirective,
    ColDirective,
    StatusComponent,
    InsuranceNumberFormatPipe,
    ButtonbarComponent,
  ],
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./applicationheader.scss'],
  host: {
    role: 'banner',
    class: 'drv-application-header',
    '[class.drv-application-header--multiline]': 'this.layout() === "multiline"',
    '[class.drv-application-header--singleline]': 'this.layout() === "singleline"',
  },
})
export class ApplicationHeaderComponent implements OnInit, AfterViewInit {
  private configService = inject(ConfigService);
  readonly triggerButtonLabel = input.required<string>();
  readonly appTitle = input<string>(undefined);
  readonly pageTitle = input<string>(undefined);
  readonly statusText = input<string>(undefined);
  readonly headerLogoPath = input<string>(undefined);
  readonly fullWidth = input(false);

  readonly showLogo = input(true);
  readonly logoAltText = input('Logo der Rentenversicherung');
  readonly clientNumber = input<string>(undefined);
  readonly clientName = input<string>(undefined);
  readonly layout = model<ApplicationHeaderLayout>(undefined);

  readonly buttons = contentChildren(ButtonComponent);

  protected isOpen = false;
  protected logoWidth: number;
  protected logoHeight: number;
  protected initialized = false;

  private getImageDimensions = getImageDimensions;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    this.layout.set(this.configService.config.applicationHeaderDefaultLayout);
  }

  /** @internal */
  ngOnInit(): void {
    const headerLogoPath = this.headerLogoPath();
    if (headerLogoPath) {
      this.getImageDimensions(headerLogoPath)
        .then(({ height, width }) => {
          this.logoWidth = width;
          this.logoHeight = height;
        })
        .catch(() => {
          this.logoWidth = 0;
          this.logoHeight = 0;
        });
    }
  }

  /** @internal */
  ngAfterViewInit(): void {
    window.setTimeout(() => {
      this.initialized = true;
    }, 10);
  }

  protected toggle(): void {
    this.isOpen = !this.isOpen;
  }
}
