import {
  Component,
  HostBinding,
  HostListener,
  ViewEncapsulation,
  TemplateRef,
  ElementRef,
  numberAttribute,
  booleanAttribute,
  model,
  input,
  inject,
  viewChild,
} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { nanoid } from 'nanoid';
import { FormitemComponent } from '../formitem/formitem.component';
import { LabelComponent } from '../label/label.component';
import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { ConfigService } from '../../services/config.service';

import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';
import { type AriaAttributes } from '../../types';

@Component({
  selector: 'drv-textarea',
  templateUrl: './textarea.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./textarea.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [FormsModule, FormitemComponent, LabelComponent, AriaAttributeDirective],
  host: {
    class: 'drv-textarea',
  },
})
export class TextareaComponent extends FormitemComponent {
  readonly valueAccessor = inject<ValueAccessorDirective<string>>(ValueAccessorDirective);
  private configService = inject(ConfigService);

  readonly id = input(`textarea-${nanoid()}`);
  readonly name = input<string>(undefined);
  readonly disabled = model<boolean>(false);
  readonly label = input<string>(undefined);
  readonly placeholder = input<string>(undefined);
  readonly rows = input(4, { transform: numberAttribute });
  readonly cols = input(50, { transform: numberAttribute });
  readonly required = model<boolean>(undefined);
  readonly hasCounter = input(false, { transform: booleanAttribute });
  readonly minLength = input<number, unknown>(undefined, { transform: numberAttribute });
  readonly maxLength = input<number, unknown>(undefined, { transform: numberAttribute });
  readonly readonly = input<boolean, unknown>(false, { transform: booleanAttribute });
  readonly counterLabel = input<string>(undefined);
  readonly counterFromLabel = input('von');
  readonly counterLimitReachedMessage = input<string>(undefined);
  readonly ariaAttributes = input<AriaAttributes>({});
  readonly autogrow = input(false, { transform: booleanAttribute });
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });
  readonly input = viewChild<ElementRef>('input');

  @HostBinding('attr.id') protected externalId = '';

  @HostBinding('class') protected get containerCssClasses(): string {
    return [...this.cssClass().split(' ')].filter(Boolean).join(' ');
  }

  private innerValue?: string;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    super();
    const valueAccessor = this.valueAccessor;
    const configService = this.configService;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.innerValue = v;
    });
    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  protected get charCount(): number {
    return this.innerValue ? this.innerValue.length : 0;
  }

  protected get readOutCounter(): boolean {
    return this.charCount > this.maxLength() * 0.8 || this.maxLength() < 100;
  }

  protected get hasCounterLimitReached(): boolean {
    const maxLength = this.maxLength();
    return this.hasCounter() && maxLength && this.charCount >= maxLength;
  }

  protected get inputAriaAttributes(): AriaAttributes {
    const describedby: string[] = [];

    const ariaAttributes = this.ariaAttributes();
    if (ariaAttributes.describedby) {
      describedby.push(ariaAttributes.describedby);
    }

    const id = this.id();
    if (this.maxLength() && !this.hasCounterLimitReached && this.readOutCounter) {
      describedby.push(`${id}_count`);
    }

    const hasError = this.hasError();
    if (hasError || this.hasCounterLimitReached || this.hasWarning() || this.hasInfo()) {
      describedby.push(`${id}_errors`);
    }

    return {
      ...ariaAttributes,
      invalid: hasError ? 'true' : ariaAttributes.invalid || 'false',
      describedby: [...new Set(describedby)].join(' '),
    };
  }

  protected get cssInputClass(): string {
    return ['drv-text-area', `drv-text-area--${this.backgroundUI()}`].filter(Boolean).join(' ');
  }

  get value(): string {
    return this.innerValue;
  }

  set value(value: string) {
    this.valueAccessor.valueChange(value);
    this.innerValue = value;
  }

  @HostListener('focusout', ['$event']) protected onFocusout(event: FocusEvent): void {
    if (event.target === this.input().nativeElement) {
      this.valueAccessor.onBlur(event);
    }
  }
}
