import {
  Component,
  ElementRef,
  ViewEncapsulation,
  Renderer2,
  OnDestroy,
  OnInit,
  OnChanges,
  SimpleChanges,
  booleanAttribute,
  input,
  inject,
  Attribute,
} from '@angular/core';
import { computePosition, flip, offset, shift } from '@floating-ui/dom';

export type PlacementType =
  | 'top'
  | 'right'
  | 'left'
  | 'bottom'
  | 'top-start'
  | 'right-start'
  | 'left-start'
  | 'bottom-start'
  | 'top-end'
  | 'right-end'
  | 'left-end'
  | 'bottom-end';

@Component({
  // eslint-disable-next-line @angular-eslint/component-selector
  selector: '[drv-tooltip]',
  template: '<ng-content />',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./tooltip.scss'],
})
export class TooltipComponent implements OnInit, OnDestroy, OnChanges {
  private elRef = inject(ElementRef);
  private renderer = inject(Renderer2);

  readonly placement = input<PlacementType>('top');
  readonly tooltip = input<string>(undefined);
  readonly dynamic = input(false, { transform: booleanAttribute });

  private titleText = '';
  private tooltipElement: HTMLElement;
  private toolTipOptions: Record<string, unknown>;
  private showTooltip: () => void;
  private hideTooltip: () => void;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor(@Attribute('title') private title = '') {
    // https://github.com/storybookjs/storybook/issues/17004 ü§¨ ü§¨ ü§¨ ü§¨
    this.toolTipOptions = {
      strategy: 'absolute',
      placement: this.placement(),
      middleware: [
        offset({
          mainAxis: 8,
        }),
        shift({ crossAxis: true }),
        flip(),
      ],
    };
    this.titleText = this.title;
  }

  protected get tooltipText(): string {
    return this.tooltip() || this.titleText;
  }

  /** @internal */
  ngOnInit(): void {
    if (!this.tooltipText) {
      return;
    }

    if (this.dynamic()) {
      this.setup();
      return;
    }

    this.createTooltipElement();
    this.bindEvents();

    if (this.elRef.nativeElement.hasAttribute('title')) {
      this.elRef.nativeElement.removeAttribute('title');
    }
  }

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (Object.keys(changes).includes('tooltip') && this.tooltipElement) {
      this.tooltipElement.innerText = this.tooltip();
    }

    if (Object.keys(changes).includes('placement')) {
      this.toolTipOptions.placement = changes.placement.currentValue;
    }
  }

  /** @internal */
  ngOnDestroy(): void {
    this.unbindEvents();
    if (this.tooltipElement) {
      this.tooltipElement.parentNode.removeChild(this.tooltipElement);
    }
  }

  private setup(): void {
    if (window && 'ResizeObserver' in window) {
      const callback: ResizeObserverCallback = ([{ target }]) => {
        if (target.scrollWidth > target.clientWidth) {
          if (!this.tooltipElement) {
            this.createTooltipElement();
          }

          this.bindEvents();
        } else if (this.tooltipElement) {
          this.unbindEvents();
        }
      };

      const resizeObserver = new ResizeObserver(callback);

      resizeObserver.observe(this.elRef.nativeElement);
      return;
    }

    if (!this.tooltipElement) {
      this.createTooltipElement();
      this.bindEvents();
    }
  }

  private createTooltipElement(): void {
    this.tooltipElement = document.createElement('span');
    this.tooltipElement.classList.add('drv-tooltip__box');
    this.tooltipElement.innerText = this.tooltipText;
    this.tooltipElement.hidden = true;

    document.body.appendChild(this.tooltipElement);
  }

  private mouseHandler(hide = false) {
    return (): void => {
      if (!this.tooltipElement) {
        return;
      }

      if (hide) {
        this.tooltipElement.style.opacity = '0';
        return;
      }

      void computePosition(this.elRef.nativeElement, this.tooltipElement, this.toolTipOptions).then(({ x, y }) => {
        Object.assign(this.tooltipElement.style, {
          opacity: 1,
          left: `${x}px`,
          top: `${y}px`,
          width: 'max-content',
        });
      });
    };
  }

  private bindEvents(): void {
    this.showTooltip = this.renderer.listen(this.elRef.nativeElement, 'mouseenter', this.mouseHandler());
    this.hideTooltip = this.renderer.listen(this.elRef.nativeElement, 'mouseleave', this.mouseHandler(true));
    this.showTooltip = this.renderer.listen(this.elRef.nativeElement, 'focusin', this.mouseHandler());
    this.hideTooltip = this.renderer.listen(this.elRef.nativeElement, 'focusout', this.mouseHandler(true));
  }

  private unbindEvents(): void {
    if (this.hideTooltip) {
      this.hideTooltip();
    }
    if (this.showTooltip) {
      this.showTooltip();
    }
    if (this.tooltipElement) {
      this.tooltipElement.style.opacity = '0';
    }
  }
}
