import {
  Component,
  ElementRef,
  OnInit,
  ViewEncapsulation,
  TemplateRef,
  booleanAttribute,
  input,
  inject,
  model,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';
import { ConfigService } from '../../services/config.service';
import { ToggletipComponent } from '../toggletip/toggletip.component';

@Component({
  selector: 'drv-label',
  templateUrl: './label.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./label.scss'],
  imports: [NgTemplateOutlet, ToggletipComponent],
})
export class LabelComponent implements OnInit {
  private elementRef = inject(ElementRef);
  private configService = inject(ConfigService);

  // eslint-disable-next-line @angular-eslint/no-input-rename
  readonly id = input<string>(undefined, { alias: 'for' });
  readonly required = model<boolean | unknown>(undefined);
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);

  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('Pflichtfeld');
  readonly optionalText = model<string>(undefined);
  readonly visuallyHidden = input(false, { transform: booleanAttribute });
  readonly removeHost = input(true, { transform: booleanAttribute });

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const configService = this.configService;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);
  }

  protected get combinedClasses(): string {
    return ['drv-label', this.visuallyHidden() ? 'drv-vh' : null].filter(Boolean).join(' ');
  }

  /** @internal */
  ngOnInit(): void {
    if (!this.removeHost()) {
      return;
    }

    const nativeElement: HTMLElement = this.elementRef.nativeElement;
    const parentElement: HTMLElement = nativeElement.parentElement;

    if (!nativeElement && !parentElement) {
      return;
    }

    while (nativeElement.firstChild) {
      parentElement.insertBefore(nativeElement.firstChild, nativeElement);
    }

    parentElement.removeChild(nativeElement);
  }
}
