import {
  Component,
  ElementRef,
  ViewEncapsulation,
  AfterContentInit,
  input,
  contentChildren,
  viewChild,
} from '@angular/core';
import { NgTemplateOutlet } from '@angular/common';

import { computePosition, offset, flip, shift } from '@floating-ui/dom';
import { trackByFn } from '../../utilities/utils';

import type { IconType } from '../../types/icons';
import { ButtonComponent, type IconOnlyType, type IconPositionType } from '../../components/button/button.component';
import type { OverflowmenuBtnVariant, OverflowmenuPlacing } from '../../components/overflowmenu/overflowmenu.component';
import { nanoid } from 'nanoid';

const HAS_ANCHOR_SUPPORT = 'positionArea' in document.documentElement.style ? true : false;

@Component({
  selector: 'drv-overflowmenu-modern',
  templateUrl: './overflowmenu-modern.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./overflowmenu-modern.scss'],
  imports: [ButtonComponent, NgTemplateOutlet],
  host: {
    class: 'drv-overflowmenu-modern',
    '[class.drv-overflowmenu-modern--place-start]': 'this.placing() === "start"',
    '[class.drv-overflowmenu-modern--place-end]': 'this.placing() === "end"',
  },
})
export class OverflowmenuModernComponent implements AfterContentInit {
  readonly id = input(`drv-overflowmenu-modern-${nanoid()}`);
  readonly placing = input<OverflowmenuPlacing>('end');
  readonly triggerButtonLabel = input.required<string>();
  readonly triggerButtonVariant = input<OverflowmenuBtnVariant>('secondary');
  readonly triggerButtonIcon = input<IconType>('overflow');
  readonly triggerButtonIconOnly = input<IconOnlyType>('only');
  readonly triggerButtonIconPosition = input<IconPositionType>('left');

  /** @internal */
  readonly buttons = contentChildren(ButtonComponent);
  /** @internal */
  readonly toggleButton = viewChild(ButtonComponent, { read: ElementRef });
  /** @internal */
  readonly menuContent = viewChild<ElementRef<HTMLElement>>('overflowMenuContent');

  protected isOpen = false;
  protected trackByFn = trackByFn;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    // This constructor is intentionally empty to allow Angular to inject dependencies.
  }

  /** @internal */
  ngAfterContentInit(): void {
    this.buttons().forEach(button => {
      const definedHandler = button.onClickHandler.bind(button);
      button.onClickHandler = (event: MouseEvent): void => {
        this.menuContent().nativeElement.hidePopover();
        if (definedHandler) {
          definedHandler(event);
        }
      };
      button.cssClass.set('drv-overflowmenu-modern__btn');
      button.kind.set('secondary');
      button.iconPosition.set('left');
    });
  }

  protected toggle(event: Event): void {
    this.isOpen = (event as ToggleEvent).newState === 'open';

    if (this.isOpen && !HAS_ANCHOR_SUPPORT) {
      setTimeout(() => this.calculatePosition(), 10);
    }
  }

  private calculatePosition(): void {
    const menuContent = this.menuContent();
    const toggleButton = this.toggleButton();
    if (!menuContent?.nativeElement || !toggleButton?.nativeElement?.firstElementChild) {
      return;
    }

    void computePosition(toggleButton.nativeElement.firstElementChild, menuContent.nativeElement, {
      placement: 'bottom-end',
      middleware: [
        offset({
          mainAxis: 8,
        }),
        shift({ crossAxis: true }),
        flip(),
      ],
    }).then(({ x, y }) => {
      Object.assign(this.menuContent().nativeElement.style, {
        left: `${x}px`,
        top: `${y}px`,
      });
    });
  }
}
