import { Injectable, InjectionToken, inject } from '@angular/core';
import { type BackgroundUIDarkColor } from '../types';
import { type NotificationToastPosition } from '../components/notification/notification.component';
import { type ApplicationHeaderLayout } from '../components/applicationheader/applicationheader.component';

interface AppConfig {
  spritePath: string;
  pictogramSpritePath: string;
  formsRequiredDefault: boolean;
  formsRequiredDefaultSign: string;
  formsOptionalDefaultText: string;
  theadDefaultBackgroundUI: BackgroundUIDarkColor;
  progressnavDefaultDisabled: boolean;
  notificationDefaultToastPosition: NotificationToastPosition;
  applicationHeaderDefaultLayout: ApplicationHeaderLayout;
  showKeyboardshortCutAsTooltip: boolean;
  reducedMotion: boolean;
  useTheme: boolean;
  themePath?: string;
  symbolsPath: string;
}

const CONFIG_TOKEN = new InjectionToken<Partial<AppConfig>>('app-config');

// Default configuration values for public theme
const DEFAULT_CONFIG_PUBLIC: AppConfig = {
  spritePath: './assets/img/sprite.svg',
  pictogramSpritePath: './assets/img/pictograms.svg',
  formsRequiredDefault: true,
  formsRequiredDefaultSign: '',
  formsOptionalDefaultText: 'optional',
  theadDefaultBackgroundUI: 'dark',
  progressnavDefaultDisabled: true,
  notificationDefaultToastPosition: 'bottom',
  applicationHeaderDefaultLayout: 'multiline',
  showKeyboardshortCutAsTooltip: true,
  reducedMotion: false,
  useTheme: false,
  themePath: undefined,
  symbolsPath: './assets/img/symbols.svg',
};

// Default configuration values for productive theme
const DEFAULT_CONFIG_PRODUCTIVE: AppConfig = {
  spritePath: './assets/img/sprite.svg',
  pictogramSpritePath: './assets/img/pictograms.svg',
  formsRequiredDefault: false,
  formsRequiredDefaultSign: '*',
  formsOptionalDefaultText: '',
  theadDefaultBackgroundUI: 'color',
  progressnavDefaultDisabled: false,
  notificationDefaultToastPosition: 'right',
  applicationHeaderDefaultLayout: 'singleline',
  showKeyboardshortCutAsTooltip: true,
  reducedMotion: true,
  useTheme: true,
  themePath: '@drv-ds/drv-design-system-ng/assets/css/themes/productive.css',
  symbolsPath: './assets/img/symbols.svg',
};

/**
 * Library users can provide a partial app configuration object, this service will
 * handle merging the partial configuration with a default configuration.
 */
@Injectable({
  providedIn: 'root',
})
export class ConfigService {
  private customConfig = inject<Partial<AppConfig>>(CONFIG_TOKEN, { optional: true });

  private _config: AppConfig;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const customConfig = this.customConfig;

    this._config = { ...DEFAULT_CONFIG_PUBLIC, ...customConfig };
  }

  get config(): AppConfig {
    return this._config;
  }
}

export { AppConfig, CONFIG_TOKEN, DEFAULT_CONFIG_PRODUCTIVE, DEFAULT_CONFIG_PUBLIC };
