import { ChangeDetectionStrategy, Component, ViewEncapsulation, numberAttribute, input } from '@angular/core';

import { AriaAttributeDirective } from '../../directives/ariaattributes.directive';

interface AriaAttributes {
  [key: string]: string | number | undefined;
  label?: string;
  valuetext?: string;
  valuemax: number;
  valuenow: number;
}

@Component({
  selector: 'drv-progressbar',
  templateUrl: 'progressbar.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./progressbar.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  imports: [AriaAttributeDirective],
  host: {
    class: 'drv-progressbar',
  },
})
export class ProgressBarComponent {
  readonly value = input<number, unknown>(undefined, { transform: numberAttribute });
  readonly max = input<number, unknown>(undefined, { transform: numberAttribute });
  readonly ariaLabel = input<string>(undefined);
  readonly ariaValuetext = input<string>(undefined);
  readonly variant = input<'sticky' | 'default'>(undefined);

  protected isNaN: typeof Number.isNaN = Number.isNaN;

  get inputAriaAttributes(): AriaAttributes {
    const value = this.value();
    return {
      label: this.ariaLabel(),
      valuetext: this.ariaValuetext(),
      valuemax: this.max(),
      valuenow: this.isNaN(value) ? undefined : value,
    };
  }
}
