import {
  Component,
  ViewEncapsulation,
  TemplateRef,
  forwardRef,
  AfterContentInit,
  ChangeDetectorRef,
  numberAttribute,
  input,
  inject,
  viewChild,
  ContentChildren,
  QueryList,
} from '@angular/core';

import { TreeViewNodeComponent } from './treeviewnode.component';

@Component({
  selector: 'drv-subtreeview',
  template: `
    <ng-template #templateref>
      <div role="group" [attr.style]="styleProps" [attr.id]="id()">
        <ng-content></ng-content>
      </div>
    </ng-template>
  `,
  encapsulation: ViewEncapsulation.None,
})
export class SubTreeViewComponent implements AfterContentInit {
  private cdRef = inject(ChangeDetectorRef);

  readonly level = input(1, { transform: numberAttribute });
  public readonly id = input.required<string>();

  /** @internal */
  readonly subtreeTemplate = viewChild<TemplateRef<unknown>>('templateref');
  /** @internal */
  @ContentChildren(forwardRef(() => TreeViewNodeComponent), { descendants: false })
  public items: QueryList<TreeViewNodeComponent>;

  protected get styleProps(): string {
    return `--drv-treeview-level: ${this.level()}`;
  }

  /** @internal */
  ngAfterContentInit(): void {
    this.cdRef.detectChanges();
  }
}
