import { Directive, ElementRef, Renderer2, OnInit, input, inject } from '@angular/core';

@Directive({
  // eslint-disable-next-line @angular-eslint/directive-selector
  selector: '[data-attributes]',
})
export class DataAttributeDirective implements OnInit {
  private elRef = inject(ElementRef);
  private renderer = inject(Renderer2);

  readonly dataAttributes = input<Record<string, unknown>>({}, { alias: 'data-attributes' });

  ngOnInit(): void {
    const keys = Object.keys(this.dataAttributes());

    if (!keys.length) {
      return;
    }

    keys.forEach(key => {
      this.renderer.setAttribute(this.elRef.nativeElement, `data-${key}`, this.dataAttributes()[key] as string);
    });
  }
}
