import { Directive, input } from '@angular/core';

@Directive({
  selector: '[drv-row]',
  host: {
    class: 'drv-row',
    '[class.drv-row-padding]': 'padding()',
    '[class.drv-row--condensed]': 'condensed()',
    '[class.drv-row--narrow]': 'narrow()',
    '[class]': 'combinedClassNames',
  },
})
export class RowDirective {
  public readonly noGutter = input<'left' | 'right' | 'both' | ''>('');
  public readonly padding = input(false);
  public readonly condensed = input(false);
  public readonly narrow = input(false);

  protected get combinedClassNames(): string {
    const noGutterClassName =
      {
        left: 'drv-no-gutter--left',
        right: 'drv-no-gutter--right',
        both: 'drv-no-gutter',
      }[this.noGutter()] || '';

    return noGutterClassName;
  }
}
