import {
  Component,
  AfterContentInit,
  ViewEncapsulation,
  OnChanges,
  TemplateRef,
  booleanAttribute,
  ElementRef,
  OnDestroy,
  input,
  model,
  inject,
  output,
  viewChild,
  OnInit,
} from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { Subject, takeUntil } from 'rxjs';
import { nanoid } from 'nanoid';

import { ValueAccessorDirective } from '../../directives/valueaccessor.directive';
import { ConfigService } from '../../services/config.service';
import { InputGroupService } from '../inputgroup/inputgroup.service';

import { IconComponent } from '../icon/icon.component';
import { SymbolsComponent } from '../symbols/symbols.component';
import { RadiobuttonComponent } from '../radiobutton/radiobutton.component';
import { CheckboxComponent } from '../checkbox/checkbox.component';
import { BackgroundUI } from '../../types';
import { FormItemMessage } from '../formitem/formitem.component';
import { trackByFn } from '../../utilities/utils';
import { ToggletipComponent } from '../toggletip/toggletip.component';

interface InputGroupChild {
  id: string;
  value: string | number;
  checked?: boolean;
  disabled: boolean;
}

@Component({
  selector: 'drv-inputgroup',
  templateUrl: './inputgroup.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./inputgroup.scss'],
  hostDirectives: [ValueAccessorDirective],
  imports: [NgClass, NgTemplateOutlet, IconComponent, ToggletipComponent, SymbolsComponent],
  host: {
    class: 'drv-input-group-host',
  },
})
export class InputGroupComponent implements AfterContentInit, OnChanges, OnDestroy, OnInit {
  readonly valueAccessor = inject<ValueAccessorDirective<(string | number)[]>>(ValueAccessorDirective);
  private configService = inject(ConfigService);
  private inputGroupService = inject(InputGroupService);

  readonly id = input<string>(`inputgroup-${nanoid()}`);
  readonly name = input('');
  // eslint-disable-next-line
  readonly innerValue = model<(string | number)[]>([], { alias: "value" });
  readonly grouplabel = input<string>(undefined);
  readonly disabled = model<boolean>(false);
  readonly required = model<boolean | unknown>(undefined);
  readonly backgroundUI = input<BackgroundUI>('light');
  readonly layout = input<'inline' | 'block' | 'columns'>('inline');
  readonly requiredSign = model<string>(undefined);
  readonly requiredSignTitleAttr = input('(Pflichtfeld)');
  readonly optionalText = model<string>(undefined);
  readonly hasError = input(false, { transform: booleanAttribute });
  readonly errorText = input<FormItemMessage[]>([]);
  readonly errorPrefix = input('Fehler:');
  readonly hasInfo = input(false, { transform: booleanAttribute });
  readonly infoText = input<FormItemMessage[]>([]);
  readonly infoPrefix = input('Hinweis:');
  readonly hasWarning = input(false, { transform: booleanAttribute });
  readonly warningText = input<FormItemMessage[]>([]);
  readonly warningPrefix = input('Warnung:');
  readonly toggletip = input<string>(undefined);
  readonly toggletipLabel = input<string>('Hinweis');
  readonly toggletipCloseLabel = input<string>('Schlie√üen');
  readonly toggletipTemplate = input<TemplateRef<unknown>>(undefined);
  readonly collapseMessages = input(false, { transform: booleanAttribute });
  readonly visuallyHiddenLabel = input(false, { transform: booleanAttribute });
  readonly announceChanges = input(true, { transform: booleanAttribute });
  readonly wrapper = viewChild<ElementRef>('wrapper');

  readonly valueChange = output<(string | number)[]>();

  /** @internal */
  public children = new Map<CheckboxComponent | RadiobuttonComponent, InputGroupChild>();

  protected trackByFn = trackByFn;

  private checkingState = false;
  private labelClicked = false;
  private destroyed$ = new Subject();

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const valueAccessor = this.valueAccessor;
    const configService = this.configService;

    this.required.set(configService.config.formsRequiredDefault);
    this.requiredSign.set(configService.config.formsRequiredDefaultSign);
    this.optionalText.set(configService.config.formsOptionalDefaultText);

    valueAccessor.setHost(this);
    valueAccessor.value.subscribe(v => {
      this.innerValue.set(v || []);

      this.children.forEach((value, key) => {
        this.children.set(key, {
          ...value,
          checked: this.innerValue().includes(value.value),
        });
      });
    });

    valueAccessor.disabled.subscribe(disabled => {
      this.disabled.set(disabled);
    });
  }

  get cssClasses(): string[] {
    return [
      ['drv-fieldset', `drv-fieldset--${this.backgroundUI()}`, this.hasError() ? 'drv-fieldset--error' : ''].join(' '),
      ['drv-input-group', `drv-input-group--${this.layout()}`].join(' '),
    ];
  }

  get isRadioInputGroup(): boolean {
    return this.children.size > 0 && [...this.children.keys()].every(child => child instanceof RadiobuttonComponent);
  }

  get isCheckboxInputGroup(): boolean {
    return this.children.size > 0 && [...this.children.keys()].every(child => child instanceof CheckboxComponent);
  }

  get value(): (string | number)[] {
    return this.innerValue();
  }

  set value(value: (string | number)[]) {
    if (typeof value === 'undefined') {
      this.innerValue.set([]);
    } else {
      this.innerValue.set(value);

      window.queueMicrotask(() => {
        this.valueAccessor.valueChange(this.innerValue());
        this.valueChange.emit(this.innerValue());
      });
    }
  }

  ngOnInit(): void {
    this.inputGroupService.register$.pipe(takeUntil(this.destroyed$)).subscribe(item => {
      if (item.id === this.id()) {
        this.children.set(item.child, {
          id: item.id,
          value: item.value,
          checked: this.innerValue().includes(item.value),
          disabled: item.disabled,
        });
      }
    });

    this.inputGroupService.unregister$.pipe(takeUntil(this.destroyed$)).subscribe(item => {
      if (item.id === this.id() || !this.children.has(item.child)) {
        this.children.delete(item.child);
      }
    });
  }

  /** @internal */
  ngAfterContentInit(): void {
    if (!this.isRadioInputGroup && !this.isCheckboxInputGroup) {
      throw new Error('Radiobuttons and Checkboxes should not be mixed within InputGroup');
    }

    this.inputGroupService.changes$.pipe(takeUntil(this.destroyed$)).subscribe(item => {
      if (item.id !== this.id() || !this.children.has(item.child)) {
        return;
      }

      const child = this.children.get(item.child);

      child.checked = item.checked !== child.checked ? item.checked : child.checked;
      child.disabled = item.disabled !== child.disabled ? item.disabled : child.disabled;

      if (this.isRadioInputGroup) {
        this.children.forEach((value, key) => {
          if (key !== item.child) {
            this.children.set(key, {
              ...value,
              checked: item.checked ? false : value.checked,
            });
          }
        });
      }

      this.setValue();
    });
  }

  /** @internal */
  ngOnChanges(): void {
    this.inputGroupService.setErrors({
      id: this.id(),
      hasError: this.hasError(),
      hasInfo: this.hasInfo(),
      hasWarning: this.hasWarning(),
    });
  }

  /** @internal */
  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  protected onFocusout(event: FocusEvent): void {
    if (this.isRadioInputGroup) {
      for (const item of this.children.keys()) {
        item.input().nativeElement.classList.remove('js-focus-visible');
      }

      this.valueAccessor.onBlur(event);
      return;
    }

    if (this.isCheckboxInputGroup) {
      const { relatedTarget } = event;

      if (relatedTarget && !this.wrapper().nativeElement.contains(relatedTarget as HTMLElement)) {
        this.valueAccessor.onBlur(event);
      }
    }
  }

  protected onFocusIn(e: FocusEvent): void {
    const target = e.target as HTMLElement;
    if (!target.matches('.drv-radio-button__input') || this.labelClicked) {
      return;
    }
    if (!target.matches(':focus-visible')) {
      target.classList.add('js-focus-visible');
    }
    this.labelClicked = false;
  }

  protected labelClick({ target }): void {
    if (target.matches('.drv-radio-button__label')) {
      this.labelClicked = true;
    }
  }

  private setValue(): void {
    this.value = [...this.children.values()]
      .filter(({ disabled }) => !disabled)
      .filter(({ checked }) => checked)
      .map(({ value }) => value);
  }
}
