import {
  Component,
  OnChanges,
  OnInit,
  ViewEncapsulation,
  numberAttribute,
  booleanAttribute,
  input,
  inject,
} from '@angular/core';
import type { IconType } from '../../types/icons';
import type { PlacementType } from '../tooltip/tooltip.component';
import { ConfigService } from '../../services/config.service';
import { TooltipComponent } from '../tooltip/tooltip.component';

export { type IconType };
@Component({
  selector: 'drv-icon',
  templateUrl: './icon.component.html',
  encapsulation: ViewEncapsulation.None,
  imports: [TooltipComponent],
  host: {
    class: 'drv-icon-host',
  },
})
export class IconComponent implements OnInit, OnChanges {
  private configService = inject(ConfigService);

  readonly type = input.required<IconType>();
  readonly label = input<string>(undefined);
  readonly size = input(24, { transform: numberAttribute });
  readonly ariaHidden = input(true, { transform: booleanAttribute });
  readonly tooltip = input<boolean, unknown>(undefined, { transform: booleanAttribute });
  readonly tooltipPosition = input<PlacementType>('top');

  protected svgSrc: string;
  protected iconSpritePath: string;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const configService = this.configService;

    this.iconSpritePath = configService.config.spritePath;
  }

  protected get xLink(): string {
    return `${this.iconSpritePath}#${this.svgSrc}`;
  }

  protected classNames = 'drv-icon';

  protected get sizeInRem(): number {
    return this.size() / 16;
  }

  /** @internal */
  ngOnInit(): void {
    this.setSource();
  }

  /** @internal */
  ngOnChanges(): void {
    window.setTimeout(() => this.setSource(), 10);
  }

  private setSource(): void {
    this.svgSrc = this.type();
  }
}
