// TODO: reintroduce as ViewTransition or delete
import {
  Component,
  OnInit,
  HostBinding,
  ViewEncapsulation,
  ElementRef,
  AfterViewInit,
  ChangeDetectorRef,
  inject,
} from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { animate, style, transition, trigger, keyframes } from '@angular/animations';

import { TableHeadComponent } from './thead.component';
import { TableComponent } from './table.component';
import { ConfigService } from '../../services/config.service';
import { ButtonComponent } from '../button/button.component';
import { IconComponent } from '../icon/icon.component';

@Component({
  selector: 'drv-table-animated',
  templateUrl: './table.animated.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./table.scss'],
  animations: [
    trigger('TableRowAnimation', [
      transition(':enter', [
        animate(
          '1s',
          keyframes([
            style({ opacity: 0, offset: 0 }),
            style({ opacity: 0, offset: 0.75 }),
            style({ opacity: 1, offset: 1.0 }),
          ])
        ),
      ]),
      transition(':leave', [
        animate(
          '1s',
          keyframes([
            style({ opacity: 1, offset: 0 }),
            style({ opacity: 1, offset: 0.75 }),
            style({ opacity: 0, offset: 1.0 }),
          ])
        ),
      ]),
    ]),
  ],
  imports: [NgClass, NgTemplateOutlet, TableHeadComponent, ButtonComponent, IconComponent],
})
export class TableAnimatedComponent extends TableComponent implements OnInit, AfterViewInit {
  private elRef: ElementRef;

  /** @internal */
  @HostBinding('@.disabled') public animationsDisabled = true;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const elRef = inject(ElementRef);
    const configService = inject(ConfigService);
    const cdRef = inject(ChangeDetectorRef);

    super(elRef, configService, cdRef);

    this.elRef = elRef;
  }

  /** @internal */
  ngAfterViewInit(): void {
    window.setTimeout(() => {
      this.animationsDisabled =
        window && window.matchMedia ? window.matchMedia('(prefers-reduced-motion: reduce)').matches : false;
    }, 10);
  }
}
