import { Component, OnChanges, input, inject } from '@angular/core';
import { DrvDropdownOption, DropdownComponent } from './dropdown.component';
import { DropdownService } from './dropdown.service';

@Component({
  selector: 'drv-optiongroup',
  template: '<ng-content />',
})
export class OptionGroupComponent implements OnChanges {
  private dropdownService = inject(DropdownService);

  readonly options = input.required<DrvDropdownOption[]>();
  readonly label = input.required<string>();
  readonly disabled = input(false);

  private parent: DropdownComponent;

  /** Inserted by Angular inject() migration for backwards compatibility */
  constructor(...args: unknown[]);

  constructor() {
    const dropdown = inject(DropdownComponent, { host: true });

    this.parent = dropdown;
  }

  ngOnChanges(): void {
    this.dropdownService.setGroup({
      id: this.parent.id(),
      group: this,
    });
  }
}
