import { Component, ViewEncapsulation, OnChanges, SimpleChanges, input, output } from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { ProgressBarComponent } from '../progressbar/progressbar.component';
import { IconComponent } from '../icon/icon.component';
import { SymbolsComponent } from '../symbols/symbols.component';
import { ButtonComponent } from '../button/button.component';
import { trackByFn } from '../../utilities/utils';
import { type FormItemMessage } from '../formitem/formitem.component';
import { HumanFileSizePipe } from '../../pipes/humanfilesize.pipe';

export interface FileListItem {
  id: string;
  name: string;
  href?: string;
  blob?: Blob;
  size: number;
  hasError?: boolean;
  deletable?: boolean;
  errorMessages?: FormItemMessage[];
  progress?: number | null;
  indeterminate?: boolean;
  downloadAble?: boolean;
  callbackFn?: () => void;
}

@Component({
  selector: 'drv-filelist',
  templateUrl: './filelist.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./upload.scss'],
  imports: [
    NgClass,
    NgTemplateOutlet,
    ProgressBarComponent,
    IconComponent,
    ButtonComponent,
    HumanFileSizePipe,
    SymbolsComponent,
  ],
  host: {
    class: 'drv-filelist',
  },
})
export class FileListComponent implements OnChanges {
  readonly headline = input<string>(undefined);
  readonly fileList = input<FileListItem[]>([]);
  readonly deleteButtonLabel = input.required<string>();
  readonly cancelButtonLabel = input.required<string>();
  readonly progressBarLabel = input.required<string>();
  readonly progressBarFromLabel = input('von');
  readonly externalLinkLabel = input('(√∂ffnet im neuen Fenster)');
  readonly errorPrefix = input('Fehler:');

  readonly deleted = output<FileListItem>();
  readonly cancelled = output<FileListItem>();

  protected templateList: FileListItem[] = [];
  protected trackByFn = trackByFn;

  /**
   * @param {SimpleChanges} changes SimpleChanges
   * @internal
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.fileList) {
      this.templateList = this.fileList().map(item => {
        if (item.blob) {
          item.href = window.URL.createObjectURL(item.blob);
        }
        return item;
      });
    }
  }

  protected getListItemClass(item: FileListItem): string {
    const classes = ['drv-filelist__item'];

    if (item.hasError) {
      classes.push('drv-filelist__item--error');
      return classes.join(' ');
    }

    if (item.progress !== null) {
      classes.push('drv-filelist__item--inprogress');
    }

    return classes.join(' ');
  }

  protected fileListActionHandler(action: 'delete' | 'cancel', item: FileListItem): void {
    const output = {
      delete: this.deleted,
      cancel: this.cancelled,
    }[action];

    if (output) {
      output.emit(item);
    }
  }

  protected itemClickHandler(e: MouseEvent, item: FileListItem): void {
    if (item.callbackFn) {
      if (item.href && item.href === '#') {
        e.preventDefault();
      }
      item.callbackFn();
    }
  }
}
