import {
  Component,
  ViewEncapsulation,
  TemplateRef,
  forwardRef,
  AfterContentInit,
  ChangeDetectorRef,
  numberAttribute,
  model,
  input,
  inject,
  viewChild,
  contentChildren,
} from '@angular/core';
import { ProgressnavNodeComponent } from './progressnavnode.component';

@Component({
  selector: 'drv-subprogressnav',
  template: `
    <ng-template #templateref>
      <div
        class="drv-progressnav__sublist"
        [attr.style]="styleProps"
        [attr.hidden]="isExpanded() ? null : true"
        [attr.inert]="!isExpanded() || null"
      >
        <ng-content></ng-content>
      </div>
    </ng-template>
  `,
  encapsulation: ViewEncapsulation.None,
})
export class SubProgressnavComponent implements AfterContentInit {
  private cdRef = inject(ChangeDetectorRef);

  readonly level = input(1, { transform: numberAttribute });
  readonly isExpanded = model(false);
  /** @internal */
  readonly subtreeTemplate = viewChild<TemplateRef<unknown>>('templateref');
  /** @internal */
  readonly items = contentChildren(forwardRef(() => ProgressnavNodeComponent));

  protected get styleProps(): string {
    return `--drv-progressnav-level: ${this.level()}`;
  }

  /** @internal */
  ngAfterContentInit(): void {
    this.cdRef.detectChanges();
  }
}
