import {
  AfterViewInit,
  Component,
  ElementRef,
  ViewEncapsulation,
  HostListener,
  input,
  inject,
  viewChild,
} from '@angular/core';
import { A11yModule } from '@angular/cdk/a11y';
import { computePosition, offset, flip, shift } from '@floating-ui/dom';
import { ButtonComponent } from '../button/button.component';

@Component({
  selector: 'drv-toggletip',
  templateUrl: './toggletip.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./toggletip.scss'],
  imports: [A11yModule, ButtonComponent],
  host: {
    class: 'drv-toggletip',
  },
})
export class ToggletipComponent implements AfterViewInit {
  private elementRef = inject(ElementRef);

  readonly btnTriggerLabel = input('Hinweis');
  readonly btnCloseLabel = input('Schlie√üen');

  readonly toggletipContent = viewChild<ElementRef<HTMLElement>>('toggletipContent');
  readonly toggletip = viewChild<ElementRef<HTMLElement>>('toggletip');

  protected visible = false;

  @HostListener('window:click', ['$event.target']) protected handleWindowClick(target: HTMLElement): void {
    if (
      !this.toggletipContent()?.nativeElement?.contains(target as Node) &&
      target !== this.toggletip().nativeElement
    ) {
      this.visible = false;
    }
  }

  /** @internal */
  ngAfterViewInit(): void {
    if (this.visible) {
      setTimeout(() => this.checkContentBox(), 10);
    }
  }

  protected toggle(): void {
    this.visible = !this.visible;

    if (this.visible) {
      queueMicrotask(() => {
        this.toggletipContent()?.nativeElement.focus();
        this.checkContentBox();
      });
    } else {
      this.toggletip().nativeElement.focus();
    }
  }

  private checkContentBox(): void {
    void computePosition(this.elementRef.nativeElement, this.toggletipContent().nativeElement, {
      placement: 'top',
      middleware: [
        offset({
          crossAxis: 0,
          mainAxis: 8,
        }),
        flip(),
        shift({ padding: 8 }),
      ],
    }).then(({ x, y }) => {
      Object.assign(this.toggletipContent().nativeElement.style, {
        left: `${x}px`,
        top: `${y}px`,
      });
    });
  }
}
